<template>

  <div class="container">
    <v-alert v-model="success" type="success" dismissible>
      {{messageFromServer}}
    </v-alert>

    <div class="row">

<!-- FOR EDITING DATA -->

      <b-form class="column" style="width:40%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="Country Name :" label-for="countryname">
          <b-form-input
            id="countryname"
            v-model="currentCountry.countryname"
            required
            placeholder="Enter Country name only"
          ></b-form-input>
        </b-form-group>
       

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>


<!-- FOR SAVING DATA -->
      <b-form class="column" style="width:40%" @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="Country Name :" label-for="countryname">
          <b-form-input
            id="countryname"
            v-model="country.countryname"
            required
            placeholder="Enter Country name only"
          ></b-form-input>
        </b-form-group>


        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
<!-- table start       -->
      <div class="column" style="width:60%">
      <v-data-table
        :headers="headers"
        :items="countries"
        :items-per-page="10"
        class="elevation-1"
      >
        <template v-slot:item="row">
          <tr v-bind:key="row.item.id">
            <td>{{row.item.countryname}}</td>
            

            <td>
              <a class="text-success" @click="isEditing=true; selectCountry(row.item)"><font-awesome-icon icon="edit"/></a>
              &emsp;
              <a class="text-danger" @click="deleteAccGroup(row.item)"><font-awesome-icon icon="trash-alt"/></a>
            </td>
          </tr>
        </template>
      </v-data-table>
      </div>


    </div>
  </div>
</template>

<script>
import Country from "../models/Country";
import axios from "axios";
import commonVari from "../models/commonVari.js"
export default {
  name: "Country",
  
  data() {
    return {
      API_URL: commonVari.API_URL,
      success : false,
      messageFromServer:'',
      headers: [
        { text: "Country", value: "countryname" },
        { text: "Action", value: "" }
       
      ],
      country: new Country(),
      isEditing: false,
      countries: [],
      currentCountry: new Country(),
      
    };
  },
  created() {
    this.getAllCountries();
  },
  methods: {
    selectCountry(country) {
      this.currentCountry = country;
    },
    getAllCountries() {
      axios
        .get(this.API_URL + "country/getAllCountry")
        .then(({ data }) => (this.countries = data));
    },
    saveData() {
      axios.post(this.API_URL + "country/saveCountry", this.country).then(data => {
        if(data.data.error){
          console.log(data.error);
          this.messageFromServer= "Not Saved ... ";
          this.success=true;
        }else{
          this.messageFromServer= "Saved !!!!";
          this.success=true;
        }
        
        this.getAllCountries();
      });
    },
    updateData() {
      axios
        .post(this.API_URL + "country/updateCountry", this.currentCountry)
        .then(data => {
          console.log(data);
          this.isEditing = false;
          this.getAllCountries();
        });
    },
    deleteAccGroup(country) {
      this.currentCountry = country;
      axios
        .post(this.API_URL + "country/deleteCountry", this.currentCountry)
        .then(data => {
          console.log(data);
          this.isEditing = false;
          this.getAllCountries();
        });
    }
  }
};
</script>
<style scoped>
.container {
  margin: 18px;
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}
</style>