<template>
  <div>
    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">
          <v-chip dense>{{this.$route.params.company}}</v-chip>LEDGER REPORT
        </v-toolbar-title>
      </v-toolbar>
      <v-card flat outlined class="pl-5 pr-5 pb-0">
        <v-row dense class="justify-center">
          <v-col>SELECT LEDGER</v-col>
          <v-col>
            <v-autocomplete
              style="width:200px"
              required
              v-model="ledger"
              :items="ledgers"
              color="blue"
              item-text="name"
              auto-select-first
              dense
              outlined
              hide-details
              return-object
            ></v-autocomplete>
          </v-col>

          <!-- <v-col>
            <v-menu
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field v-model="date" outlined dense readonly v-on="on"></v-text-field>
              </template>
              <v-date-picker v-model="date" @input="menu2 = false"></v-date-picker>
            </v-menu>
          </v-col> -->
          <!-- <v-col>
            <v-menu
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field v-model="date1" outlined dense readonly v-on="on"></v-text-field>
              </template>
              <v-date-picker v-model="date1" @input="menu2 = false"></v-date-picker>
            </v-menu>
          </v-col> -->
          <v-col>
            <v-btn @click="getReport">GET REPORT</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-container>
    <ejs-grid
      ref="grid"
      :dataSource="transactions"
      :allowPaging="true"
      gridLines="Both"
      rowHeight="25"
      :allowSorting="true"
      :allowFiltering="true"
      :toolbar="toolbar"
      :toolbarClick="toolbarClick"
      :allowExcelExport="true"
      :allowPdfExport="true"
      :queryCellInfo="queryCellInfo"
    >
      <e-columns>
        <e-column field="transdate" headerText="Date" textAlign="Left" width="90"></e-column>
        <e-column field="transType" headerText="PARTICULAR" textAlign="Left" width="90"></e-column>
        <e-column field="account" headerText="PARTICULAR" textAlign="Left" width="90"></e-column>
        
        <e-column field="crInr" headerText="CREDIT INR" textAlign="Left" width="90"></e-column>
        <e-column field="dbInr" headerText="DEBIT INR" textAlign="Left" width="90"></e-column>
        <e-column field="balanceInr" headerText="BALANCE INR" textAlign="Left" width="90"></e-column>
      </e-columns>
    </ejs-grid>
  </div>
</template>

<script>
import axios from "axios";
import commonVari from "../models/commonVari.js";
import {
  Page,
  Toolbar,
  ExcelExport,
  PdfExport,
  
} from "@syncfusion/ej2-vue-grids";
export default {
  data() {
    return {
      toolbar: ["ExcelExport", "PdfExport", "CsvExport", "Print"],
      API_URL: commonVari.API_URL,
      transactions: [],
      ledgers: [],
      ledger: null,
      menu2: false,
      balanceInr: 0,
      date: new Date().toISOString().substr(0, 10),
      date1: new Date().toISOString().substr(0, 10)
    };
  },
  created() {
    this.companyId = this.$route.params.companyId;
    this.companyname = this.$route.params.company;
    this.getAllLedgers();
  },
  provide: {
    grid: [PdfExport, ExcelExport, Toolbar, Page]
  },
  methods: {
    toolbarClick: function(args) {
      switch (args.item.text) {
        case "PDF Export":
          this.$refs.grid.pdfExport({ fileName: "new.pdf" });
          break;
        case "Excel Export":
          this.$refs.grid.excelExport({ fileName: "new.xlsx" });
          break;
        case "CSV Export":
          this.$refs.grid.csvExport({ fileName: "new.csv" });
          break;
      }
    },

    queryCellInfo(args) {
      
      var crAcc = args.data.crAcc.name;
      var dbAcc = args.data.dbAcc.name;
      var crInr = 0;
      var dbInr = 0;
      

      if (
        args.column.field == "account" ||
        args.column.field == "crInr" ||
        args.column.field == "dbInr"
      ) {
        var account = "";
        if (crAcc == this.ledger.name) {
          account = dbAcc;
          crInr = args.data.crAmtInr;
          dbInr = 0;
        } else if (dbAcc == this.ledger.name) {
          account = crAcc;
          dbInr = args.data.dbAmtInr;
          crInr = 0;
        }
        args.cell.innerText = account;
        if (args.column.field == "crInr") {
          args.cell.innerText = crInr;
        }
        if (args.column.field == "dbInr") {
          args.cell.innerText = dbInr;
        }
      }
      if (args.column.field == "balanceInr") {
        if (crAcc == this.ledger.name) {
          account = dbAcc;
          crInr = args.data.crAmtInr;
          if (crInr != null) {
            this.balanceInr = this.balanceInr + parseFloat(crInr);
          } else {
            this.balanceInr = this.balanceInr + 0;
          }
        } else if (dbAcc == this.ledger.name) {
          account = crAcc;
          dbInr = args.data.dbAmtInr;
          if (dbInr !== null) {
            this.balanceInr = this.balanceInr - parseFloat(dbInr);
          } else {
            this.balanceInr = this.balanceInr - 0;
          }
        }

        var balanceString = "";
        if (this.balanceInr > 0) {
          balanceString = this.balanceInr.toFixed(2) + " CR";
        }
        if (this.balanceInr < 0) {
          balanceString = (this.balanceInr.toFixed(2) * -1) + " DB";
        }
        args.cell.innerText = balanceString;
      }
    },
    getReport() {
      this.balanceInr=0.00;
      const formData = new FormData();
      formData.append("ledger", JSON.stringify(this.ledger));

      axios
        .post(this.API_URL + "trans/getTransByLedger", formData)
        .then(({ data }) => ((this.transactions = data)));
    },

    getAllLedgers() {
      const formData = new FormData();
      formData.append("companyId", this.companyId);

      axios
        .post(this.API_URL + "ledger/getAllLedgerByCompany", formData)
        .then(({ data }) => (this.ledgers = data));
    }
  }
};
</script>

<style scoped>
</style>