<template>
  <div class="mainClass">
    
    <v-expansion-panels  focusable>
      <v-expansion-panel>
        <v-expansion-panel-header class="purple lighten-2">SEARCH DIAMONDS</v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-container class="pink" style="width:100%">
            <div>
              <!-- STOCK ID & REPORT NO & SHAPE + SHAPE IMAGE -->

              <v-container fluid>
                <v-row>
                  <v-col class="pr-1" sm="4">
                    <v-card>
                      <div>
                        <v-radio-group
                          hide-details
                          class="mt-0 mb-0 body-2"
                          v-model="options"
                          row
                          dense
                        >
                          <v-radio label="STOCK" value="byStockId"></v-radio>
                          <v-radio label="REPORT" value="byReportNo"></v-radio>
                          <v-radio label="MEMO NO" value="byMemoNo"></v-radio>
                        </v-radio-group>
                      </div>
                      <div>
                        <v-text-field
                          class="pt-0 pb-0"
                          v-model="selectedOption"
                          hide-details
                          outlined
                          dense
                        ></v-text-field>
                      </div>
                    </v-card>
                  </v-col>
                  <v-col cols="12" sm="2">
                    <v-card class="justify-center">
                      <div>
                        <v-card class="mb-2" flat>Stone Status</v-card>
                      </div>
                      <div>
                        <v-select outlined dense hide-details></v-select>
                      </div>
                    </v-card>
                  </v-col>
                  <v-col cols="12" sm="2">
                    <v-card>
                      <div>
                        <v-card class="justify-center mb-2" flat>Stone Location</v-card>
                      </div>
                      <div>
                        <v-autocomplete
                          v-model="country"
                          :items="countries"
                          item-text="countryname"
                          outlined
                          hide-details
                          dense
                          small-chips
                          multiple
                          return-object
                        ></v-autocomplete>
                      </div>
                    </v-card>
                  </v-col>
                  <v-col cols="12" sm="2">
                    <v-card>
                      <div>
                        <v-card class="justify-center mb-2" flat>Company</v-card>
                      </div>
                      <div>
                        <v-autocomplete
                          v-model="company"
                          :items="companies"
                          item-text="name"
                          outlined
                          hide-details
                          dense
                          chips
                          small-chips
                          multiple
                          return-object
                        ></v-autocomplete>
                      </div>
                    </v-card>
                  </v-col>
                  <v-col cols="12" sm="1">
                    <v-card>
                      <div>
                        <v-card class="justify-center mb-2" flat>Rap Back</v-card>
                      </div>
                      <div>
                        <v-text-field
                          class="pt-0 pb-0"
                          v-model="rapback"
                          hide-details
                          outlined
                          dense
                        ></v-text-field>
                      </div>
                    </v-card>
                  </v-col>
                  <v-col cols="12" sm="1">
                    <v-btn @click="updateData">
                      <v-icon>mdi-refresh</v-icon>
                    </v-btn>
                  </v-col>
                </v-row>

                <v-card style="border:1px solid" elevation="3" outlined>
                  <v-row no-gutters>
                    <v-col style="width:75px" v-for="shape in shapes" :key="shape.id">
                      <!-- SHAPE IMAGE       -->
                      <v-card flat class="d-flex justify-space-around mb-0">
                        <v-btn-toggle
                          color="purple"
                          style="border:1px"
                          group
                          dense
                          multiple
                          v-model="selectedShapes"
                        >
                          <v-btn
                            :value="shape.id"
                            style="width:75px;height:70px;border:2px solid"
                            outlined
                          >
                            <v-img
                              :src="shapeImage(shape)"
                              class="white lighten-2"
                              max-width="70px"
                              max-height="65px"
                            ></v-img>
                          </v-btn>
                        </v-btn-toggle>
                      </v-card>
                      <!-- SHAPE NAME     -->
                      <v-card flat class="d-flex justify-center mb-0">
                        <v-btn-toggle
                          dense
                          active-class="pink white--text"
                          color="purple"
                          style="border:1px"
                          multiple
                          small
                          v-model="selectedShapes"
                        >
                          <v-btn
                            text
                            small
                            :value="shape.id"
                            style="font-size:12px; width:75px;border:1px solid"
                            outlined
                          >{{shape.shapename}}</v-btn>
                        </v-btn-toggle>
                      </v-card>
                    </v-col>
                  </v-row>
                </v-card>

                <div class="pointer">
                  <v-card outlined>
                    <v-card flat class="ml-4 mr-2">
                      <v-row dense align="center" justify="space-around">
                        <v-col cols="12" sm="1" class="pa-0 pb-0">
                          <v-card align="start" justify="space-around" flat>Pointers</v-card>
                        </v-col>
                        <v-col cols="2" sm="11" class="pa-0">
                          <v-btn-toggle
                            class="d-flex flex-wrap"
                            multiple
                            active-class="pink white--text"
                            dense
                            v-model="selectedPointers"
                          >
                            <v-btn
                              small
                              depressed
                              v-for="(n, index) in pointers"
                              :value="n"
                              :key="index"
                              outlined
                            >{{n.fromSize}}-{{n.toSize}}</v-btn>
                          </v-btn-toggle>
                        </v-col>
                      </v-row>
                    </v-card>
                  </v-card>
                </div>
              </v-container>
              <!-- COLOR AND CLARITY -->
              <v-container fluid class="my-0">
                <v-card style="border:1px solid" outlined class="pl-5 pr-5">
                  <v-layout row wrap class="justify-space-around">
                    <v-flex xs6 md6>
                      <v-row>
                        <v-col cols="12" sm="3" class="ml-1 pa-1 pb-1">
                          <v-select
                            dense
                            class="small"
                            v-model="FromColor"
                            hide-details
                            :items="colors"
                            item-text="colorname"
                            label="From"
                            outlined
                            return-object
                          ></v-select>
                        </v-col>

                        <v-col cols="12" sm="3" class="ml-1 pa-1 pb-1">
                          <v-select
                            dense
                            clearable
                            hide-details
                            v-model="ToColor"
                            :items="colors"
                            item-text="colorname"
                            label="To"
                            outlined
                            return-object
                          ></v-select>
                        </v-col>
                      </v-row>

                      <v-row>
                        <v-col cols="12" sm="6" class="ml-1 pa-1 pb-1">
                          <v-select
                            dense
                            v-model="selectedColor"
                            item-text="colorname"
                            :items="colors"
                            label="Select Color"
                            multiple
                            outlined
                            class="ml-1"
                          >
                            <template v-slot:prepend-item>
                              <v-list-item ripple @click="toggle">
                                <v-list-item-action>
                                  <v-icon
                                    :color="selectedColor.length > 0 ? 'indigo darken-4' : ''"
                                  >{{ icon }}</v-icon>
                                </v-list-item-action>
                                <v-list-item-content>
                                  <v-list-item-title>Select All</v-list-item-title>
                                </v-list-item-content>
                              </v-list-item>
                              <v-divider class="mt-2"></v-divider>
                            </template>
                          </v-select>
                        </v-col>
                      </v-row>
                    </v-flex>
                    <v-flex xs6 md6>
                      <!-- <v-card class="ml-1 row"> -->
                      <v-row>
                        <v-col cols="12" sm="3" class="ml-1 pa-1 pb-1">
                          <v-select
                            dense
                            hide-details
                            v-model="FromClarity"
                            :items="clarities"
                            item-text="clarityname"
                            label="From"
                            outlined
                            return-object
