                        </v-col>
                        <v-col cols="12" sm="3" class="ml-1 pa-1 pb-1">
                          <v-select
                            dense
                            clearable
                            v-model="ToClarity"
                            hide-details
                            :items="clarities"
                            item-text="clarityname"
                            label="To"
                            outlined
                            return-object
                          ></v-select>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col cols="12" sm="6" class="ml-1 pa-1 pb-1">
                          <v-select
                            v-model="selectedClarity"
                            item-text="clarityname"
                            :items="clarities"
                            label="Select Clarity"
                            multiple
                            dense
                            outlined
                            class="ml-1"
                          >
                            <template v-slot:prepend-item>
                              <v-list-item ripple @click="toggle1">
                                <v-list-item-action>
                                  <v-icon
                                    :color="selectedClarity.length > 0 ? 'indigo darken-4' : ''"
                                  >{{ icon }}</v-icon>
                                </v-list-item-action>
                                <v-list-item-content>
                                  <v-list-item-title>Select All</v-list-item-title>
                                </v-list-item-content>
                              </v-list-item>
                              <v-divider class="mt-2"></v-divider>
                            </template>
                          </v-select>
                        </v-col>
                      </v-row>
                      <!-- </v-card> -->
                    </v-flex>
                  </v-layout>
                </v-card>
              </v-container>

              <v-container fluid class="my-0">
                <v-card style="border:1px solid" outlined class="pl-5 pr-5">
                  <v-layout row wrap class="justify-space-around">
                    <v-flex xs6 md6>
                      <v-card flat class="ml-2 mr-2">
