                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2" class="pa-0 pb-0">
                            <v-card align="start" justify="space-around" flat>Carats</v-card>
                          </v-col>
                          <v-col
                            class="pt-0 pb-0"
                            cols="12"
                            sm="10"
                            align="start"
                            justify="space-around"
                          >
                            <v-row align="center" dense>
                              <v-col>
                                <v-text-field
                                  v-model="fromCarat"
                                  :disabled="isPointer"
                                  height="30px"
                                  hide-details
                                  type="number"
                                  outlined
                                  dense
                                ></v-text-field>
                              </v-col>
                              <v-col>
                                <v-text-field
                                  v-model="toCarat"
                                  :disabled="isPointer"
                                  height="30px"
                                  hide-details
                                  type="number"
                                  outlined
                                  dense
                                ></v-text-field>
                              </v-col>
                            </v-row>
                          </v-col>
                        </v-row>
                        <!-- Lab -->
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2" class="pa-0 pb-0">
                            <v-card align="start" justify="space-around" flat>Lab</v-card>
                          </v-col>
                          <v-col
                            class="pt-0 pb-0"
                            cols="12"
                            sm="10"
                            align="start"
                            justify="space-around"
                          >
                            <v-btn-toggle
                              style="width:100%"
                              color="purple"
                              tile
                              active-class="pink white--text"
                              dense
                              multiple
                              v-model="selectedLab"
                            >
                              <v-row align="center" dense>
                                <v-col v-for="lab in labs" :key="lab.id">
                                  <v-btn :value="lab.id" block outlined>{{lab.labname}}</v-btn>
                                </v-col>
                              </v-row>
                            </v-btn-toggle>
                          </v-col>
                        </v-row>

                        <!-- Make -->
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2" class="pa-0 pb-0">
                            <v-card align="start" justify="space-around" flat>Make</v-card>
                          </v-col>
                          <v-col
                            class="pt-0 pb-0"
                            cols="12"
                            sm="10"
                            align="start"
                            justify="space-around"
                          >
                            <v-row align="center" dense>
                              <v-col>
                                <v-btn @click="selectXXX" block outlined>3EX</v-btn>
                              </v-col>
                              <v-col>
                                <v-btn @click="selectXXXNone" block outlined>3EX NONE</v-btn>
                              </v-col>
                            </v-row>
                          </v-col>
                        </v-row>
                        <!-- Cut -->
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2" class="pa-0 pb-0">
                            <v-card align="start" justify="space-around" flat>Cut</v-card>
                          </v-col>
                          <v-col
                            class="pt-0 pb-0"
                            cols="12"
                            sm="10"
                            align="start"
                            justify="space-around"
                          >
                            <v-btn-toggle
                              style="width:100%"
                              color="purple"
                              tile
                              dense
                              active-class="pink white--text"
                              multiple
                              v-model="selectedCut"
                            >
                              <v-row align="center" dense>
                                <v-col v-for="cut in cuts" :key="cut.id">
                                  <v-btn :value="cut.id" block outlined>{{cut.name}}</v-btn>
                                </v-col>
                              </v-row>
                            </v-btn-toggle>
                          </v-col>
                        </v-row>
                        <!-- Polish -->
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2" class="pa-0 pb-0">
                            <v-card align="start" justify="space-around" flat>Polish</v-card>
                          </v-col>
                          <v-col
                            class="pt-0 pb-0"
                            cols="12"
                            sm="10"
                            align="start"
                            justify="space-around"
                          >
                            <v-btn-toggle
                              style="width:100%"
                              color="purple"
                              tile
                              active-class="pink white--text"
                              dense
                              multiple
                              v-model="selectedPolish"
                            >
                              <v-row align="center" dense>
                                <v-col v-for="pol in cuts" :key="pol.id">
                                  <v-btn :value="pol.id" block outlined>{{pol.name}}</v-btn>
                                </v-col>
                              </v-row>
                            </v-btn-toggle>
                          </v-col>
                        </v-row>
                        <!-- Symmetry -->
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2" class="pa-0 pb-0">
                            <v-card align="start" justify="space-around" flat>Symmetry</v-card>
                          </v-col>
                          <v-col
                            class="pt-0 pb-0"
                            cols="12"
                            sm="10"
                            align="start"
                            justify="space-around"
                          >
                            <v-btn-toggle
                              style="width:100%"
                              color="purple"
                              tile
                              dense
                              active-class="pink white--text"
                              multiple
                              v-model="selectedSym"
                            >
                              <v-row align="center" dense>
                                <v-col v-for="sym in cuts" :key="sym.id">
                                  <v-btn :value="sym.id" block outlined>{{sym.name}}</v-btn>
                                </v-col>
                              </v-row>
                            </v-btn-toggle>
                          </v-col>
                        </v-row>
                        <!-- Fluro -->
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2" class="pa-0 pb-0">
                            <v-card align="start" justify="space-around" flat>Flurocense</v-card>
                          </v-col>
                          <v-col
                            class="pt-0 pb-0"
                            cols="12"
                            sm="10"
                            align="start"
                            justify="space-around"
                          >
                            <v-btn-toggle
                              style="width:100%"
                              color="purple"
                              tile
                              dense
                              active-class="pink white--text"
                              multiple
                              v-model="selectedFluro"
                            >
                              <v-row align="center" dense>
                                <v-col v-for="fluro in fluros" :key="fluro.id">
                                  <v-btn :value="fluro.id" block outlined>{{fluro.fluroname}}</v-btn>
                                </v-col>
                              </v-row>
                            </v-btn-toggle>
                          </v-col>
                        </v-row>
                      </v-card>
                    </v-flex>
                    <v-flex xs6 md6>
                      <v-card flat class="ml-2 mr-2">
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>SHADE</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>EYE CLEAN</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>MILKY</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                        </v-row>
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>H & A</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>TYPE 2A</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>SEAL</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                        </v-row>
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>TBL INC</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>TBL BLK</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>SIDE INC</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                        </v-row>
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>SIDE BLK</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>INC PTRN</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>INT GRN</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                        </v-row>
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>TBL OPEN</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>PAV OPEN</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>CR OPEN</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                        </v-row>
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>SPRD RATIO</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>REFLECTION</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select flat outlined dense hide-details></v-select>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-card align="end" flat>ROUGH ORIGIN</v-card>
                          </v-col>
                          <v-col cols="12" sm="2">
                            <v-select outlined dense hide-details></v-select>
                          </v-col>
                        </v-row>
                        <v-row align="center" justify="space-around" dense>
                          <v-col cols="12" sm="2" class="pa-0 pb-0">
                            <v-card align="start" justify="space-around" flat></v-card>
                          </v-col>
                          <v-col
                            class="pt-0 pb-0"
                            cols="12"
                            sm="10"
                            align="start"
                            justify="space-around"
                          >
                            <v-row align="center" dense>
                              <v-col>
                                <v-btn @click="searchItem" block outlined>SEARCH</v-btn>
                              </v-col>
                              <v-col>
                                <v-btn block outlined>RESET SEARCH</v-btn>
                              </v-col>
                            </v-row>
                          </v-col>
                        </v-row>
                      </v-card>
                    </v-flex>
                  </v-layout>
                </v-card>
              </v-container>
              <!-- TABLE DETAILS LIKE TABLE% , DEPTH PT -->
              <v-container fluid class="my-0">
                <v-card style="border:1px solid" outlined class="pl-5 pr-5">
                  <div>MEASUREMENTS</div>
                  <v-layout row wrap class="justify-space-around">
                    <v-flex xs6 md12>
                      <v-row align="center" dense>
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>TABLE %</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>CROWN HEIGHT %</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                      </v-row>
                      <v-row align="center" dense>
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>DEPTH %</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>CROWN ANGLE</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                      </v-row>
                      <v-row align="center" dense>
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>LENGTH (mm)</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>PAV DEPTH %</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                      </v-row>
                      <v-row align="center" dense>
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>WIDTH (mm)</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>PAV ANG</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                      </v-row>
                      <v-row align="center" dense>
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>DEPTH (mm)</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>GIRDLE</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                      </v-row>
                      <v-row align="center" dense>
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>RATIO</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                        <v-col cols="12" sm="2">
                          <v-card align="end" flat>OR</v-card>
                        </v-col>
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>% -
                        <v-col cols="12" sm="1">
                          <v-text-field type="number" hide-details dense outlined></v-text-field>
                        </v-col>%
                      </v-row>
                    </v-flex>
                  </v-layout>
                </v-card>
              </v-container>
            </div>
          </v-container>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <v-divider></v-divider>
    <div>
      <v-btn @click="sendToMemo">MEMO ISSUE</v-btn>
      <v-btn @click="holdStones">HOLD</v-btn>
      <a href="http://localhost:8081/diamond/download/stockList.xlsx">STOCK LIST DOWNLOAD & EMAIL</a>
      <ejs-grid
        ref="grid"
        :dataSource="state.gridOrderData"
        :allowPaging="true"
        gridLines="Both"
        rowHeight="40"
        :allowSorting="true"
        :allowFiltering="true"
        :allowExcelExport="true"
        :dataBound="dataBound"
        :pageSettings="pageSettings"
        :filterSettings="filterOptions"
        :toolbarClick="toolbarClick"
        :rowSelected="rowSelected"
      >
        <e-columns>
          <e-column type="checkbox" width="50"></e-column>
          <e-column field="idForStock" headerText="Stk No" textAlign="Left" width="90"></e-column>
          <e-column field="stockid" headerText="Stock#" width="90"></e-column>
          <e-column field="currentweight" headerText="Ct" format="N2" textAlign="Right" width="90"></e-column>
          <e-column field="shape.shapename" headerText="Shape" textAlign="Left" width="90"></e-column>
          <e-column field="color.colorname" headerText="Color" textAlign="Left" width="90"></e-column>
          <e-column field="clarity.clarityname" headerText="Clarity" textAlign="Left" width="90"></e-column>
          <e-column field="cut.name" headerText="Cut" textAlign="Left"></e-column>
          <e-column field="polish.name" headerText="Pol" textAlign="Left"></e-column>
          <e-column field="sym.name" headerText="Sym" textAlign="Left"></e-column>
          <e-column field="fluro.fluroname" headerText="Fluro" textAlign="Left" width="90"></e-column>
          <e-column field="lab.labname" headerText="Lab" textAlign="Left" width="90"></e-column>
          <e-column field="reportno" headerText="Cert #" textAlign="Left" width="90"></e-column>
          <e-column field="rapprice" headerText="Rap" textAlign="Right" width="90"></e-column>
          <e-column field="saleDis" headerText="Dis%" textAlign="Right" width="90"></e-column>
          <e-column field="salePerCarat" headerText="$/Ct" textAlign="Right" width="90"></e-column>
          <e-column field="totalUsd" headerText="Total $" textAlign="Right" width="90"></e-column>
          <e-column field="length" headerText="Length" textAlign="Left" width="90"></e-column>
          <e-column field="width" headerText="Width" textAlign="Left" width="90"></e-column>
          <e-column field="depth" headerText="Depth" textAlign="Left" width="90"></e-column>
          <e-column field="tablePt" headerText="Table %" textAlign="Left" width="90"></e-column>
          <e-column field="depthPt" headerText="DEPTH %" textAlign="Left" width="90"></e-column>
          <e-column field="crAg" headerText="CR AG" textAlign="Left" type="number" width="90"></e-column>
          <e-column field="crHt" headerText="CR HT" textAlign="Left" width="90"></e-column>
          <e-column field="pavAg" headerText="PV AG" textAlign="Left" width="90"></e-column>
          <e-column field="depth" headerText="PV DP" textAlign="Left" width="90"></e-column>
          <e-column field="culetSize" headerText="CULET" textAlign="Left" width="90"></e-column>
          <e-column field="girdle" headerText="GRDL" textAlign="Left" width="90"></e-column>
          <e-column field="girdleCondition" headerText="GRDL CON" textAlign="Left" width="90"></e-column>
          <e-column field="girdlePct" headerText="GRDL %" textAlign="Left" width="90"></e-column>
          <e-column field="keyToSymbol" headerText="KET TO SYMBOL" textAlign="Left"></e-column>
          <e-column field="infoMessage" headerText="REPORT COMMENT" textAlign="Left" width="90"></e-column>
          <e-column field="inscription" headerText="INCRIPTION" textAlign="Left" width="90"></e-column>
          <e-column field="starLn" headerText="STAR LN" textAlign="Left" width="90"></e-column>
          <e-column field="lrHalf" headerText="LOWER HF" textAlign="Left" width="90"></e-column>
          <e-column field="memo" headerText="MEMO NO" textAlign="Left" width="90"></e-column>
        </e-columns>
        <e-aggregates>
          <e-aggregate>
            <e-columns>
              <e-column type="Sum" field="currentweight" :footerTemplate="footerSum"></e-column>
              <e-column type="Sum" field="totalUsd" :footerTemplate="footerUsd"></e-column>
            </e-columns>
          </e-aggregate>
        </e-aggregates>
      </ejs-grid>
    </div>

    <!-- DIALOGUE FOR MEMO     -->
    <v-row justify="center">
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">{{holdOrMemo}}</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-autocomplete
                    style="width:200px"
                    required
                    v-model="buyer"
                    auto-select-first
                    :items="buyers"
                    color="blue"
                    item-text="name"
                    dense
                    label="BUYER NAME"
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-autocomplete
                    style="width:200px"
                    required
                    v-model="salePerson"
                    auto-select-first
                    :items="salePersons"
                    color="blue"
                    item-text="name"
                    dense
                    label="SALES PERSON"
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </v-col>
              </v-row>
              <v-row>
                <v-data-table
                  :headers="headers"
                  hide-default-footer
                  :items="memoEligibleDiamonds"
                  :items-per-page="50"
                  class="elevation-1"
                >
                  <template
                    v-slot:item.id="{ item }"
                  >{{(memoEligibleDiamonds.map(function(x) {return x.id; }).indexOf(item.id)) + 1}}</template>
                </v-data-table>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
            <v-btn
              color="blue darken-1"
              v-if="this.holdOrMemo == 'MEMO ISSUE'"
              text
              @click="saveMemo"
            >Save</v-btn>
            <v-btn
              color="blue darken-1"
              v-if="this.holdOrMemo == 'STONE HOLD'"
              text
              @click="saveHold"
            >Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
import Vue from "vue";
import {
  Page,
  Sort,
  Filter,
  Resize,
  Aggregate,
  Group,
  Toolbar,
  ExcelExport
} from "@syncfusion/ej2-vue-grids";
import axios from "axios";
import commonVari from "../models/commonVari.js";

export default {
  data() {
    return {
      buyer: null,
      buyers: [],
      salePerson: null,
      salePersons: [],
      holdOrMemo: "",
      dialog: false,
      memoEligibleDiamonds: [],
      holdEligibleDiamonds:[],
      selectedDiamonds: [],
      toolbarOptions: ["ExcelExport"],

      FromColor: null,
      ToColor: null,
      FromClarity: null,
      ToClarity: null,

      pointermodel: [],

      selectedOption: "",
      options: "byStockId",
      filterOptions: { type: "Excel" },
      searchResult: [],
      state: {
        gridOrderData: []
      },
      country: [],
      isPointer: false,
      fromCarat: 0,
      toCarat: 0,
      priceFrom: 0,
      priceTo: 0,
      disFrom: 0,
      disTo: 0,
      rapback: 0,
      packetno: "",
      reportno: "",
      memono: "",
      API_URL: commonVari.API_URL,
      company: [],
      companies: [],
      selectedShapes: [],
      shapes: [],
      colors: [],
      clarities: [],
      cuts: [],
      fluros: [],
      labs: [],
      countries: [],
      selectedCut: [],
      selectedPolish: [],
      selectedSym: [],
      selectedFluro: [],
      selectedLab: [],
      selectedColor: [],
      selectedClarity: [],
      selectedColorGroup: [],
      selectedPointers: [],
      pointers: [
        { name: "+10.00 Up", fromSize: "0.01", toSize: "0.22" },
        { name: "+5.00 Up", fromSize: "0.23", toSize: "0.29" },
        { name: "+4.00 Up", fromSize: "0.30", toSize: "0.34" },
        { name: "+3.00 Up", fromSize: "0.35", toSize: "0.39" },
        { name: "+2.00 Up", fromSize: "0.40", toSize: "0.49" },
        { name: "+1.50 Up", fromSize: "0.50", toSize: "0.59" },
        { name: "+1.00 Up", fromSize: "0.60", toSize: "0.69" },
        { name: "+0.50 Up", fromSize: "0.60", toSize: "0.69" },
        { name: "Up to 0.50", fromSize: "0.60", toSize: "0.69" }
      ],
      headers: [
        { text: "NO", value: "id" },
        { text: "STOCK #", value: "stockid" },
        { text: "WEIGHT", value: "currentweight" },
        { text: "SHAPE", value: "shape.shapename" },
        { text: "COLOR", value: "color.colorname" },
        { text: "CLARITY", value: "clarity.clarityname" },
        { text: "LAB", value: "lab.labname" },
        { text: "REPORT #", value: "reportno" }
      ],
      pageSettings: { pageSize: 50 },
      footerSum: function() {
        return {
          template: Vue.component("sumTemplate", {
            template: `<span>{{(data.Sum).toFixed(2)}}</span>`,
            data() {
              return { data: {} };
            }
          })
        };
      },
      footerUsd: function() {
        return {
          template: Vue.component("sumTemplate", {
            template: `<span>{{(data.Sum).toFixed(2)}}</span>`,
            data() {
              return { data: {} };
            }
          })
        };
      },
      calculatePerCarat: function() {
        return {
          template: Vue.component("sumTemplate", {
            template: `<span>{{(data.rapprice +(data.rapprice* data.saleDis/100) )}}</span>`,
            data() {
              return { data: {} };
            }
          })
        };
      }
    };
  },

  provide: {
    grid: [Page, Sort, Filter, Resize, Aggregate, Group, Toolbar, ExcelExport]
  },
  watch: {
    selectedPointers: function() {
      if (this.selectedPointers.length > 0) {
        this.isPointer = true;
      } else {
        this.isPointer = false;
      }
    },
    selectedColorGroup: function() {
      this.selectedColor = [];
      this.selectedColorGroup.forEach(item => {
        if (item == "DEF") {
          this.selectedColor.push(1);
          this.selectedColor.push(2);
          this.selectedColor.push(3);
        }
        if (item == "GHI") {
          this.selectedColor.push(4);
          this.selectedColor.push(5);
          this.selectedColor.push(6);
        }
        if (item == "JKL") {
          this.selectedColor.push(7);
          this.selectedColor.push(8);
          this.selectedColor.push(9);
        }
      });
    },
    selectedDiamonds: function() {
      this.memoEligibleDiamonds = [];
      this.selectedDiamonds.forEach(item => {
        if (item.memo == null || parseFloat(item.memo) == 0) {
          this.memoEligibleDiamonds.push(item);
        }
      });
    }
  },
  computed: {
    perCarat: function() {
      return "digesh";
    },
    likesAllFruit() {
      return this.selectedColor.length === this.colors.length;
    },
    likesAllFruit1() {
      return this.selectedClarity.length === this.clarities.length;
    },
    likesSomeFruit() {
      return this.selectedColor.length > 0 && !this.likesAllFruit;
    },
    icon() {
      if (this.likesAllFruit) return "mdi-close-box";
      if (this.likesSomeFruit) return "mdi-minus-box";
      return "mdi-checkbox-blank-outline";
    }
  },

  created() {
    this.getAllShapes();
    this.getAllColors();
    this.getAllClarity();
    this.getAllCuts();
    this.getAllFluro();
    this.getAllLabs();
    this.getAllCountries();
    this.getAllCompanies();
    this.getAllBuyers();
    this.getAllSalesPerson();
  },

  methods: {
    getAllBuyers() {
      axios
        .get(this.API_URL + "ledger/getBuyers")
        .then(({ data }) => (this.buyers = data));
    },
    getAllSalesPerson() {
      axios
        .get(this.API_URL + "ledger/getSalePersons")
        .then(({ data }) => (this.salePersons = data));
    },
    sendToMemo() {
      this.holdOrMemo = "MEMO ISSUE";
      this.dialog = true;
    },
    async saveHold() {
      var stoneList = [];

      this.holdEligibleDiamonds.forEach(item => {
        stoneList.push(item.stockid);
      });

      const formData = new FormData();
      formData.append("date", new Date());
      formData.append("party", JSON.stringify(this.buyer));
      formData.append("saleperson", JSON.stringify(this.salePerson));
      formData.append("stonelist", stoneList);

      await axios
        .post(this.API_URL + "memo/saveMemo", formData)
        .then(() => {})
        .catch(error => {
          alert(error);
        });
      this.dialog = false;
    },
    async saveMemo() {
      var stoneList = [];

      this.memoEligibleDiamonds.forEach(item => {
        stoneList.push(item.stockid);
      });

      const formData = new FormData();
      formData.append("date", new Date());
      formData.append("party", JSON.stringify(this.buyer));
      formData.append("saleperson", JSON.stringify(this.salePerson));
      formData.append("stonelist", stoneList);

      await axios
        .post(this.API_URL + "memo/saveMemo", formData)
        .then(() => {})
        .catch(error => {
          alert(error);
        });
      this.dialog = false;
    },
    holdStones() {
      
      this.holdOrMemo = "STONE HOLD";
      this.dialog = true;
    
    },
    rowSelected() {
      this.selectedDiamonds = this.$refs.grid.getSelectedRecords(); // Get the selected records.
    },
    toolbarClick: function(args) {
      if (args.item.id === "Grid_excelexport") {
        // 'Grid_excelexport' -> Grid component id + _ + toolbar item name
        this.$refs.grid.excelExport();
      }
    },
    toggle() {
      this.$nextTick(() => {
        if (this.likesAllFruit) {
          this.selectedColor = [];
        } else {
          this.selectedColor = this.colors.slice();
        }
      });
    },
    toggle1() {
      this.$nextTick(() => {
        if (this.likesAllFruit1) {
          this.selectedClarity = [];
        } else {
          this.selectedClarity = this.clarities.slice();
        }
      });
    },
    edit(index, item) {
      if (!this.editing) {
        this.editing = item;
        this.index = index;
      } else {
        this.editing = null;
        this.index = -1;
      }
    },
    filter(item, queryText, itemText) {
      if (item.header) return false;

      const hasValue = val => (val != null ? val : "");

      const text = hasValue(itemText);
      const query = hasValue(queryText);

      return (
        text
          .toString()
          .toLowerCase()
          .indexOf(query.toString().toLowerCase()) > -1
      );
    },
    updateData() {
      // var search = this.state.gridOrderData;

      this.state.gridOrderData.forEach(item => {
        var saleDis = 0;
        if (item.saleDis == null && !isNaN(item.saleDis)) {
          saleDis = 0;
        } else {
          saleDis = parseFloat(item.saleDis);
        }

        var rapb = 0;
        if (!isNaN(this.rapback)) {
          rapb = parseFloat(this.rapback);
        }
        var finalDis = saleDis + rapb;

        var rap = item.rapprice;

        if (rap == null) {
          rap = 0;
        }

        var salePerCarat =
          parseFloat(rap) + (parseFloat(rap) * parseFloat(finalDis)) / 100;
        item.salePerCarat = salePerCarat.toFixed(2);

        var weight = item.currentweight;
        var total = parseFloat(weight) * parseFloat(salePerCarat);
        item.totalUsd = total.toFixed(2);

        item.saleDis = finalDis;
      });

      this.state.gridOrderData = [...this.state.gridOrderData];
    },
    getAllCompanies() {
      axios
        .get(this.API_URL + "companyrelation/getAllCompany")
        .then(({ data }) => (this.companies = data));
    },

    shapeImage(shape) {
      // return "../Images/Shape/"+shape.shapename+".png";
      // ../Images/Shape/ROUND.png
      var images = require.context("../Images/Shape/", false, /\.png$/);
      return images("./" + shape.shapename + ".png");
      // var image = "../Images/Shape/"+shape.shapename+".png";
      // return image;
    },
    dataBound: function() {
      this.$refs.grid.autoFitColumns([
        "idForStock",
        "stockid",
        "keyToSymbol",
        "shape.shapename",
        "color.colorname",
        "clarity.clarityname",
        "cut.name",
        "polish.name",
        "sym.name",
        "fluro.fluroname",
        "lab.labname",
        "reportno",
        "rapprice",
        "length",
        "width",
        "depth",
        "tablePt",
        "depthPt",
        "crAg",
        "crHt",
        "pavAg",
        "depth",
        "culetSize",
        "girdle",
        "girdleCondition",
        "girdlePct",
        "keyToSymbol",
        "infoMessage",
        "inscription",
        "starLn",
        "lrHalf",
        "saleDis",
        "salePerCarat",
        "totalUsd"
      ]);
    },
    selectXXX() {
      this.selectedCut = [];
      this.selectedPolish = [];
      this.selectedSym = [];

      this.selectedCut.push(1);
      this.selectedPolish.push(1);
      this.selectedSym.push(1);
    },
    selectXXXNone() {
      this.selectedCut = [];
      this.selectedPolish = [];
      this.selectedSym = [];
      this.selectedFluro = [];

      this.selectedCut.push(1);
      this.selectedPolish.push(1);
      this.selectedSym.push(1);
      this.selectedFluro.push(1);
    },
    searchItem() {
      if (this.selectedShapes.length == 0) {
        this.shapes.forEach(shape => {
          this.selectedShapes.push(shape.id);
        });
      }
      if (this.selectedColor.length == 0) {
        this.colors.forEach(shape => {
          this.selectedColor.push(shape.id);
        });
      }
      if (this.selectedClarity.length == 0) {
        this.clarities.forEach(shape => {
          this.selectedClarity.push(shape.id);
        });
      }
      if (this.selectedCut.length == 0) {
        this.cuts.forEach(shape => {
          this.selectedCut.push(shape.id);
        });
      }
      if (this.selectedPolish.length == 0) {
        this.cuts.forEach(shape => {
          this.selectedPolish.push(shape.id);
        });
      }
      if (this.selectedSym.length == 0) {
        this.cuts.forEach(shape => {
          this.selectedSym.push(shape.id);
        });
      }
      if (this.selectedFluro.length == 0) {
        this.fluros.forEach(shape => {
          this.selectedFluro.push(shape.id);
        });
      }
      if (this.selectedLab.length == 0) {
        this.labs.forEach(shape => {
          this.selectedLab.push(shape.id);
        });
      }
      this.reportno = "";
      this.packetno = "";
      this.memono = "";
      if (this.options == "byStockNo") {
        this.packetno = this.selectedOption;
      } else if (this.options == "byReportNo") {
        this.reportno = this.selectedOption;
      } else if (this.option == "byMemoNo") {
        this.memono = this.selectedOption;
      }

      const formData = new FormData();
      formData.append("shape", this.selectedShapes);
      formData.append("color", this.selectedColor);
      formData.append("clarity", this.selectedClarity);
      formData.append("cut", this.selectedCut);
      formData.append("polish", this.selectedPolish);
      formData.append("sym", this.selectedSym);
      formData.append("fluro", this.selectedFluro);
      formData.append("lab", this.selectedLab);
      formData.append("fromCarat", this.fromCarat);
      formData.append("toCarat", this.toCarat);
      formData.append("fromPrice", this.priceFrom);
      formData.append("toPrice", this.priceTo);
      formData.append("fromDis", this.disFrom);
      formData.append("toDis", this.disTo);
      formData.append("packetno", this.packetno);
      formData.append("reportno", this.reportno);
      formData.append("pointers", JSON.stringify(this.selectedPointers));
      formData.append("location", JSON.stringify(this.country));

      axios
        .post(this.API_URL + "diamond/diamondSearchEngine", formData)
        .then(data => {
          this.searchResult = data.data;
          console.log(this.searchResult);
          this.searchResult.forEach(item => {
            var rap = item.rapprice;
            var dis = item.saleDis;
            if (rap == null) {
              rap = 0;
            }
            if (dis == null) {
              dis = 0;
            }
            var salePerCarat =
              parseFloat(rap) + (parseFloat(rap) * parseFloat(dis)) / 100;
            item.salePerCarat = salePerCarat.toFixed(2);

            var weight = item.currentweight;
            var total = parseFloat(weight) * parseFloat(salePerCarat);
            item.totalUsd = total.toFixed(2);
          });
          this.state.gridOrderData = this.searchResult;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getAllCountries() {
      axios
        .get(this.API_URL + "country/getAllCountry")
        .then(({ data }) => (console.log(data), (this.countries = data)));
    },
    getAllShapes() {
      axios
        .get(this.API_URL + "shape/getAllShapes")
        .then(({ data }) => (this.shapes = data));
    },
    getAllColors() {
      axios
        .get(this.API_URL + "color/getAllColors")
        .then(({ data }) => (this.colors = data));
    },
    getAllClarity() {
      axios
        .get(this.API_URL + "clarity/getAllClarity")
        .then(({ data }) => (this.clarities = data));
    },
    getAllCuts() {
      axios
        .get(this.API_URL + "cps/getAllcps")
        .then(({ data }) => (this.cuts = data));
    },
    getAllFluro() {
      axios
        .get(this.API_URL + "fluro/getAllFluro")
        .then(({ data }) => (this.fluros = data));
    },
    getAllLabs() {
      axios
        .get(this.API_URL + "lab/getAllLabs")
        .then(({ data }) => (this.labs = data));
    }
  }
};
</script>

