<template>
  <div class="container">
    <v-alert v-model="success" type="success" dismissible>{{messageFromServer}}</v-alert>

    <div class="row">
      <!-- FOR EDITING DATA -->

      <b-form class="column" style="width:40%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="Country Name :" label-for="countryname">
          <b-form-input
            id="countryname"
            v-model="currentconstate.statename"
            required
            placeholder="Enter Country name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="State Name :" label-for="statecode">
          <b-form-input
            id="statecode"
            v-model="currentconstate.statecode"
            required
            placeholder="Enter state code only"
          ></b-form-input>
        </b-form-group>
        <v-autocomplete
          v-model="currentconstate.country"
          :items="countries"
          color="white"
          item-text="countryname"
          label="Country"
          return-object
        ></v-autocomplete>

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>

      <!-- FOR SAVING DATA -->
      <b-form class="column" style="width:40%" @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="State Name :" label-for="statename">
          <b-form-input
            id="statename"
            v-model="constate.statename"
            required
            placeholder="Enter state name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="State Code :" label-for="statecode">
          <b-form-input
            id="statecode"
            v-model="constate.statecode"
            required
            placeholder="Enter state code only"
          ></b-form-input>
        </b-form-group>
        <v-autocomplete
          v-model="constate.country"
          :items="countries"
          color="white"
          item-text="countryname"
          label="Country"
          return-object
          dense
          outlined
        ></v-autocomplete>

        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
      <!-- table start       -->
      <div class="column" style="width:60%">
        <v-data-table
          :headers="headers"
          :items="constates"
          :items-per-page="10"
          class="elevation-1"
        >
          <template v-slot:item="row">
            <tr v-bind:key="row.item.id">
              <td>{{row.item.statename}}</td>
              <td>{{row.item.statecode}}</td>
              <td>{{row.item.country.countryname}}</td>

              <td>
                <a class="text-success" @click="isEditing=true; selectCountryState(row.item)">
                  <font-awesome-icon icon="edit" />
                </a>
                &emsp;
                <a class="text-danger" @click="deleteCountryState(row.item)">
                  <font-awesome-icon icon="trash-alt" />
                </a>
              </td>
            </tr>
          </template>
        </v-data-table>
      </div>
    </div>
  </div>
</template>

<script>
import CountryState from "../models/CountryState";
import Country from "../models/Country";
import axios from "axios";
import commonVari from "../models/commonVari.js"
export default {
  name: "CountryState",

  data() {
    return {
      API_URL: commonVari.API_URL,
      success: false,
      messageFromServer: "",
      headers: [
        { text: "State", value: "statename" },
        { text: "State code", value: "statecode" },
        { text: "Action", value: "" }
      ],
      country: new Country(),
      isEditing: false,
      countries: [],
      currentCountry: new Country(),
      constate: new CountryState(),
      constates: [],
      currentconstate: new CountryState()
    };
  },
  created() {
    this.getAllStates();
    this.getAllCountries();
  },
  methods: {
    getAllCountries() {
      axios
        .get(this.API_URL + "country/getAllCountry")
        .then(({ data }) => (this.countries = data));
    },
    selectCountryState(country) {
      this.currentconstate = country;
    },
    getAllStates() {
      axios
        .get(this.API_URL + "states/getAllStates")
        .then(({ data }) => (this.constates = data));
    },
    saveData() {
      
      axios.post(this.API_URL + "states/saveState", this.constate).then(data => {
        if (data.data.error) {
          console.log(data.error);
          this.messageFromServer = "Not Saved ... ";
          this.success = true;
        } else {
          this.messageFromServer = "Saved !!!!";
          this.success = true;
        }

        this.getAllStates();
      });
    },
    updateData() {
      // this.constate.country = this.country;
      axios
        .post(this.API_URL + "states/updateState", this.currentconstate)
        .then(data => {
          console.log(data);
          this.isEditing = false;
          this.getAllStates();
        });
    },
    deleteCountryState(country) {
      this.currentconstate = country;
      if (confirm) {
        axios
          .post(this.API_URL + "states/deleteStates", this.currentconstate)
          .then(data => {
            console.log(data);
            this.isEditing = false;
            this.getAllStates();
          });
      }
    }
  }
};
</script>
<style scoped>
.container {
  margin: 18px;
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}
</style>