<template>
  <div class="container">
    <div class="row">
<!-- TABLE DATA         -->
      <div class="column" style="width:60%">
        <div class="table-responsive" style="overflow-x:auto;">
          <table class="table table-condensed">
            <thead>
              <tr>
                <th>Color</th>
                <th>Color Reference</th>
                <th>Display Order</th>
                <th>Edit</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in colors" v-bind:key="item.id">
                <td>{{item.colorname}}</td>
                <td>{{item.color_ref}}</td>
                <td>{{item.orderId}}</td>
                <td><a class="text-success" variant="primary" @click="isEditing=true; selectColor(item)"><font-awesome-icon icon="edit"/></a></td>
                    <td><a class="text-danger" variant="primary" @click="deleteColor(item)"><font-awesome-icon icon="trash-alt"/></a>

                  <!-- <td><b-button variant="primary" @click="isEditing=true; selectColor(item)">Edit</b-button></td>
                    <td><b-button variant="primary" @click="deleteColor(item)">Delete</b-button> -->
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <v-divider vertical></v-divider>
<!-- FOR SAVING DATA -->
      <b-form class="column" style="width:30%"  @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="Color Name :" label-for="colorname">
          <b-form-input
            id="colorname"
            v-model="color.colorname"
            required
            placeholder="Enter Color name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Color Reference :" label-for="colorref">
          <b-form-input
            id="colorref"
            v-model="color.color_ref"
            required
            placeholder="Enter Color Reference like ( ,D, ,E, ,F, )"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Color Display Order :" label-for="colordo">
          <b-form-input
            type="number"
            id="colordo"
            v-model="color.orderId"
            required
            placeholder="Enter Color Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
<!-- FOR EDITING DATA -->

      <b-form class="column" style="width:30%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="Color Name :" label-for="colorname">
          <b-form-input
            id="colorname"
            v-model="currentColor.colorname"
            required
            placeholder="Enter Color name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Color Reference :" label-for="colorref">
          <b-form-input
            id="colorref"
            v-model="currentColor.color_ref"
            required
            placeholder="Enter Color Reference like ( ,D, ,E, ,F, )"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Color Display Order :" label-for="colordo">
          <b-form-input
            type="number"
            id="colordo"
            v-model="currentColor.orderId"
            required
            placeholder="Enter Color Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
import Color from "../models/Color";
import axios from "axios";
import commonVari from "../models/commonVari.js"
export default {
  name: "Color",
  data() {
    return {
      API_URL: commonVari.API_URL,
      color: new Color(),
      isEditing: false,
      colors: [], 
      currentColor: new Color(),
    };
  },
  created() {
    this.getAllColors();
  },
  methods: {
      selectColor(color){
          this.currentColor = color;
      },
    getAllColors() {
      axios
        .get(this.API_URL + "color/getAllColors")
        .then(({ data }) => (this.colors = data));
    },
    saveData() {
      axios.post(this.API_URL + "color/saveColor", this.color).then(() => {
        
        this.color.colorname = "";
        this.color.color_ref = "";
        this.color.orderId = "";
        this.getAllColors();
      });
    },
    updateData() {
      axios.post(this.API_URL + "color/updateColor", this.currentColor).then(() => {
        
        this.isEditing = false;
        this.color.colorname = "";
        this.color.color_ref = "";
        this.color.orderId = "";
        this.getAllColors();
      });
    }, 
    deleteColor(color){
         
      this.currentColor = color;
      axios.post(this.API_URL + "color/deleteColor", this.currentColor).then(() => {
        
        this.isEditing = false;
        this.getAllColors();
      });
    }

  }
};
</script>

<style scoped>
.container {
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}

</style>