<template>
  <div>
    <v-toolbar class="toolbase" elevation="3" dense>
      <v-toolbar-title elevation="3">SALE REMARK REPORT</v-toolbar-title>
    </v-toolbar>
    <v-snackbar v-model="snackbar" :color="color" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-row>
      <v-col cols="12" md="3">
        <v-radio-group v-model="option" row>
          <v-radio label="BY STOCK #" value="byStockId"></v-radio>
          <v-radio label="BY STOCK NO." value="byStockNo"></v-radio>
          <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
        </v-radio-group>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
      </v-col>

      <v-col cols="12" md="1">
        <v-btn outlined color="indigo" @click="selectDataInTables">GET RESULT</v-btn>
      </v-col>
    </v-row>
    <v-card
      flat
      color="teal lighten-3"
      v-for="(items, index) in this.trackingResults"
      v-bind:key="index"
      style="height:100%"
    >
      <v-chip class="ma-2" color="pink" label text-color="white">STOCK NO : {{items.stockid}}</v-chip>

      <v-card-text class="pt-1 pb-1">
        <v-data-table
          dense
          hide-default-footer
          :headers="headers"
          :items="items.saleRemark"
          class="elevation-1"
        >
          <template v-slot:item="row">
            <tr v-bind:key="row.item.id">
              <td>{{row.item.remark}}</td>
            </tr>
          </template>
        </v-data-table>
      </v-card-text>

      <v-divider class="pt-0"></v-divider>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
import commonVari from "../models/commonVari.js";
import { DetailRow, Sort } from "@syncfusion/ej2-vue-grids";

export default {
  data() {
    return {
      color: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      requiredParams: "",
      option: null,
      actiontaken: [],
      API_URL: commonVari.API_URL,
      reportnos: [],
      stocknos: [],
      finalStocknos: [],
      trackingResults: [],
      filteredObjects: [],
      trackingResults1: [],
      filteredObjects1: [],
      purchaseline: {},
      gialine: {},
      singleResult: [],

      headers: [{ text: "SALE REMARK", value: "saleRemark.remark" }]
    };
  },
  provide: {
    grid: [DetailRow, Sort]
  },
  created() {},
  computed: {},

  methods: {
    getStockNo(item) {
      return item.diamond.stockid;
    },
    selectDataInTables() {
      if (this.option != null) {
        var option = this.option;
        var reqQuery = "";
        const formData = new FormData();
        if (option == "byStockId") {
          reqQuery = "diamond/getAllByStockId";
          formData.append("stockId", this.requiredParams);
        } else if (option == "byReportNo") {
          reqQuery = "diamond/getAllByReportNo";
          formData.append("reportno", this.requiredParams);
        } else if (option == "byStockNo") {
          reqQuery = "diamond/getAllByStockNo";
          formData.append("stockNo", this.requiredParams);
        }

        axios
          .post(this.API_URL + reqQuery, formData)
          .then(response => {
            console.log(response.data);
            this.trackingResults = response.data;
          })
          .catch(error => {
            console.log(error);
          });
      } else {
        alert("Please specify valid input");
      }
    }
  }
};
</script>

