export default class Expense {
    constructor(id, date, chargeParty, company, chargeAcc, paymentFrom, refno, remark, invoiceAmount,
        invoiceCgst,
        invoiceSgst,
        invoiceIgst,
        invoiceNetTotal,
        invoiceUsdTotal,
        invoiceTds) {
        this.id = id;
        this.paymentFrom = paymentFrom;
        this.date = date;
        this.company = company;
        this.chargeAcc = chargeAcc;
        this.chargeParty = chargeParty;
        this.refno = refno;
        this.remark = remark;
        this.invoiceAmount = invoiceAmount;
        this.invoiceCgst = invoiceCgst;
        this.invoiceSgst = invoiceSgst;
        this.invoiceIgst = invoiceIgst;
        this.invoiceNetTotal = invoiceNetTotal;
        this.invoiceUsdTotal = invoiceUsdTotal;
        this.invoiceTds = invoiceTds;

    }
}