export default class PurchaseBill {
    constructor(transdate,
        terms,
        duedate,
        caratTotal,
        usdTotal,
        inrTotal,
        cgstAmt,
        sgstAmt,
        igstAmt,
        roundup,
        invoiceAmt,
        invoiceNo,
        invoiceId,
        party,
        company,
        ThirdPartyBill,
        BillReceivedFromParty,
        BillGivenToConcern,
        BillReceivedFromConcern,
        ChqReceivedFromConcern,
        ChqGivenToParty,
        remark,
        comissionRate,
        comissionAmt,
        netAmt,
        billForparty

    ) {
        this.billForparty = billForparty;
        this.comissionRate = comissionRate;
        this.comissionAmt = comissionAmt;
        this.netAmt = netAmt;
        this.ThirdPartyBill = ThirdPartyBill;
        this.BillReceivedFromParty = BillReceivedFromParty;
        this.BillGivenToConcern = BillGivenToConcern;
        this.BillReceivedFromConcern = BillReceivedFromConcern;
        this.ChqGivenToParty = ChqGivenToParty;
        this.ChqReceivedFromConcern = ChqReceivedFromConcern;
        this.remark = remark;
        this.company = company;
        this.transdate = transdate
        this.terms = terms
        this.duedate = duedate
        this.caratTotal = caratTotal
        this.usdTotal = usdTotal
        this.inrTotal = inrTotal;
        this.cgstAmt = cgstAmt
        this.sgstAmt = sgstAmt
        this.igstAmt = igstAmt
        this.roundup = roundup
        this.invoiceAmt = invoiceAmt
        this.invoiceNo = invoiceNo
        this.invoiceId = invoiceId
        this.party = party

    }
}