<script>
// import homepage from './components/homepage'
import axios from "axios";
import Loader from "./components/_loader";

export default {
  components: {
    Loader
  },
  
  data() {
    return {
      processList: [],
      proList: [],
      proListShine: []
    };
  },
  computed:{
    loggedIn(){
      return this.$store.getters.loggedIn
    }
  },
  name: "app",
  methods: {
    pushToRoute(nameOfRoute, companyId, companyName, process, purpose) {
      this.$router.push({
        name: nameOfRoute,
        params: {
          companyId: companyId,
          company: companyName,
          process: process,
          purpose: purpose
        }
      });
    }
  },
  created() {
    this.proList = this.$store.state.processList;
    this.proListShine = this.$store.state.processListForShine;
    // this.proList.forEach(element => {
    //   this.processList.push(element.name);
    // });

    axios.interceptors.request.use(
      config => {
        this.$store.commit("LOADER", true);
        return config;
      },
      error => {
        this.$store.commit("LOADER", false);
        return Promise.reject(error);
      }
    );

    // Add a response interceptor
    axios.interceptors.response.use(
      response => {
        this.$store.commit("LOADER", false);
        return response;
      },
      error => {
        return Promise.reject(error);
      }
    );
  }
  // components:{
  //   homepage,

  // },
};
</script>
