<template>
  <div>
    <v-container>
      <v-toolbar :tile="false" class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">{{this.$route.params.company}} | PAYMENT / RECEIPT REPORTS</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-container>
    <v-container>
      <v-row>
        <!-- <v-col cols="12" md="2">
          <v-autocomplete
            required
            v-model="selectedCompany"
            :items="companies"
            color="blue"
            item-text="name"
            solo
            dense
            outlined
            hide-details
            opeque
            return-object
          ></v-autocomplete>
        </v-col> -->
        <v-col cols="12" md="2">
          <v-btn outlined color="indigo" @click="getReport()">Retrive Report</v-btn>
        </v-col>
        <v-col cols="12" md="4">
          <!-- <v-card-title> -->
            <v-text-field
              outlined
              dense
              v-model="search"
              label="Search"
              append-icon="mdi-magnify"
              hide-details
            ></v-text-field>
          <!-- </v-card-title> -->
        </v-col>
      </v-row>
    </v-container>

    <!-- <v-divider></v-divider> -->

    <v-data-table
    
      :headers="headers"
      :items="purchaselists"
      :items-per-page="50"
      calculate-widths
      mobile-breakpoint
      :search="search"
      class="elevation-1"
    >
      <template v-slot:item="row">
        <tr v-bind:key="row.item.id">
          <td>
            <v-chip dense :color="getColor(row.item.paymentType)" dark>{{row.item.paymentType }}</v-chip>
          </td>
          <td>{{row.item.opendate}}</td>
          <td>{{row.item.company.name}}</td>
          <td>{{row.item.cashBank.name}}</td>
          <td>{{row.item.party.name}}</td>
          <td>{{row.item.usdAmt}}</td>
          <td>{{row.item.curRate}}</td>
          <td>{{row.item.paymentAmt}}</td>
          <td>{{row.item.receiptAmt}}</td>
          <td>{{row.item.remark}}</td>
        </tr>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import CompanyRelation from "../models/CompanyRelation";
import commonVari from "../models/commonVari.js";
import axios from "axios";
export default {
  data() {
    return {
      search: "",
      API_URL: commonVari.API_URL,
      purchaselists: [],
      companies: [],
      selectedCompany: new CompanyRelation(),
      headers: [
        { text: "TYPE", value: "paymentType" },
        { text: "DATE", value: "opendate" },
        { text: "COMPANY", value: "company.name" },
        { text: "PAYMENT MODE", value: "cashBank.name" },
        { text: "PARTY NAME", value: "party.name" },
        { text: "AMOUNT $", value: "usdAmt" },
        { text: "CUR RATE", value: "curRate" },
        { text: "PAYMENT AMOUNT", value: "paymentAmt" },
        { text: "RECEIPT AMOUNT", value: "receiptAmt" },
        { text: "REMARK", value: "remark" }
      ]
    };
  },
  created() {
    this.companyname = this.$route.params.company;
    this.companyId = this.$route.params.companyId;
    this.getAllCompanies();
  },

  methods: {
    getColor(calories) {
      if (calories == "PAYMENT") return "orange";
      else if (calories == "RECEIPT") return "green";
      else return "blue";
    },
    getAllCompanies() {
      const formData = new FormData();
      formData.append("name", this.companyname);
      axios
        .post(
          this.API_URL + "companyrelation/getCompanyByName",
          formData
        )
        .then(({ data }) => (this.selectedCompany = data));
    },
    getReport() {
      axios
        .post(this.API_URL + "payment/getAllPaymentsByCompany", this.selectedCompany)
        .then(({ data }) => (console.log(data), this.purchaselists = data));
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(12, 3, 94);
  color: white;
}
.container {
  max-width: 1185px;
  min-width: -webkit-fill-available;
}
</style>