export default class ExpenseEntry {
    constructor(id, opendate, stockid, remark, paidBy, fees, charge, amount, cgstAmt, sgstAmt, igstAmt, totalAmt, convRate, totalAmtUsd, tdsAmt ) {
        this.id = id;
        this.opendate = new Date().toISOString().substr(0, 10);
        this.stockid = stockid;
        this.fees = fees;
        this.charge = charge;
        this.paidBy = paidBy;
        this.amount = amount;
        this.cgstAmt = cgstAmt;
        this.sgstAmt = sgstAmt;
        this.igstAmt = igstAmt;
        this.totalAmt = totalAmt;
        this.convRate = convRate;
        this.totalAmtUsd = totalAmtUsd;
        this.tdsAmt = tdsAmt;
        this.remark = remark;
        
    }
}