<template>
  <div>
    <v-snackbar v-model="snackbar" :color="color" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">RECUT UPDATE</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-container>
    <div>
      <v-row>
        <v-col cols="12" md="3">
          <v-radio-group v-model="option" row>
            <v-radio label="BY STOCK #" value="byStockId"></v-radio>
            <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
          </v-radio-group>
        </v-col>

        <v-col cols="12" md="3">
          <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
        </v-col>
        <v-col cols="12" md="1">
          <v-btn outlined color="indigo" @click="selectDataInTables">LOAD DATA</v-btn>
        </v-col>
      </v-row>
    </div>
    <v-chip class="ma-1" color="green" text-color="white">PURCHASE DETAILS</v-chip>
    <v-card flat>
      <v-card-text>
        <v-data-table
          dense
          :headers="headers"
          :items="purchaseDetails"
          hide-default-footer
          class="elevation-1"
        >
          <template
            v-slot:item.id="{ item }"
          >{{(purchaseDetails.map(function(x) {return x.id; }).indexOf(item.id)) + 1}}</template>
        </v-data-table>
      </v-card-text>
    </v-card>
    <b-form @submit.prevent="saveData">
      <v-chip class="ma-1" color="blue" text-color="white">UPDATE DETAILS</v-chip>
      <v-simple-table dense hide-default-footer>
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">CURRENT STATUS</th>
              <th class="text-left">SHAPE</th>
              <th class="text-left">WEIGHT</th>
              <th class="text-left">COLOR</th>
              <th class="text-left">CLARITY</th>
              <th class="text-left">CUT</th>
              <th class="text-left">POLISH</th>
              <th class="text-left">SYM</th>
              <th class="text-left">FLURO</th>
              <th class="text-left">LASER INSCRIPTION</th>
              <th class="text-left">TYPE 2A</th>
              <th class="text-left">IMAGE</th>
              <th class="text-left">PLOTTING</th>
              <th class="text-left">VIDEO</th>
              <th class="text-left">SARIN</th>
              <th class="text-left">RAP SPEC</th>
              <th class="text-left">REMARK</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{{currentStatus}}</td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:150px"
                  required
                  v-model="diamond.shape"
                  :items="shapes"
                  color="blue"
                  item-text="shapename"
                  dense
                  solo
                  hide-details
                  outlined
                  return-object
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <input
                  style="width:80px"
                  class="form-control text-right"
                  type="number"
                  min="0.01"
                  step=".01"
                  v-model="diamond.weight"
                />
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:150px"
                  required
                  v-model="diamond.color"
                  :items="colors"
                  color="blue"
                  item-text="colorname"
                  dense
                  solo
                  full-width
                  hide-details
                  outlined
                  return-object
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:150px"
                  required
                  v-model="diamond.clarity"
                  :items="clarities"
                  color="blue"
                  item-text="clarityname"
                  dense
                  solo
                  full-width
                  hide-details
                  outlined
                  return-object
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:150px"
                  required
                  v-model="diamond.cut"
                  :items="cuts"
                  color="blue"
                  item-text="name"
                  dense
                  solo
                  full-width
                  hide-details
                  outlined
                  return-object
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:150px"
                  required
                  v-model="diamond.polish"
                  :items="cuts"
                  color="blue"
                  item-text="name"
                  dense
                  solo
                  full-width
                  hide-details
                  outlined
                  return-object
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:150px"
                  required
                  v-model="diamond.sym"
                  :items="cuts"
                  color="blue"
                  item-text="name"
                  dense
                  solo
                  full-width
                  hide-details
                  outlined
                  return-object
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:150px"
                  required
                  v-model="diamond.fluro"
                  :items="fluros"
                  color="blue"
                  item-text="fluroname"
                  dense
                  solo
                  hide-details
                  outlined
                  return-object
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.laserinscription"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.type2a"
                />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="diamond.image" />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.plotting"
                />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="diamond.viedo" />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="diamond.sarin" />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.rapspec"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.remark"
                />
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>

      <v-chip class="ma-1" color="orange" text-color="white">PERSON VIEWS</v-chip>
      <v-simple-table dense hide-default-footer>
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">ADD ROW</th>
              <th class="text-left">SR.NO</th>
              <th class="text-left">PERSON NAME</th>
              <th class="text-left">REMARK</th>
              <th class="text-left">COLOR</th>
              <th class="text-left">CLARITY</th>
              <th class="text-left">BGM</th>
              <th class="text-left">SHADE</th>
              <th class="text-left">EYE CLEAN</th>
              <th class="text-left">TABLE INCLUSION</th>
              <th class="text-left">TABLE BLACK</th>
              <th class="text-left">SIDE BLACK</th>
              <th class="text-left">INCLUSION PATTERN</th>
              <th class="text-left">INTERNAL GRAINING</th>
              <th class="text-left">TABLE OPEN</th>
              <th class="text-left">CROWN OPEN</th>
              <th class="text-left">PAV OPEN</th>
              <th class="text-left">REFLECTION</th>
              <th class="text-left">HEAR & ARROW</th>
              <th class="text-left">SPREAD RATION</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(person, k) in persons" :key="k">
              <td>
                <a class="text-success" @click="addNewRow">
                  <font-awesome-icon icon="plus-square" />
                </a>
              </td>

              <td>{{persons.indexOf(person)+1}}</td>

              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="person.name" />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="person.remark" />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="person.color" />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.clarity"
                />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="person.bgm" />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="person.shade" />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.eyeClean"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.tableInc"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.tableBlack"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.sideBlack"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.incPattern"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.intGraining"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.tableOpen"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.crownOpen"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.pavOpen"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.reflection"
                />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="person.heart" />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="person.spreadRatio"
                />
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>

        <v-chip class="ma-1" color="blue" text-color="white">DNA UPDATE</v-chip>
      <v-simple-table dense hide-default-footer>
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">COLOR</th>
              <th class="text-left">CLARITY</th>
              <th class="text-left">BGM</th>
              <th class="text-left">SHADE</th>
              <th class="text-left">EYE CLEAN</th>
              <th class="text-left">TABLE INCLUSION</th>
              <th class="text-left">TABLE BLACK</th>
              <th class="text-left">SIDE BLACK</th>
              <th class="text-left">INCLUSION PATTERN</th>
              <th class="text-left">INTERNAL GRAINING</th>
              <th class="text-left">TABLE OPEN</th>
              <th class="text-left">CROWN OPEN</th>
              <th class="text-left">PAV OPEN</th>
              <th class="text-left">REFLECTION</th>
              <th class="text-left">HEAR & ARROW</th>
              <th class="text-left">SPREAD RATION</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="diamond.dnaColor" />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="diamond.dnaClarity" />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="diamond.bgm" />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="diamond.shade" />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.eyeClean"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.tableInclusion"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.tableBlack"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.sideBlack"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.incPattern"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.intGraining"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.tableOpen"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.crownOpen"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.pavOpen"
                />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.reflection"
                />
              </td>
              <td class="tabledata">
                <input class="form-control" style="width:150px" type="text" v-model="diamond.heartAndArrow" />
              </td>
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  type="text"
                  v-model="diamond.spreadRatio"
                />
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
      <v-btn
        style="margin-top:10px; margin-left:10px;"
        color="success"
        type="submit"
        class="mr-4"
      >Submit</v-btn>
    </b-form>

    <v-chip class="ma-2" color="pink" label text-color="white">
      <v-icon left>mdi-label</v-icon>UPDATED DETAILS
    </v-chip>
    <v-card flat col>
      <v-card-text>
        <v-data-table
          hide-default-footer
          :headers="diamondheader"
          :items="updateddetails"
          :items-per-page="50"
          class="elevation-1"
        ></v-data-table>
      </v-card-text>
    </v-card>
    <v-chip class="ma-2" color="pink" label text-color="white">
      <v-icon left>mdi-label</v-icon>PERSION VIEW DETAILS
    </v-chip>
    <v-card flat col>
      <v-card-text>
        <v-data-table
          hide-default-footer
          :headers="personheader"
          :items="personviews"
          :items-per-page="50"
          class="elevation-1"
        ></v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import commonVari from "../models/commonVari.js";
import axios from "axios";
export default {
  data() {
    return {
      personviews: [],
      updateddetails: [],
      currentStatus: "",
      color: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      action: "",
      requiredParams: "",
      option: null,
      option1: null,
      trackingResults: [],
      purchaseDetails: [],
      shape: "",
      dcolor: "",
      clarity: "",
      cut: "",
      polish: "",
      sym: "",
      fluro: "",
      shapes: [],
      colors: [],
      clarities: [],
      cuts: [],
      fluros: [],
      diamond: {},
      

      persons: [
        {
          name: "",
          remark: "",
          color: "",
          clarity: "",
          bgm: "",
          shade: "",
          eyeClean: "",
          tableInc: "",
          tableBlack: "",
          sideBlack: "",
          incPattern: "",
          intGraining: "",
          tableOpen: "",
          crownOpen: "",
          pavOpen: "",
          reflection: "",
          heart: "",
          spreadRatio: ""
        }
      ],

      API_URL: commonVari.API_URL,
      currentDetails: [
        { text: "CURRENT STATUS", value: "processStatus" },
        { text: "SHAPE", value: "shape.shapename" },
        { text: "WEIGHT", value: "currentweight" },
        { text: "COLOR", value: "color.colorname" },
        {
          text: "CLARITY",
          value: "clarity.clarityname"
        },
        { text: "CUT", value: "cut.name" },
        { text: "POLISH", value: "polish.name" },
        { text: "SYM", value: "sym.name" },
        { text: "FLURO", value: "fluro.fluroname" },
        {
          text: "LASER INSCRIPTION",
          value: "inscription"
        },
        { text: "TYPE 2A", value: "" },
        { text: "IMAGE", value: "" },
        {
          text: "PLOTTING",
          value: ""
        },
        { text: "VIDEO", value: "" },
        {
          text: "SARIN REPORT",
          value: ""
        },
        {
          text: "RAP SPEC",
          value: ""
        },
        { text: "REMARK", value: "remark" }
      ],
      diamondheader:[
        { text: "SHAPE", value: "shape.shapename" },
        { text: "WEIGHT", value: "weight" },
        { text: "COLOR", value: "color.colorname" },
        { text: "CLARITY", value: "clarity.clarityname" },
        { text: "CUT", value: "cut.name" },
        { text: "POLISH", value: "polish.name" },
        { text: "SYM", value: "sym.name" },
        { text: "FLURO", value: "fluroname" },
        { text: "LASER", value: "laserinscription" },
        { text: "IMAGE", value: "imageplotting" },
        { text: "VIDEO", value: "viedo" },
        { text: "SARIN", value: "sarin" },
        { text: "RAP-SPEC", value: "rapspecremark" },
        
      ],
      personheader:[
        
        { text: "NAME", value: "name" },
        { text: "REMARK", value: "remark" },
        { text: "COLOR", value: "color" },
        { text: "CLARITY", value: "clarity" },
        { text: "BGM", value: "bgm" },
        { text: "SHADE", value: "shade" },
        { text: "EYE CLEAN", value: "eyClean" },
        { text: "TABLE INC", value: "tableInc" },
        { text: "TABLE BLACK", value: "tableBlack" },
        { text: "SIDE BLACK", value: "sideBlack" },
        { text: "INC PATTERN", value: "incPattern" },
        { text: "INT GRAINING", value: "intGraining" },
        { text: "TABLE OPEN", value: "tableOpen" },
        { text: "CROWN OPEN", value: "crownOpen" },
        { text: "PAV OPEN", value: "pavOpen" },
        { text: "HEART & ARROW", value: "heart" },
        { text: "SPREAD RATIO", value: "spreadRatio" },

      ],

      headers: [
        { text: "DATE", value: "pur_lines.transdate" },
        { text: "STOCK #", value: "stockid" },
        { text: "BUYING COMPANY", value: "pur_lines.company[0].name" },
        { text: "SUPPLIER", value: "pur_lines.supplier[0].name" },
        { text: "SHAPE", value: "pur_lines.diamondTracking.shape.shapename" },
        { text: "WEIGHT", value: "pur_lines.diamondTracking.originalweight" },
        { text: "COLOR", value: "pur_lines.diamondTracking.color.colorname" },
        {
          text: "CLARITY",
          value: "pur_lines.diamondTracking.clarity.clarityname"
        },
        { text: "CUT", value: "pur_lines.diamondTracking.cut.name" },
        { text: "POLISH", value: "pur_lines.diamondTracking.polish.name" },
        { text: "SYM", value: "pur_lines.diamondTracking.sym.name" },
        { text: "FLURO", value: "pur_lines.diamondTracking.fluro.fluroname" },
        { text: "LAB", value: "pur_lines.diamondTracking.lab.labname" },
        { text: "REPORT #", value: "pur_lines.diamondTracking.reportno" },
        { text: "RAP", value: "pur_lines.rapprice" },
        { text: "DISCOUNT", value: "pur_lines.discount" },
        { text: "DIS 1", value: "pur_lines.dis1" },
        { text: "DIS 2", value: "pur_lines.dis2" },
        { text: "CUR RATE", value: "pur_lines.curRate" },
        { text: "$ / CT", value: "pur_lines.usdPerCt" },
        { text: "TOTAL $", value: "pur_lines.usdTotal" },
        { text: "RS / CT", value: "pur_lines.inrPerCt" },
        { text: "TOTAL RS", value: "pur_lines.inrtoal" },
        { text: "EXPENSE USD", value: "pur_lines.expenseusd" },
        { text: "GROSS USD", value: "pur_lines.grossUsd" },
        { text: "EXPNESE RS", value: "pur_lines.expense" },
        { text: "GROSS RS", value: "pur_lines.grossTotal" }
      ]
    };
  },
  created() {
    this.getAllShapes();
    this.getAllColors();
    this.getAllClarity();
    this.getAllCuts();
    this.getAllFluro();
  },

  methods: {
    saveData() {
      var formData = new FormData();
      formData.append("diamondToUpdate", JSON.stringify(this.diamond));
      formData.append("personView", JSON.stringify(this.persons));

      axios
        .post(this.API_URL + "diamondtracking/updateRecutDetails", formData)
        .then(data => {
          (this.text = "SUCCESS ...."),
            (this.snackbar = true);
          console.log(data);
          this.updateddetails.push(this.diamond);
          this.personviews = this.persons;
          console.log(this.personviews);
          
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
    },
    addNewRow() {
      this.persons.push({
        name: "",
        remark: "",
        color: "",
        clarity: "",
        bgm: "",
        shade: "",
        eyeClean: "",
        tableInc: "",
        tableBlack: "",
        sideBlack: "",
        incPattern: "",
        intGraining: "",
        tableOpen: "",
        crownOpen: "",
        pavOpen: "",
        reflection: "",
        heart: "",
        spreadRatio: ""
      });
    },
    async selectDataInTables() {
      var formData = new FormData();
      formData.append("stonelist", this.requiredParams);
      formData.append("typeofinput", this.option);
      await axios
        .post(this.API_URL + "diamondtracking/getDataForRecutUpdate", formData)
        .then(data => {
          this.trackingResults = data.data;
          this.currentStatus = data.data.processStatus;
          this.diamond.diamondId = data.data.id;
        });
      this.purchaseDetails = [];
      this.purchaseDetails.push(this.trackingResults);
    },

    saveSelectedCertList(selectedList) {
      const formData = new FormData();
      formData.append("company", JSON.stringify(selectedList));
      formData.append("action", this.action);

      axios
        .post(this.API_URL + "diamond/updateDiamondProcess", formData)
        .then(data => {
          console.log(data),
            (this.text = "SUCCESS ...."),
            (this.snackbar = true);
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
    },
    getAllShapes() {
      axios
        .get(this.API_URL + "shape/getAllShapes")
        .then(({ data }) => (this.shapes = data));
    },
    getAllColors() {
      axios
        .get(this.API_URL + "color/getAllColors")
        .then(({ data }) => (this.colors = data));
    },
    getAllClarity() {
      axios
        .get(this.API_URL + "clarity/getAllClarity")
        .then(({ data }) => (this.clarities = data));
    },
    getAllCuts() {
      axios
        .get(this.API_URL + "cps/getAllcps")
        .then(({ data }) => (this.cuts = data));
    },
    getAllFluro() {
      axios
        .get(this.API_URL + "fluro/getAllFluro")
        .then(({ data }) => (this.fluros = data));
    }
  }
};
</script>

