<template>
  <div>
    <v-snackbar v-model="snackbar" :color="color" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">{{this.$route.params.company}} | EXPORT FORM</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-container>
    <div>
      <v-row>
        <v-col cols="12" md="3">
          <v-radio-group v-model="option" row>
            <v-radio label="BY STOCK #" value="byStockId"></v-radio>
            <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
          </v-radio-group>
        </v-col>

        <v-col cols="12" md="3">
          <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
        </v-col>
        <v-col cols="12" md="1">
          <v-btn outlined color="indigo">LOAD DATA</v-btn>
        </v-col>
      </v-row>
    </div>

    <v-container>
      <v-row>
        <v-col cols="12" md="5">
          <v-autocomplete
            chips
            required
            v-model="selectedCompanies"
            :items="companies"
            color="blue"
            item-text="name"
            solo
            dense
            outlined
            hide-details
            opeque
            return-object
            multiple
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="3">
          <v-autocomplete
            required
            v-model="action"
            auto-select-first
            :items="statuses"
            color="white"
            item-text="name"
            dense
            solo-inverted
            hide-details
            outlined
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="2">
          <v-btn outlined color="indigo" @click="getPurchase()">Retrive Report</v-btn>
        </v-col>
        <v-col cols="12" md="2">
          <v-btn outlined color="indigo" @click="exportToExcel()">Excel Report</v-btn>
        </v-col>
      </v-row>
    </v-container>

    <v-card flat color="pink lighten-5">
      <v-card-text>
        <v-data-table
          v-model="selectedStones"
          :headers="headers"
          hide-default-footer
          :items="stoneCertLists"
          :items-per-page="50"
          show-select
          class="elevation-1"
        >
          <template
            v-slot:item.id="{ item }"
          >{{(stoneCertLists.map(function(x) {return x.id; }).indexOf(item.id)) + 1}}</template>

          <template v-slot:item.exportRapprice="{ item }">
            <v-text-field
              style="width: 100px;"
              class="v-input--selection-controls"
              v-model="item.exportRapprice"
              outlined
              @input="calculateData(item)"
              type="number"
            ></v-text-field>
          </template>

          <template v-slot:item.downPt="{ item }">
            <v-text-field
              style="width: 100px;"
              class="v-input--selection-controls"
              v-model="item.downPt"
              outlined
              @input="calculateData1(item)"
              type="number"
            ></v-text-field>
          </template>

          <template v-slot:item.exportPerCt="{ item }">
            <v-text-field
              style="width: 100px;"
              class="v-input--selection-controls"
              v-model="item.exportPerCt"
              outlined
              type="number"
            ></v-text-field>
          </template>

          <template v-slot:item.totalAmt="{ item }">
            <v-text-field
              style="width: 150px;"
              class="v-input--selection-controls"
              v-model="item.totalAmt"
              outlined
              type="number"
            ></v-text-field>
          </template>

          <template v-slot:item.through="{ item }">
            <v-text-field
              style="width: 150px;"
              class="v-input--selection-controls"
              v-model="item.through"
              outlined
              type="text"
            ></v-text-field>
          </template>

          <template v-slot:item.rDownPt="{ item }">
            <v-text-field
              style="width: 150px;"
              class="v-input--selection-controls"
              v-model="item.rDownPt"
              outlined
              type="text"
            ></v-text-field>
          </template>

          <template v-slot:item.rBack="{ item }">
            <v-text-field
              style="width: 150px;"
              class="v-input--selection-controls"
              v-model="item.rBack"
              outlined
              type="text"
            ></v-text-field>
          </template>

          <template v-slot:item.exprotStone="{ item }">
            <v-text-field
              style="width: 150px;"
              class="v-input--selection-controls"
              v-model="item.exprotStone"
              outlined
              type="text"
            ></v-text-field>
          </template>

          <template v-slot:item.exportCert="{ item }">
            <v-text-field
              style="width: 150px;"
              class="v-input--selection-controls"
              v-model="item.exportCert"
              outlined
              type="text"
            ></v-text-field>
          </template>

          <template v-slot:item.buyerName="{ item }">
            <v-text-field
              style="width: 150px;"
              class="v-input--selection-controls"
              v-model="item.buyerName"
              outlined
              type="text"
            ></v-text-field>
          </template>

          <template
            v-slot:item.pur_lines.transdate="{ item }"
          >{{countDays(item.pur_lines.transdate)}}</template>
        </v-data-table>
        <v-divider></v-divider>
        <v-btn outlined color="indigo" @click="saveSelectedCertList(selectedStones)">SAVE SELECTED</v-btn>
      </v-card-text>
    </v-card>

    <v-row>
      <v-col>
        <v-chip class="ma-2" color="pink" label text-color="white">
          <v-icon left>mdi-label</v-icon>FORMAT 1
        </v-chip>
        <v-card flat color="purple lighten-5">
          <v-card-text>
            <v-data-table
              hide-default-footer
              :headers="format1"
              :items="selectedStones"
              :items-per-page="50"
              class="elevation-1"
            >
              <template
                v-slot:item.id="{ item }"
              >{{(selectedStones.map(function(x) {return x.id; }).indexOf(item.id)) + 1}}</template>
              <template v-slot:body.append>
                <tr>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td></td>
                  <td>
                    <v-text-field dense outlined v-model="grossWeight" type="number" readonly></v-text-field>
                  </td>
                  <td></td>
                  <td>
                    <v-text-field dense outlined v-model="grossTotal" type="number" readonly></v-text-field>
                  </td>
                </tr>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col>
        <v-chip class="ma-2" color="pink" label text-color="white">
          <v-icon left>mdi-label</v-icon>FORMAT 2
        </v-chip>
        <v-card flat color="pink lighten-5">
          <v-card-text>
            <v-data-table
              hide-default-footer
              :headers="format2"
              :items="selectedStones"
              :items-per-page="50"
              class="elevation-1"
            >
              <template
                v-slot:item.id="{ item }"
              >{{(selectedStones.map(function(x) {return x.id; }).indexOf(item.id)) + 1}}</template>
              <template v-slot:item.ritc="{  }">71023910</template>
              <template v-slot:item.cst="{  }">CST</template>
              <template v-slot:item.avg="{  }">1 PC</template>
              <template
                v-slot:item.shape.shapename="{ item }"
              >CPD-D.CUT-{{item.shape.shapename}}-{{item.color.colorname}}-{{item.clarity.clarityname}}-{{item.lab.labname}}-{{item.reportno}}</template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-divider></v-divider>
  </div>
</template>

<script>
import Vue from "vue";
import CompanyRelation from "../models/CompanyRelation";
import commonVari from "../models/commonVari.js";
import axios from "axios";
import XLSX from "xlsx";
import { GridPlugin } from "@syncfusion/ej2-vue-grids";

Vue.use(GridPlugin);
export default {
  data() {
    return {
      grossWeight: 0,
      grossTotal: 0,
      color: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      action: "SEND TO HK",
      actions: [],
      statuses: [
        { name: "SEND TO INDIA" },
        { name: "COLLECT FROM HK" },
        { name: "SEND FOR RECUT" },
        { name: "RECEIVE FROM RECUT" },
        { name: "SEND TO LAB" },
        { name: "RECEIVE FROM LAB" },
        { name: "QC CHECK FROM LAB" },
        { name: "DNA" },
        { name: "PRICING" },
        { name: "SEND TO HK" },
        { name: "COLLECT FROM INDIA" }
      ],
      requiredParams: "",
      option: null,
      option1: null,
      purchaselists1: [],
      selectedStones: [],
      selectedstoneCertLists: [],
      selectedstoneLists: [],
      selectedcertLists: [],
      stoneCertLists: [],
      stoneLists: [],
      certLists: [],
      savedList: [],
      tabs: null,
      actionPurpose: "",

      API_URL: commonVari.API_URL,
      purchaselists: [],
      companies: [],
      selectedCompanies: [],
      selectcompany: new CompanyRelation(),
      company: new CompanyRelation(),

      headers: [
        { text: "NO", value: "id" },
        { text: "STOCK #", value: "stockid" },
        { text: "PURPOSE", value: "purpose" },
        { text: "STATUS", value: "processActionRequire" },
        { text: "BOUGHT FOR", value: "stoneOwner.name" },
        { text: "SHAPE", value: "shape.shapename" },
        { text: "WEIGHT", value: "currentweight" },
        { text: "COLOR", value: "color.colorname" },
        { text: "CLARITY", value: "clarity.clarityname" },
        { text: "LAB", value: "lab.labname" },
        { text: "REPORT #", value: "reportno" },
        { text: "PURCHASE RAP", value: "pur_lines.rapprice" },
        { text: "RAP %", value: "pur_lines.usdPerCt" },
        { text: "TOTAL", value: "pur_lines.usdTotal" },
        { text: "NET USD", value: "pur_lines.grossUsd" },
        { text: "EXPORT RAP", value: "exportRapprice" },
        { text: "DOWN %", value: "downPt" },
        { text: "$ / CT", value: "exportPerCt" },
        { text: "TOTAL AMT", value: "totalAmt" },
        { text: "THROUGH", value: "through" },
        { text: "R-DOWN%", value: "rDownPt" },
        { text: "R-BACK", value: "rBack" },
        { text: "STONE", value: "exprotStone" },
        { text: "CERT", value: "exportCert" },
        { text: "BUYER NAME", value: "buyerName" }
      ],
      // exportRapprice:0,
      // totalAmt:0,
      // downPt:0,

      format1: [
        { text: "NO", value: "id" },
        // { text: "STOCK #", value: "stockid" },
        { text: "SHAPE", value: "shape.shapename" },
        { text: "COLOR", value: "color.colorname" },
        { text: "CLARITY", value: "clarity.clarityname" },
        // { text: "LAB", value: "lab.labname" },
        { text: "REPORT #", value: "reportno" },
        { text: "WEIGHT", value: "currentweight" },
        { text: "$ / CT", value: "exportPerCt" },
        { text: "TOTAL AMT", value: "totalAmt" }
      ],
      format2: [
        { text: "NO", value: "id" },
        { text: "RTIC CODE", value: "ritc" },
        { text: "ITEM DESCRIPTION", value: "shape.shapename" },
        { text: "CST", value: "cst" },
        { text: "AVG", value: "avg" },
        { text: "UNIT OF QTY", value: "currentweight" },
        { text: "UNIT PRICE", value: "exportPerCt" },
        { text: "TOTAL AMT $", value: "totalAmt" }
      ]
    };
  },

  created() {
    this.company = this.$route.params.company;
    this.getAllCompanies(this.company);
  },
  watch: {
    selectedStones: function() {
      var weight = 0;
      var total = 0;
      this.selectedStones.filter(shp => {
        weight = weight + shp.currentweight;
        total = total + parseFloat(shp.totalAmt);
      });
      this.grossWeight = weight;
      this.grossTotal = total;
    }
  },

  methods: {
    exportToExcel() {
      var Heading = [["Export Details"], ["From Location : ", "India"]];
      // stoneCertLists
      // var animalWS = XLSX.utils.json_to_sheet(this.stoneCertLists);
      var animalWS = XLSX.utils.json_to_sheet(Heading);
      XLSX.utils.sheet_add_json(animalWS, this.stoneCertLists, {
        skipHeader: true,
        origin: -1
      });
      var merge = { s: {r:1, c:0}, e: {r:1, c:1} };
      /* add merges */
      if (!animalWS["!merges"]) animalWS["!merges"] = [];
      animalWS["!merges"].push(merge);

      // A workbook is the name given to an Excel file
      var wb = XLSX.utils.book_new(); // make Workbook of Excel
      // XLSX.utils.decode_cell("A1:B1");
      // add Worksheet to Workbook
      // Workbook contains one or more worksheets
      XLSX.utils.book_append_sheet(wb, animalWS, "animals");
      XLSX.writeFile(wb, "book.xlsx");
    },
    calculateData1(item) {
      var weight = 0;
      var pRap = 0;
      var pUsdPerCt = 0;

      item.exportRapprice = 0;
      var eDownPt = 0;

      if (!isNaN(item.downPt)) {
        eDownPt = parseFloat(item.downPt);
      }
      if (isNaN(eDownPt)) {
        eDownPt = 0;
      }

      if (!isNaN(item.currentweight)) {
        weight = parseFloat(item.currentweight);
      }
      if (!isNaN(item.pur_lines.rapprice)) {
        pRap = parseFloat(item.pur_lines.rapprice);
      }
      if (!isNaN(item.pur_lines.usdPerCt)) {
        pUsdPerCt = parseFloat(item.pur_lines.usdPerCt);
      }
      console.log(pUsdPerCt);
      var exportPerCt = 0;
      var totalAmt = 0;
      var rBack = 0;
      var rDownPt = 0;
      exportPerCt = pUsdPerCt + (pUsdPerCt * eDownPt) / 100;
      totalAmt = exportPerCt * weight;

      rBack = (exportPerCt / pRap) * 100 - 100;
      if (!isNaN(exportPerCt)) {
        item.exportPerCt = exportPerCt.toFixed(2);
      }
      if (!isNaN(totalAmt)) {
        item.totalAmt = totalAmt.toFixed(2);
      }
      if (!isNaN(rBack)) {
        item.rBack = rBack.toFixed(2);
      }
      if (!isNaN(rDownPt)) {
        item.rDownPt = rDownPt.toFixed(2);
      }
    },
    calculateData(item) {
      var weight = 0;
      var pRap = 0;
      var pTotal = 0;
      var eRap = 0;
      item.downPt = 0;
      var pUsdPerCt = 0;
      if (!isNaN(item.exportRapprice)) {
        eRap = parseFloat(item.exportRapprice);
      }
      if (isNaN(eRap)) {
        eRap = 0;
      }

      if (!isNaN(item.currentweight)) {
        weight = parseFloat(item.currentweight);
      }
      if (!isNaN(item.pur_lines.rapprice)) {
        pRap = parseFloat(item.pur_lines.rapprice);
      }
      if (!isNaN(item.pur_lines.usdTotal)) {
        pTotal = parseFloat(item.pur_lines.usdTotal);
      }
      console.log(pTotal);
      if (!isNaN(item.pur_lines.usdPerCt)) {
        pUsdPerCt = parseFloat(item.pur_lines.usdPerCt);
      }
      var exportPerCt = 0;
      var totalAmt = 0;
      var rDownPt = 0;
      var rBack = 0;

      exportPerCt = pRap + (pRap * eRap) / 100;
      totalAmt = exportPerCt * weight;

      rDownPt = ((exportPerCt - pUsdPerCt) / pUsdPerCt) * 100;
      // rBack = (exportPerCt / pRap - 1) * 100;
      if (!isNaN(exportPerCt)) {
        item.exportPerCt = exportPerCt.toFixed(2);
      }
      if (!isNaN(totalAmt)) {
        item.totalAmt = totalAmt.toFixed(2);
      }
      if (!isNaN(rDownPt)) {
        item.rDownPt = rDownPt.toFixed(2);
      }
      if (!isNaN(rBack)) {
        item.rBack = rBack.toFixed(2);
      }
    },

    getIndexOf(value) {
      console.log(value);
      return 1;
    },

    saveSelectedCertList(selectedList) {
      const formData = new FormData();
      formData.append("company", JSON.stringify(selectedList));
      formData.append("action", this.action);
      formData.append("isExportForm", true);
      formData.append("actionPurpose", this.actionPurpose);

      axios
        .post(this.API_URL + "diamond/updateDiamondProcess", formData)
        .then(data => {
          console.log(data),
            (this.text = "SUCCESS ...."),
            (this.snackbar = true),
            (this.purchaselists1 = []),
            (this.selectedStones = []),
            (this.selectedstoneCertLists = []),
            (this.selectedstoneLists = []),
            (this.selectedcertLists = []),
            (this.stoneCertLists = []),
            (this.stoneLists = []),
            (this.certLists = []),
            (this.selectedList = []);
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
    },
    countDays(date) {
      var dt = new Date(date);
      var currentDate = new Date();
      var timeDiff = currentDate.getTime() - dt.getTime();
      var days = Math.round(timeDiff / (1000 * 3600 * 24));
      return days;
    },
    async getAllCompanies(company) {
      const formData = new FormData();
      formData.append("company", company);
      await axios
        .post(
          this.API_URL + "companyrelation/getCompanyByMainCompany",
          formData
        )
        .then(
          ({ data }) => (
            (this.companies = data), (this.selectedCompanies = data)
          )
        );
      this.getPurchase();
    },
    async getPurchase() {
      this.actions.push("SEND TO HK"); //  = "SEND TO HK";
      this.actions.push("SEND TO HK FOR LAB");
      const formData = new FormData();
      formData.append("company", JSON.stringify(this.selectedCompanies));
      formData.append("action", JSON.stringify(this.actions));
      await axios
        .post(this.API_URL + "diamond/getDiamondProcess", formData)
        .then(({ data }) => ((this.stoneCertLists = data), console.log(data)));
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(26, 172, 216);
  color: white;
}
.container {
  max-width: 1185px;
  min-width: -webkit-fill-available;
}
.v-input--selection-controls {
  margin-top: 0px;
  padding-top: 0px;
  height: 56px;
}
.text-start {
  padding-left: 0px;
  padding-right: 0px;
}
.v-text-field__details {
  padding-right: 0px;
  padding-left: 0px;
}
</style>