<template>
  <div>
    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">{{this.$route.params.company}} | PURCHAE REPORTS</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-container>

    <v-container>
      <v-row>
        <v-col cols="12" md="2">
          <v-autocomplete
            required
            v-model="selectedCompany"
            :items="companies"
            color="blue"
            item-text="name"
            solo
            dense
            outlined
            hide-details
            opeque
            return-object
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="2">
          <v-btn outlined color="indigo" @click="getPurchase()">Retrive Report</v-btn>
        </v-col>
      </v-row>
    </v-container>

    <v-divider></v-divider>

<ejs-grid
      ref="grid"
      :dataSource="purchaselists"
      :allowPaging="true"
      gridLines="Both"
      rowHeight="25"
      :allowSorting="true"
      :allowFiltering="true"
      :toolbar="toolbar"
      :toolbarClick="toolbarClick"
      :allowExcelExport="true"
      :allowPdfExport="true"
      
    >
      <e-columns>
        <e-column field="transdate" headerText="DATE" textAlign="Left" width="120"></e-column>
        <e-column field="diamondTracking.diamond.stockid" headerText="STOCK#" textAlign="Left" width="120"></e-column>
        <e-column field="company.0.name" headerText="BUYING COMPANY" textAlign="Left" width="200"></e-column>
        <e-column field="partnerratio" headerText="PARTNERSHIP RATIO" textAlign="Left" width="150"></e-column>
        <e-column field="diamondTracking.purpose" headerText="PURPOSE" textAlign="Left" width="120"></e-column>
        <e-column field="supplier.0.name" headerText="SUPPLIER" textAlign="Left" width="200"></e-column>
        <e-column field="diamondTracking.shape.shapename" headerText="SHAPE" textAlign="Left" width="120"></e-column>
        <e-column field="diamondTracking.originalweight" headerText="WEIGHT" textAlign="Left" width="120"></e-column>
        <e-column field="diamondTracking.color.colorname" headerText="COLOR" textAlign="Left" width="120"></e-column>
        <e-column field="diamondTracking.clarity.clarityname" headerText="CLARITY" textAlign="Left" width="120"></e-column>
        <e-column field="diamondTracking.cut.name" headerText="CUT" textAlign="Left" width="90"></e-column>
        <e-column field="diamondTracking.polish.name" headerText="POLISH" textAlign="Left" width="120"></e-column>
        <e-column field="diamondTracking.sym.name" headerText="SYM" textAlign="Left" width="120"></e-column>
        <e-column field="diamondTracking.fluro.fluroname" headerText="FLURO" textAlign="Left" width="120"></e-column>
        <e-column field="diamondTracking.lab.labname" headerText="LAB" textAlign="Left" width="90"></e-column>
        <e-column field="diamondTracking.reportno" headerText="REPORT #" textAlign="Left" width="90"></e-column>
        <e-column field="rapprice" headerText="RAP" textAlign="Right" width="90"></e-column>
        <e-column field="discount" headerText="DIS" textAlign="Right" width="90"></e-column>
        <e-column field="dis1" headerText="DIS 1" textAlign="Right" width="90"></e-column>
        <e-column field="dis2" headerText="DIS 2" textAlign="Right" width="90"></e-column>
        <e-column field="usdPerCt" headerText="$/CT" textAlign="Right" width="200"></e-column>
        <e-column field="usdTotal" headerText="TOTAL $" textAlign="Right" width="200"></e-column>
        <e-column field="curRate" headerText="CUR RATE" textAlign="Right" width="200"></e-column>
        <e-column field="inrtoal" headerText="TOTAL RS" textAlign="Right" width="200"></e-column>
        <e-column field="expenseusd" headerText="EXPENSE USD" textAlign="Right" width="200"></e-column>
        <e-column field="grossUsd" headerText="GROSS USD" textAlign="Right" width="200"></e-column>
        <e-column field="expense" headerText="EXPENSE RS" textAlign="Right" width="200"></e-column>
        <e-column field="grossTotal" headerText="GROSS RS" textAlign="Right" width="200"></e-column>
        <e-column field="diamondTracking.invoiceStatus" headerText="INVOICE STATUS" textAlign="Left" width="200"></e-column>
        <e-column field="term" headerText="TERM" textAlign="Left" width="90"></e-column>
        <e-column field="transdate" headerText="DUE DATE" textAlign="Left" width="120"></e-column>
        <e-column field="diamondTracking.stoneStatus" headerText="STONE STATUS" textAlign="Left" width="90"></e-column>
        <e-column field="diamondTracking.certStatus" headerText="CERT STATUS" textAlign="Left" width="90"></e-column>
        
      </e-columns>
    </ejs-grid>

     
  </div>
</template>

<script>
import CompanyRelation from "../models/CompanyRelation";
import commonVari from "../models/commonVari.js";
import axios from "axios";
import {
  Page,
  Toolbar,
  ExcelExport,
  PdfExport,
  Resize,
  
} from "@syncfusion/ej2-vue-grids";
export default {
  data() {
    return {
       toolbar: ["ExcelExport", "PdfExport", "CsvExport", "Print"],
      API_URL: commonVari.API_URL,
      purchaselists: [],
      companies: [],
      selectedCompany: new CompanyRelation(),
      headers: [
        { text: "DATE", value: "transdate" },
        { text: "PURPOSE", value: "diamondTracking.purpose" },
        {
          text: "FOR LOCATION",
          value: "diamondTracking.buyForLocation.countryname"
        },
        { text: "STOCK #", value: "diamondTracking.diamond.stockid" },
        { text: "SHAPE", value: "diamondTracking.shape.shapename" },
        { text: "WEIGHT", value: "diamondTracking.originalweight" },
        { text: "COLOR", value: "diamondTracking.color.colorname" },
        { text: "CLARITY", value: "diamondTracking.clarity.clarityname" },
        { text: "CUT", value: "diamondTracking.cut.name" },
        { text: "POLISH", value: "diamondTracking.polish.name" },
        { text: "SYM", value: "diamondTracking.sym.name" },
        { text: "FLURO", value: "diamondTracking.fluro.fluroname" },
        { text: "FLURO COLOR", value: "diamondTracking.flurocolor" },
        { text: "LAB", value: "diamondTracking.lab.labname" },
        { text: "REPORT #", value: "item.diamondTracking.reportno" },
        { text: "RAP", value: "rapprice" },
        { text: "DISCOUNT", value: "discount" },
        { text: "DIS 1", value: "dis1" },
        { text: "DIS 2", value: "dis2" },
        { text: "$/CT", value: "usdPerCt" },
        { text: "TOTAL $", value: "usdTotal" },
        { text: "CUR RATE", value: "curRate" },
        { text: "RS / CT", value: "inrPerCt" },
        { text: "TOTAL RS", value: "inrtoal" },
        { text: "REMARK", value: "diamondTracking.remark" }
      ]
    };
  },
  created() {
    this.company = this.$route.params.company;
    this.getAllCompanies(this.company);
  },
   provide: {
    grid: [PdfExport, ExcelExport, Toolbar, Page, Resize]
  },
  methods: {
    toolbarClick: function(args) {
      switch (args.item.text) {
        case "PDF Export":
          this.$refs.grid.pdfExport({ fileName: "new.pdf" });
          break;
        case "Excel Export":
          this.$refs.grid.excelExport({ fileName: "new.xlsx" });
          break;
        case "CSV Export":
          this.$refs.grid.csvExport({ fileName: "new.csv" });
          break;
      }
    },
    getAllCompanies(company) {
      const formData = new FormData();
      formData.append("company", company);
      axios
        .post(
          this.API_URL + "companyrelation/getCompanyByMainCompany",
          formData
        )
        .then(({ data }) => (this.companies = data));
    },
    getPurchase() {
      console.log(this.selectedCompany);
      axios
        .post(
          this.API_URL + "purchaseline/getPurchaseByCompany",
          this.selectedCompany
        )
        .then(({ data }) => (console.log(data), this.purchaselists = data));
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(26, 172, 216);
  color: white;
}
.container {
  max-width: 1185px;
  min-width: -webkit-fill-available;
}
</style>