<template>
  <div>
    <v-snackbar v-model="snackbar" :color="color" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">
          <v-chip dense>{{this.$route.params.company}}</v-chip>ENTRY TRANSFER
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-menu left bottom>
          <template v-slot:activator="{ on }">
            <v-btn icon v-on="on">
              <v-icon>mdi-dots-vertical</v-icon>
            </v-btn>
          </template>
          <v-list>
            <v-list-item>
              <v-list-item-title @click="addAccountForm=true">ADD ACCOUNT</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-toolbar>
    </v-container>
    <div>
      <v-col cols="12" sm="12" md="3">
        <button type="button" class="btn btn-info" @click="addNewRow">Add New Row</button>
      </v-col>
      <b-form @submit.prevent="saveEntry">
        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left"></th>
                <th class="text-left">NO</th>
                <th class="text-left">DATE</th>
                <th class="text-left">AMOUNT</th>
                <th class="text-left">FROM TRANSFER</th>
                <th class="text-left">TO TRANSFER</th>
                <th class="text-left">CONVERSION RATE</th>
                <th class="text-left">NET AMOUNT</th>
                <th class="text-left">FROM REMARK</th>
                <th class="text-left">TO REMARK</th>
                <th class="text-left">REMARK</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, k) in items" :key="k">
                <td>
                  <a class="text-success" @click="addNewRow">
                    <font-awesome-icon icon="plus-square" />
                  </a>
                </td>
                <td>{{items.indexOf(item)+1}}</td>
                <td class="tabledata">
                  <input
                    required
                    class="form-control"
                    v-model="item.entrytransfer.opendate"
                    type="date"
                  />
                </td>
                <td v-if="!usdEntry" class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    step=".01"
                    v-model="item.entrytransfer.amount"
                    @input="calculateNetAmt(item.entrytransfer)"
                  />
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    required
                    v-model="item.entrytransfer.fromAcc"
                    auto-select-first
                    :items="ledgers"
                    color="blue"
                    item-text="name"
                    dense
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    required
                    v-model="item.entrytransfer.toAcc"
                    auto-select-first
                    :items="ledgers"
                    color="blue"
                    item-text="name"
                    dense
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td v-if="!usdEntry" class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    step=".001"
                    v-model="item.entrytransfer.convRate"
                    @input="calculateNetAmt(item.entrytransfer)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    step=".01"
                    v-model="item.entrytransfer.netAmt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="text"
                    v-model="item.entrytransfer.fromNameRemark"
                  />
                </td>
                <td class="tabledata">
                  <input class="form-control" type="text" v-model="item.entrytransfer.toNameRemark" />
                </td>
                <td class="tabledata">
                  <input class="form-control" type="text" v-model="item.entrytransfer.remark" />
                </td>
                <td scope="row" class="trashIconContainer">
                  <a class="text-danger" @click="deleteRow(k, item)">
                    <font-awesome-icon icon="trash-alt" />
                  </a>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <v-btn
          style="margin-top:10px; margin-left:10px;"
          color="success"
          type="submit"
          class="mr-4"
        >Submit</v-btn>
      </b-form>
    </div>
    <v-divider></v-divider>
    <loader></loader>
    <PaymentReport></PaymentReport>
  </div>
</template>

<script>
import Loader from "../components/_loader";
import EntryTransfer from "../models/PaymentReceipt";
import axios from "axios";
import PaymentReport from "../reports/PaymentReport";
import commonVari from "../models/commonVari.js";
// import Company from '../models/Company';
export default {
  components: {
    PaymentReport,
    Loader
  },
  data() {
    return {
      usdEntry: true,
      API_URL: commonVari.API_URL,
      color: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      companies: [],
      entryCompany: "",
      ledgers: [],
      cashbankledgers: [],
      currentDate: "",
      statuses: [{ name: "PAYMENT" }, { name: "RECEIPT" }],
      items: [
        {
          no: "",
          entrytransfer: new EntryTransfer(),
          curdate: this.currentDate
        }
      ]
    };
  },

  created() {
    this.companyId = this.$route.params.companyId;
    this.companyname = this.$route.params.company;
    if (this.companyId == 2) {
      this.usdEntry = true;
    } else {
      this.usdEntry = false;
    }

    this.getAllCompanies();
    this.getAllLedgers();
    this.setCurrentDate();
    this.getCashBankLedgers();
    // this.addTenRows();
  },
  methods: {
    calculateNetAmt(item) {
      var amount = 0;
      var convRate = 0;
      var netAmt = 0;

      if (!isNaN(item.amount)) {
        amount = parseFloat(item.amount);
      }
      if (!isNaN(item.convRate)) {
        convRate = parseFloat(item.convRate);
      }
      netAmt = convRate * amount;
      if (!isNaN(netAmt)) {
        item.netAmt = netAmt.toFixed(2);
      }
    },
    addTenRows() {
      for (var i = 1; i < 10; i++) {
        this.items.push({
          no: "",
          entrytransfer: new EntryTransfer(),
          curdate: this.currentDate
        });
      }
    },
    addNewRow() {
      this.items.push({
        no: "",
        entrytransfer: new EntryTransfer(),
        curdate: this.currentDate
      });
    },
    saveEntry() {
      var paymentlist = [];

      for (var i = 0; i < this.items.length; i++) {
        this.items[i].entrytransfer.company = this.entryCompany;
        paymentlist.push(this.items[i].entrytransfer);
      }

      const formData = new FormData();
      formData.append("paymentdto", JSON.stringify(paymentlist));

      axios
        .post(this.API_URL + "entryTransfer/savePayment", formData)
        .then(() => {
          this.text = "SUCCESS";
          this.snackbar = true;
          this.items = [];
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
    },
    getAllCompanies() {
      const formData = new FormData();
      formData.append("name", this.companyname);
      axios
        .post(this.API_URL + "companyrelation/getCompanyByName", formData)
        .then(({ data }) => (this.entryCompany = data));
    },
    getAllLedgers() {
      const formData = new FormData();
      formData.append("companyId", this.companyId);

      axios
        .post(this.API_URL + "ledger/getAllLedgerByCompany", formData)
        .then(({ data }) => (this.ledgers = data));
    },
    getCashBankLedgers() {
      var groupList = [];
      groupList.push("cash in hands");
      groupList.push("BANK ACCOUNTS");
      const formData = new FormData();
      formData.append("companyId", this.companyId);
      formData.append("grouplist", groupList);
      axios
        .post(
          this.API_URL + "ledger/getLedgerByCompanyByAccountGroup",
          formData
        )
        .then(({ data }) => (this.cashbankledgers = data));
    },
    setCurrentDate() {
      var d = new Date();
      var dateObj = new Date(d.getTime());

      var year = dateObj.getFullYear();
      var month = dateObj.getMonth() + 1;
      var dt = dateObj.getDate();

      if (dt < 10) {
        dt = "0" + dt;
      }
      if (month < 10) {
        month = "0" + month;
      }

      this.currentDate = year + "-" + month + "-" + dt;
    },
    deleteRow(index, item) {
      var idx = this.items.indexOf(item);

      if (idx > -1) {
        this.items.splice(idx, 1);
      }
    },
    onReset() {
      // Reset our User values
      this.items = [];
    },
    getColor(companyId) {
      if (companyId == 1) return "orange";
      else if (companyId == 2) return "green";
      else return "blue";
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(26, 172, 216);
  color: white;
}
.container {
  max-width: 1185px;
  min-width: -webkit-fill-available;
}
table,
th,
td {
  border: 1px rgb(221, 189, 85) solid;
}
.tabledata {
  border-color: brown;
  padding: 1px;
  outline: black;
  max-height: 30px;
  /* font-weight: bold; */
}

table tfoot tr td {
  text-align: right;
  padding: 10px;
  color: black;
  background: rgb(136, 235, 235);
  font-weight: bold;
  font-size: 15px;
}
</style>