<template>
  <div>
    <v-snackbar v-model="snackbar" :color="snackcolor" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">{{this.$route.params.company}} | COLLECTION PENDING REPORTS</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-container>
    <div>
      <v-row>
        <!-- <v-col cols="12" md="4">
          <v-radio-group row v-model="option1">
            <v-radio label="STONE & CERT" value="stoneAndCert"></v-radio>
            <v-radio label="STONE ONLY" value="stoneOnly"></v-radio>
            <v-radio label="CERT ONLY" value="certOnly"></v-radio>
          </v-radio-group>
        </v-col>
        <v-col cols="12" md="3">
          <v-radio-group v-model="option" row>
            <v-radio label="BY STOCK #" value="byStockId"></v-radio>
            <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
          </v-radio-group>
        </v-col>

        <v-col cols="12" md="3">
          <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
        </v-col>
        <v-col cols="12" md="1">
          <v-btn outlined color="indigo" @click="selectDataInTables">LOAD DATA</v-btn>
        </v-col>-->
      </v-row>
    </div>

    <v-container>
      <v-row dense>
        <v-col cols="12" md="5">
          <v-autocomplete
            chips
            required
            v-model="selectedCompanies"
            :items="companies"
            color="blue"
            item-text="name"
            solo
            dense
            outlined
            hide-details
            opeque
            return-object
            multiple
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="2">
          <v-btn outlined color="indigo" @click="getPurchase()">Retrive Report</v-btn>
        </v-col>
        <v-col
          cols="12"
          md="5"
          style="background:orange;color:white;font-size:20px"
          align="center"
          justify="center"
        >{{updatedStoneList}}</v-col>
        <!-- </v-row> -->
      </v-row>
    </v-container>

    <v-tabs v-model="tabs" background-color="indigo accent-10" dark dense>
      <v-tab dense href="#StoneAndCert">STONE AND CERT PENDING</v-tab>
      <v-tab dense href="#StoneOnly">ONLY STONE PENDING</v-tab>
      <v-tab dense href="#CertOnly">ONLY CERT PENDING</v-tab>
    </v-tabs>
    <v-tabs-items v-model="tabs">
      <v-tab-item :value="'StoneAndCert'">
        <v-card flat>
          <v-card-text>
            <v-data-table
              v-model="selectedstoneCertLists"
              :headers="headers"
              :items="stoneCertLists"
              :items-per-page="50"
              show-select
              class="elevation-1"
            >
              <template
                v-slot:item.id="{ item }"
              >{{(stoneCertLists.map(function(x) {return x.id; }).indexOf(item.id)) + 1}}</template>

              <template v-slot:item.transdate="{ item }">{{countDays(item.transdate)}}</template>
            </v-data-table>
            <v-divider></v-divider>
            <v-btn
              outlined
              color="indigo"
              @click="saveSelectedCertList(selectedstoneCertLists, true, true)"
            >SAVE SELECTED</v-btn>
            <download-excel
              class="btn btn-default"
              :data="stoneCertLists"
              :fields="json_fields"
              worksheet="My Worksheet"
              name="filename.xls"
            >DownLoad Excel</download-excel>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item :value="'StoneOnly'">
        <v-card flat>
          <v-card-text>
            <v-data-table
              v-model="selectedstoneLists"
              :headers="headers"
              :items="stoneLists"
              :items-per-page="50"
              show-select
              class="elevation-1"
            >
              <template
                v-slot:item.id="{ item }"
              >{{(stoneLists.map(function(x) {return x.id; }).indexOf(item.id)) + 1}}</template>

              <template v-slot:item.transdate="{ item }">{{countDays(item.transdate)}}</template>
            </v-data-table>
            <v-divider></v-divider>
            <v-btn
              outlined
              color="indigo"
              @click="saveSelectedCertList(selectedstoneLists, true, false)"
            >SAVE SELECTED</v-btn>

            <download-excel
              class="btn btn-default"
              :data="stoneLists"
              :fields="json_fields"
              worksheet="My Worksheet"
              name="filename.xls"
            >DownLoad Excel</download-excel>
          </v-card-text>
        </v-card>
      </v-tab-item>

      <v-tab-item :value="'CertOnly'">
        <v-card flat>
          <v-card-text>
            <v-data-table
              id="employees"
              v-model="selectedcertLists"
              :headers="headers"
              :items="certLists"
              :items-per-page="50"
              show-select
              class="elevation-1"
            >
              <template
                v-slot:item.id="{ item }"
              >{{(certLists.map(function(x) {return x.id; }).indexOf(item.id)) + 1}}</template>

              <template v-slot:item.transdate="{ item }">{{countDays(item.transdate)}}</template>
            </v-data-table>
            <v-divider></v-divider>
            <v-btn
              outlined
              color="indigo"
              @click="saveSelectedCertList(selectedcertLists, false, true)"
            >SAVE SELECTED</v-btn>

            <download-excel
              class="btn btn-default"
              :data="certLists"
              :fields="json_fields"
              worksheet="My Worksheet"
              name="filename.xls"
              this.count="0"
            >DownLoad Excel</download-excel>
          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs-items>

    <v-divider></v-divider>
  </div>
</template>

<script>
import Vue from "vue";
import CompanyRelation from "../models/CompanyRelation";
import commonVari from "../models/commonVari.js";
import axios from "axios";
import { GridPlugin } from "@syncfusion/ej2-vue-grids";
Vue.use(GridPlugin);
export default {
  data() {
    return {
      snackcolor: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      updatedStoneList: "UPDATED STONE OR CERT LIST",
      requiredParams: "",
      option: null,
      option1: null,
      purchaselists1: [],
      selectedstoneCertLists: [],
      selectedstoneLists: [],
      selectedcertLists: [],
      stoneCertLists: [],
      stoneLists: [],
      certLists: [],
      tabs: null,
      API_URL: commonVari.API_URL,
      purchaselists: [],
      companies: [],
      selectedCompanies: [],
      selectcompany: new CompanyRelation(),
      company: new CompanyRelation(),
      rowno: 1,

      json_fields: {
        "#": {
          field: "", // nested attribute supported
          count: 0,
          callback: count => {
            count = parseInt(count) + 1;
            return count; //`formatted value ${value}`
          }
        },
        PURPOSE: "diamondTracking.purpose",
        "STOCK #": "diamondTracking.diamond.idForStock",
        "BOUGHT FOR": "company.0.name",
        "SUPPLIER NAME": "supplier.0.name",
        SHAPE: "diamondTracking.shape.shapename",
        WEIGHT: "diamondTracking.originalweight",
        COLOR: "diamondTracking.color.colorname",
        CLARITY: "diamondTracking.clarity.clarityname",
        CUT: "diamondTracking.cut.name",
        POLISH: "diamondTracking.polish.name",
        SYM: "diamondTracking.sym.name",
        FLURO: "diamondTracking.fluro.fluroname",
        LAB: "diamondTracking.lab.labname",
        "REPORT #": "diamondTracking.reportno",
        "TOTAL RS": "grossTotal",
        "TOTAL $": "grossUsd",
        "CERT STATUS": "diamondTracking.certStatus",
        "STONE STATUS": "diamondTracking.stoneStatus",
        "BUY DAY": {
          field: "transdate", // nested attribute supported
          callback: value => {
            return this.countDays(value); //`formatted value ${value}`
          }
        },
        REMARK: {
          field: "", // nested attribute supported
          callback: value => {
            console.log(value);
            this.rowno = this.rowno + 1;
            return "=G" + this.rowno + "+P" + this.rowno; //`formatted value ${value}`
          }
        }
        // REMARK: "diamondTracking.remark"
      },

      headers: [
        { text: "NO", value: "id" },
        { text: "PURPOSE", value: "diamondTracking.purpose" },
        { text: "STOCK #", value: "diamondTracking.diamond.stockid" },
        { text: "BOUGHT FOR", value: "company[0].name" },
        { text: "SUPPLIER NAME", value: "supplier[0].name" },
        { text: "SHAPE", value: "diamondTracking.shape.shapename" },
        { text: "WEIGHT", value: "diamondTracking.originalweight" },
        { text: "COLOR", value: "diamondTracking.color.colorname" },
        { text: "CLARITY", value: "diamondTracking.clarity.clarityname" },
        { text: "CUT", value: "diamondTracking.cut.name" },
        { text: "POLISH", value: "diamondTracking.polish.name" },
        { text: "SYM", value: "diamondTracking.sym.name" },
        { text: "FLURO", value: "diamondTracking.fluro.fluroname" },
        { text: "LAB", value: "diamondTracking.lab.labname" },
        { text: "REPORT #", value: "diamondTracking.reportno" },
        { text: "TOTAL RS", value: "grossTotal" },
        { text: "TOTAL $", value: "grossUsd" },
        { text: "CERT STATUS", value: "diamondTracking.certStatus" },
        { text: "STONE STATUS", value: "diamondTracking.stoneStatus" },
        { text: "BUY DAY", value: "transdate" },
        { text: "REMARK", value: "diamondTracking.remark" }
      ]
    };
  },
  created() {
    this.company = this.$route.params.company;
    this.getAllCompanies(this.company);
  },
  methods: {
    selectDataInTables() {
      if (this.option == "byReportNo") {
        var reportnos = this.requiredParams.split(",");
        if (this.option1 == "certOnly") {
          this.certLists.filter(shp => {
            if (reportnos.includes(shp.diamondTracking.reportno)) {
              this.selectedcertLists.push(shp);
            }
          });
        } else if (this.option1 == "stoneOnly") {
          this.stoneLists.filter(shp => {
            if (reportnos.includes(shp.diamondTracking.reportno)) {
              this.selectedstoneLists.push(shp);
            }
          });
        } else if (this.option1 == "stoneAndCert") {
          this.stoneCertLists.filter(shp => {
            if (reportnos.includes(shp.diamondTracking.reportno)) {
              this.selectedstoneCertLists.push(shp);
            }
          });
        }
      } else if (this.option == "byStockId") {
        var stocknos = this.requiredParams.split(",");
        if (this.option1 == "certOnly") {
          this.certLists.filter(shp => {
            if (stocknos.includes(shp.diamondTracking.diamond.stockid)) {
              this.selectedcertLists.push(shp);
            }
          });
        } else if (this.option1 == "stoneOnly") {
          this.stoneLists.filter(shp => {
            if (stocknos.includes(shp.diamondTracking.diamond.stockid)) {
              this.selectedstoneLists.push(shp);
            }
          });
        } else if (this.option1 == "stoneAndCert") {
          this.stoneCertLists.filter(shp => {
            if (stocknos.includes(shp.diamondTracking.diamond.stockid)) {
              this.selectedstoneCertLists.push(shp);
            }
          });
        }
      }
    },
    getIndexOf(value) {
      console.log(value);
      return 1;
    },
    cTemplate() {
      return {
        template: Vue.component("columnTemplate", {
          template: "<span>{{modify(data.index)}}</span>",
          data: function(e) {
            console.log(e);
            return {
              data: {}
            };
          },
          methods: {
            modify: function(e) {
              return parseInt(e, 0) + 1;
            }
          }
        })
      };
    },

    async saveSelectedCertList(selectedList, isStone, isCert) {
      var CertOrStone = "";
      if (isStone && !isCert) {
        CertOrStone = "STONES : ";
      } else if (isCert && !isStone) {
        CertOrStone = "CERTS : ";
      } else if (isStone && isCert) {
        CertOrStone = "STONES & CERTS : ";
      }
      var certOrStoneList = [];
      var stoneidList = [];
      var stoneListForDiamondTable = [];
      selectedList.forEach(function(item) {
        stoneidList.push(item.diamondTracking.id);
        stoneListForDiamondTable.push(item.diamondTracking.diamond.id);
        certOrStoneList.push(item.diamondTracking.diamond.stockid);
      });

      const formData = new FormData();
      formData.append("stonelist", stoneidList);
      formData.append("stonelistForDiamond", stoneListForDiamondTable);
      formData.append("stone", isStone);
      formData.append("cert", isCert);

      await axios
        .post(
          this.API_URL + "diamondtracking/updateStoneandCertCollection",
          formData
        )
        .then(() => {
          (this.text = "SUCCESS"),
            (this.snackbar = true),
            (this.updatedStoneList = CertOrStone + certOrStoneList),
            (this.selectedstoneCertLists = []),
            (this.selectedstoneLists = []),
            (this.selectedcertLists = []),
            (this.stoneCertLists = []),
            (this.stoneLists = []),
            (this.certLists = []);
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
      this.getPurchase();
    },
    countDays(date) {
      var dt = new Date(date);
      var currentDate = new Date();
      var timeDiff = currentDate.getTime() - dt.getTime();
      var days = Math.round(timeDiff / (1000 * 3600 * 24));
      return days;
    },
    async getAllCompanies(company) {
      const formData = new FormData();
      formData.append("company", company);
      await axios
        .post(
          this.API_URL + "companyrelation/getCompanyByMainCompany",
          formData
        )
        .then(
          ({ data }) => (
            (this.companies = data), (this.selectedCompanies = data)
          )
        );
      this.getPurchase();
    },
    async getPurchase() {
      this.stoneCertLists = [];
      this.stoneLists = [];
      this.certLists = [];
      const formData = new FormData();
      formData.append("company", JSON.stringify(this.selectedCompanies));
      await axios
        .post(this.API_URL + "diamondtracking/CollectionPending", formData)
        .then(({ data }) => (console.log(data), this.filterData(data)));
    },
    async filterData(purchases) {
      await purchases.filter(shp => {
        if (
          shp.diamondTracking.certStatus == "NO" &&
          shp.diamondTracking.stoneStatus == "NO"
        ) {
          this.stoneCertLists.push(shp);
        }
        if (
          shp.diamondTracking.certStatus != "NO" &&
          shp.diamondTracking.stoneStatus == "NO"
        ) {
          this.stoneLists.push(shp);
        }
        if (
          shp.diamondTracking.certStatus == "NO" &&
          shp.diamondTracking.stoneStatus != "NO"
        ) {
          this.certLists.push(shp);
        }
      });
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(26, 172, 216);
  color: white;
}
.container {
  max-width: 1185px;
  min-width: -webkit-fill-available;
}
.v-input--selection-controls {
  margin-top: 0px;
  padding-top: 4px;
  height: 30px;
}
</style>