<template>
  <div>
    <v-container fluid>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">
          <v-chip dense>{{this.$route.params.company}}</v-chip>EXPENSE ENTRY
        </v-toolbar-title>
      </v-toolbar>
      <v-card flat outlined class="pl-5 pr-5">
        <v-row>
          <v-col>
            <v-row>Party Name</v-row>
            <v-row>
              <v-autocomplete
                style="width:200px"
                required
                v-model="expense.chargeParty"
                :items="chargeparties"
                color="blue"
                item-text="name"
                auto-select-first
                dense
                outlined
                hide-details
                return-object
              ></v-autocomplete>
            </v-row>
          </v-col>
          <v-col>
            <v-row>Charge A/C</v-row>
            <v-row>
              <v-autocomplete
                style="width:200px"
                required
                v-model="expense.chargeAcc"
                :items="chargeaccounts"
                color="blue"
                item-text="name"
                auto-select-first
                dense
                outlined
                hide-details
                return-object
              ></v-autocomplete>
            </v-row>
          </v-col>
          <v-col>
            <v-row>Payment From</v-row>
            <v-row>
              <v-autocomplete
                style="width:200px"
                required
                v-model="expense.paymentFrom"
                :items="paymentaccounts"
                color="blue"
                item-text="name"
                auto-select-first
                dense
                outlined
                hide-details
                return-object
              ></v-autocomplete>
            </v-row>
          </v-col>
          <v-col>
            <v-row>Date</v-row>
            <v-row>
              <v-menu
                v-model="menu2"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field v-model="expense.date" outlined dense readonly v-on="on"></v-text-field>
                </template>
                <v-date-picker v-model="expense.date" @input="menu2 = false"></v-date-picker>
              </v-menu>
            </v-row>
          </v-col>
          <v-col>
            <v-row>Reference No</v-row>
            <v-row>
              <v-text-field v-model="expense.refno" outlined dense></v-text-field>
            </v-row>
          </v-col>
          <v-col>
            <v-row>Remark</v-row>
            <v-row>
              <v-text-field v-model="expense.remark" outlined dense></v-text-field>
            </v-row>
          </v-col>
        </v-row>
      </v-card>
      <v-card flat outlined class="pl-5 pr-5">
        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left"></th>
                <th class="text-left">NO</th>
                <th class="text-left">STOCK NO</th>
                <th class="text-left">STOCK ID</th>

                <th class="text-left">PAID BY</th>
                <th class="text-left">FEES</th>
                <th class="text-left">CHARGE</th>
                <th class="text-left">AMOUNT</th>
                <th class="text-left">CGST AMT</th>
                <th class="text-left">SGST AMT</th>
                <th class="text-left">IGST AMT</th>
                <th class="text-left">NET AMOUNT</th>
                <th class="text-left">CONVERSION RATE</th>
                <th class="text-left">TOTAL AMT</th>
                <th class="text-left">TDS</th>
                <th class="text-left">REMARK</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, k) in items" :key="k">
                <td>
                  <a class="text-success" @click="addNewRow">
                    <font-awesome-icon icon="plus-square" />
                  </a>
                </td>
                <td>{{items.indexOf(item)+1}}</td>

                <td class="tabledata">
                  <v-autocomplete
                    required
                    style="width:100px"
                    v-model="item.entrytransfer.stockid"
                    auto-select-first
                    :items="stonelist"
                    color="blue"
                    item-text="idForStock"
                    dense
                    hide-details
                    outlined
                    @change="selectStoneOwner(item.entrytransfer)"
                    return-object
                  ></v-autocomplete>
                </td>
                <td>{{item.entrytransfer.stock}}</td>

                <td class="tabledata">
                  <v-autocomplete
                    required
                    style="width:200px"
                    v-model="item.entrytransfer.paidBy"
                    auto-select-first
                    :items="companies"
                    color="blue"
                    item-text="name"
                    dense
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    style="width:150px"
                    step=".01"
                    v-model="item.entrytransfer.fees"
                    @change="calculateTax(item.entrytransfer);calculateNetAmt(item.entrytransfer)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    style="width:150px"
                    step=".01"
                    v-model="item.entrytransfer.charge"
                    @change="calculateTax(item.entrytransfer);calculateNetAmt(item.entrytransfer)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    readonly
                    style="width:150px"
                    step=".01"
                    v-model="item.entrytransfer.amount"
                    @change="calculateNetAmt(item.entrytransfer)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    style="width:100px"
                    step=".01"
                    @change="calculateNetAmt(item.entrytransfer)"
                    v-model="item.entrytransfer.cgstAmt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    style="width:100px"
                    step=".01"
                    @change="calculateNetAmt(item.entrytransfer)"
                    v-model="item.entrytransfer.sgstAmt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    style="width:100px"
                    step=".01"
                    @change="calculateNetAmt(item.entrytransfer)"
                    v-model="item.entrytransfer.igstAmt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    style="width:150px"
                    step=".01"
                    @change="calculateNetAmt(item.entrytransfer)"
                    v-model="item.entrytransfer.totalAmt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    style="width:150px"
                    step=".01"
                    @change="calculateNetAmt(item.entrytransfer)"
                    v-model="item.entrytransfer.convRate"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    style="width:150px"
                    step=".01"
                    @change="calculateNetAmt(item.entrytransfer)"
                    v-model="item.entrytransfer.totalAmtUsd"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    type="number"
                    step=".01"
                    @change="calculateNetAmt(item.entrytransfer)"
                    v-model="item.entrytransfer.tdsAmt"
                  />
                </td>

                <td class="tabledata">
                  <input
                    class="form-control"
                    type="text"
                    v-model="item.entrytransfer.remark"
                  />
                </td>
                <td scope="row" class="trashIconContainer">
                  <a class="text-danger" @click="deleteRow(k, item)">
                    <font-awesome-icon icon="trash-alt" />
                  </a>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <v-row>
          <v-col>Amount : {{this.expense.invoiceAmount}}</v-col>
          <v-col>CGST : {{this.expense.invoiceCgst}}</v-col>
          <v-col>SGST : {{this.expense.invoiceSgst}}</v-col>
          <v-col>IGST : {{this.expense.invoiceIgst}}</v-col>
          <v-col>NET : {{this.expense.invoiceNetTotal}}</v-col>
          <v-col>USD AMT : {{this.expense.invoiceUsdTotal}}</v-col>
          <v-col>TDS : {{this.expense.invoiceTds}}</v-col>
        </v-row>
        <v-btn primary @click="saveExpense">SAVE</v-btn>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import commonVari from "../models/commonVari.js";
import ExpenseEntry from "../models/ExpenseEntry.js";
import Expense from "../models/Expense.js";

export default {
  data() {
    return {
      expense: new Expense(),
      invoiceAmount: 0,
      invoiceCgst: 0,
      invoiceSgst: 0,
      invoiceIgst: 0,
      invoiceNetTotal: 0,
      invoiceUsdTotal: 0,
      invoiceTds: 0,
      stonelist: [],
      paymentaccounts: [],
      chargeparties: [],
      chargeaccounts: [],
      entryCompany:null,
      menu2: false,
      date: new Date().toISOString().substr(0, 10),
      API_URL: commonVari.API_URL,
      companies: [],
      chargeParty: null,
      refno: "",
      remark: "",
      items: [
        {
          no: "",
          entrytransfer: new ExpenseEntry()
        }
      ]
    };
  },
  created() {
    this.companyId = this.$route.params.companyId;
    this.companyname = this.$route.params.company;
    this.getCompany();
    this.getAllCompanies();
    this.getAllStones();
    this.getAllParty();
    this.getAllExpenseAcc();
    this.getAllPaymentAcc();
  },
  methods: {
    getAllPaymentAcc() {
      var groupList = [];
      groupList.push("cash in hands");
      groupList.push("BANK ACCOUNTS");
      const formData = new FormData();
      formData.append("companyId", this.companyId);
      formData.append("grouplist", groupList);

      axios
        .post(this.API_URL + "ledger/getLedgerByCompanyByAccountGroup", formData)
        .then(({ data }) => (this.paymentaccounts = data));
    },
    getAllExpenseAcc() {
      var groupList = [];
      groupList.push("DIRECT EXPENSES");
      const formData = new FormData();
      formData.append("companyId", this.companyId);
      formData.append("grouplist", groupList);

      axios
        .post(this.API_URL + "ledger/getLedgerByCompanyByAccountGroup", formData)
        .then(({ data }) => (this.chargeaccounts = data));
    },
    getAllParty() {
      var groupList = [];
      groupList.push("CREDITORS FOR OTHER");
      const formData = new FormData();
      formData.append("companyId", this.companyId);
      formData.append("grouplist", groupList);

      axios
        .post(this.API_URL + "ledger/getLedgerByCompanyByAccountGroup", formData)
        .then(({ data }) => (this.chargeparties = data));
    },

    calculateTax(item) {
      var fees = parseFloat(item.fees);
      var charge = parseFloat(item.charge);
      var amt = 0;
      // var amt = parseFloat(item.amount);
      if (item.amount != null) {
        amt = fees + charge;
      }
      console.log(amt);
      item.amount = amt;
      var cgst = (amt * 9) / 100;
      var sgst = (amt * 9) / 100;
      var igst = 0;
      item.cgstAmt = cgst.toFixed(2);
      item.sgstAmt = sgst.toFixed(2);
      item.igstAmt = igst.toFixed(2);
      var totalAmt = amt + cgst + sgst + igst;
      item.totalAmt = totalAmt.toFixed(2);
    },
    saveExpense() {
      var paymentlist = [];

      for (var i = 0; i < this.items.length; i++) {
        this.items[i].entrytransfer.stockid = this.items[i].entrytransfer.stockid.id;
        paymentlist.push(this.items[i].entrytransfer);
      }
      console.log(this.expense);
      this.expense.company = this.entryCompany;
      console.log(paymentlist);

      const formData = new FormData();
      formData.append("expensehead", JSON.stringify(this.expense));
      formData.append("expenseline", JSON.stringify(paymentlist));

      axios
        .post(this.API_URL + "expense/saveExpense", formData)
        .then(() => {
          // this.text = "SUCCESS";
          // this.snackbar = true;
          // this.items = [];
        })
        .catch(error => {
          this.text = error;
          // this.snackbar = true;
        });
    },
    selectStoneOwner(item) {
      item.stock = item.stockid.stockid;
    },
    deleteRow(index, item) {
      var idx = this.items.indexOf(item);

      if (idx > -1) {
        this.items.splice(idx, 1);
      }
      this.calculateTotal();
    },
    getAllStones() {
      axios
        .get(this.API_URL + "diamond/AllSearchDiamond")
        .then(({ data }) => (this.stonelist = data));
    },
    calculateTotal() {
      var amounttotal;
      amounttotal = this.items.reduce(function(sum, diamond) {
        var lineWeightTotal = parseFloat(diamond.entrytransfer.amount);
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(amounttotal)) {
        this.expense.invoiceAmount = amounttotal.toFixed(2);
      }

      var cgst;
      cgst = this.items.reduce(function(sum, diamond) {
        var lineWeightTotal = parseFloat(diamond.entrytransfer.cgstAmt);
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(cgst)) {
        this.expense.invoiceCgst = cgst.toFixed(2);
      }

      var sgst;
      sgst = this.items.reduce(function(sum, diamond) {
        var lineWeightTotal = parseFloat(diamond.entrytransfer.sgstAmt);
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(sgst)) {
        this.expense.invoiceSgst = sgst.toFixed(2);
      }

      var igst;
      igst = this.items.reduce(function(sum, diamond) {
        var lineWeightTotal = parseFloat(diamond.entrytransfer.igstAmt);
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(igst)) {
        this.expense.invoiceIgst = igst.toFixed(2);
      }

      var total;
      total = this.items.reduce(function(sum, diamond) {
        var lineWeightTotal = parseFloat(diamond.entrytransfer.totalAmt);
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(total)) {
        this.expense.invoiceNetTotal = total.toFixed(2);
      }

      var totalUsd;
      totalUsd = this.items.reduce(function(sum, diamond) {
        var lineWeightTotal = parseFloat(diamond.entrytransfer.totalAmtUsd);
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(totalUsd)) {
        this.expense.invoiceUsdTotal = totalUsd.toFixed(2);
      }

      var tds;
      tds = this.items.reduce(function(sum, diamond) {
        var lineWeightTotal = parseFloat(diamond.entrytransfer.tdsAmt);
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(tds)) {
        this.expense.invoiceTds = tds.toFixed(2);
      }
    },
    calculateNetAmt(item) {
      var fees = parseFloat(item.fees);
      var charge = parseFloat(item.charge);
      var amt = 0;
      // var amt = parseFloat(item.amount);
      if (item.amount != null) {
        amt = fees + charge;
      }
      console.log(amt);
      item.amount = amt;
      // var cgst = (amt * 9/100);
      // var sgst = (amt * 9/100);
      // var igst = 0;
      var cgst = parseFloat(item.cgstAmt);
      var sgst = parseFloat(item.sgstAmt);
      var igst = parseFloat(item.igstAmt);
      if (isNaN(amt)) {
        amt = 0;
      }
      if (isNaN(cgst)) {
        cgst = 0;
      }
      if (isNaN(sgst)) {
        sgst = 0;
      }
      if (isNaN(igst)) {
        igst = 0;
      }
      var con = 0;
      try {
        con = eval(item.convRate);
      } catch (error) {
        alert(error);
      }

      if (typeof con == "undefined") {
        con = 0;
      }
      item.cgstAmt = cgst.toFixed(2);
      item.sgstAmt = sgst.toFixed(2);
      item.igstAmt = igst.toFixed(2);
      var totalAmt = amt + cgst + sgst + igst;
      item.totalAmt = totalAmt.toFixed(2);
      var totalUsd = item.totalAmt * con;
      if (!isNaN(totalUsd)) {
        item.totalAmtUsd = totalUsd.toFixed(2);
      } else {
        item.totalAmtUsd = 0;
      }
      this.calculateTotal();
    },
    addNewRow() {
      this.items.push({
        no: "",
        entrytransfer: new ExpenseEntry(),
        curdate: this.currentDate
      });
    },
    getAllCompanies() {
      axios
        .get(this.API_URL + "companyrelation/getAllCompany")
        .then(({ data }) => (this.companies = data));
    },
    getCompany() {
      const formData = new FormData();
      formData.append("name", this.companyname);
      axios
        .post(this.API_URL + "companyrelation/getCompanyByName", formData)
        .then(({ data }) => (this.entryCompany = data));
    }
  }
};
</script>

<style scoped>
.tabledata {
  border-color: brown;
  padding: 1px;
  outline: black;
  max-height: 30px;
  /* font-weight: bold; */
}
</style>