<template>
  <div class="container">
    <v-alert v-model="success" type="success" dismissible>{{messageFromServer}}</v-alert>
    <v-snackbar
      v-model="snackbar"
      :color="color"
      :timeout="timeout"
      :top="true"
      
    >
      {{ text }}
      <v-btn
        dark
        text
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>

    <div class="row">
      <!-- FOR EDITING DATA -->

      <b-form class="column" style="width:40%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="Company Name :" label-for="countryname">
          <b-form-input
            id="countryname"
            v-model="currentcomRelation.name"
            required
            placeholder="COMPANY NAME"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Company Code :" label-for="countryname">
          <b-form-input
            id="countryname"
            v-model="currentcomRelation.code"
            required
            placeholder="COMPANY CODE"
          ></b-form-input>
        </b-form-group>
         <v-autocomplete
          v-model="currentcomRelation.mainCompany"
          :items="mainCompanies"
          color="white"
          item-text="name"
          label="Parent Company"
          dense
          outlined
          return-object
        ></v-autocomplete>
        <v-autocomplete
          v-model="currentcomRelation.childCompany"
          :items="childCompanies"
          color="white"
          item-text="name"
          label="Child Company"
          dense
          outlined
          return-object
        ></v-autocomplete>

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>

      <!-- FOR SAVING DATA -->
      <b-form class="column" style="width:40%" @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="Company Name :" label-for="statename">
          <b-form-input
            id="statename"
            v-model="comRelation.name"
            required
            placeholder="COMPANY NAME"
          ></b-form-input>
        </b-form-group>
         <b-form-group id="input-group" label="Company Code :" label-for="countryname">
          <b-form-input
            id="countryname"
            v-model="comRelation.code"
            required
            placeholder="COMPANY CODE"
          ></b-form-input>
        </b-form-group>
        <v-autocomplete
          v-model="comRelation.mainCompany"
          :items="mainCompanies"
          color="white"
          item-text="name"
          label="Parent Company"
          dense
          solo
          required
          outlined
          return-object
        ></v-autocomplete>
        <v-autocomplete
          v-model="comRelation.childCompany"
          :items="childCompanies"
          color="white"
          item-text="name"
          label="Child Company"
          dense
          solo
          required
          outlined
          return-object
        ></v-autocomplete>

        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
      <!-- table start       -->
      <div class="column" style="width:60%">
        <v-data-table
          :headers="headers"
          :items="comRelations"
          :items-per-page="10"
          class="elevation-1"
        >
          <template v-slot:item="row">
            <tr v-bind:key="row.item.id">
              <td>{{row.item.name}}</td>
              <td>{{row.item.code}}</td>
              <td>{{row.item.mainCompany.name}}</td>
              <td>{{row.item.childCompany.name}}</td>

              <td>
                <a class="text-success" @click="isEditing=true; selectCompanyRelation(row.item)">
                  <font-awesome-icon icon="edit" />
                </a>
                &emsp;
                <a class="text-danger" @click="deleteCompanyRelation(row.item)">
                  <font-awesome-icon icon="trash-alt" />
                </a>
              </td>
            </tr>
          </template>
        </v-data-table>
      </div>
    </div>
  </div>
</template>

<script>
import Company from "../models/Company";
import ChildCompany from "../models/ChildCompany";
import CompanyRelation from "../models/CompanyRelation";
import axios from "axios";
import commonVari from "../models/commonVari.js";

export default {
  name: "CountryState",

  data() {
    return {
      color:'success',
      snackbar:false,
      text:'',
      timeout: 3000,
      API_URL: commonVari.API_URL,
      success: false,
      messageFromServer: "",
      headers: [
        { text: "Name", value: "statename" },
        { text: "Main Comapny", value: "statecode" },
        { text: "Child Comapny", value: "statecode" },
        { text: "Action", value: "" }
      ],
      isEditing: false,
      mainCompanies:[],
      childCompanies:[],
      mainCompany: new Company(),
      childCompany : new ChildCompany(),
      comRelation: new CompanyRelation(),
      comRelations:[],
      currentcomRelation: new CompanyRelation(),
      
    };
  },
  created() {
    this.getAllCompanies();
    this.getAllChildCompanies();
    this.getAllRelationalCompany();
    
  },
  methods: {
    getAllRelationalCompany() {
      axios
        .get(this.API_URL + "companyrelation/getAllCompany")
        .then(({ data }) => (this.comRelations = data));
    },
    getAllCompanies() {
      axios
        .get(this.API_URL + "company/getAllCompany")
        .then(({ data }) => (this.mainCompanies = data));
    },
    getAllChildCompanies() {
      axios
        .get(this.API_URL + "childcompany/getAllCompany")
        .then(({ data }) => (this.childCompanies = data));
    },
    selectCompanyRelation(country) {
      this.currentcomRelation = country;
    },
    
    saveData() {
      
      axios.post(this.API_URL + "companyrelation/saveCompany", this.comRelation).then(data => {
        if (data.data.error) {
          console.log(data.error);
          this.text = "ERROR..."
          this.snackbar = true;
          // this.messageFromServer = "Not Saved ... ";
          // this.success = true;
        } else {
          this.text = "SUCCESS..."
          this.snackbar = true;
          this.comRelation= new CompanyRelation()
          // this.messageFromServer = "Saved !!!!";
          // this.success = true;
        }

        this.getAllRelationalCompany();
      });
    },
    updateData() {
      // this.comRelation.country = this.country;
      axios
        .post(this.API_URL + "companyrelation/updateCompany", this.currentcomRelation)
        .then(data => {
          console.log(data);
          this.isEditing = false;
          this.getAllRelationalCompany();
        });
    },
    deleteCompanyRelation(country) {
      this.currentcomRelation = country;
      if (confirm) {
        axios
          .post(this.API_URL + "companyrelation/deleteCompany", this.currentcomRelation)
          .then(data => {
            console.log(data);
            this.isEditing = false;
            this.getAllRelationalCompany();
          });
      }
    }
  }
};
</script>
<style scoped>
.container {
  margin: 18px;
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}
</style>