<template>
  <div>
    <v-snackbar v-model="snackbar" :color="colorSnack" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-toolbar class="toolbase" elevation="3" dense>
      <v-toolbar-title elevation="3">PURCHASE BILL</v-toolbar-title>
    </v-toolbar>
    <v-radio-group v-model="options" row style="height:30px">
      <v-radio label="BY STOCK #" value="byStockId"></v-radio>
      <v-radio label="BY STOCK NO." value="byStockNo"></v-radio>
      <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
    </v-radio-group>
    <v-row>
      <v-col cols="12" sm="6" md="4">
        <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
      </v-col>
      <v-col cols="12" sm="6" md="4">
        <v-btn outlined color="indigo" @click="loadDataFromMasters">LOAD DATA</v-btn>
      </v-col>
      <v-col cols="12" sm="12" md="3">
        <button type="button" class="btn btn-info" @click="addNewRow">Add New Row</button>
      </v-col>
    </v-row>
    <v-card style="height:auto" class="justify-center mb-2" flat outlined>
      <v-row dense class="pl-5 pr-5">
        <v-checkbox v-model="purbil.ThirdPartyBill" label="THIRD PARTY BILL"></v-checkbox>
      </v-row>
      <v-row dense class="pl-5 pr-5">
        <v-col v-if="purbil.ThirdPartyBill" cols="12" sm="2">
          <v-row class="pl-5 pr-5">
            <div>PARTY NAME</div>
          </v-row>
          <v-row class="pl-5 pr-5">
            <v-autocomplete
              style="width:200px"
              required
              @change="calculateTotal"
              v-model="purbil.party"
              :items="billingparties"
              color="blue"
              item-text="name"
              auto-select-first
              dense
              outlined
              hide-details
              return-object
            ></v-autocomplete>
          </v-row>
        </v-col>
         <v-col v-if="!purbil.ThirdPartyBill" cols="12" sm="2">
          <v-row class="pl-5 pr-5">
            <div>SELECT PARTY NAME</div>
          </v-row>
          <v-row class="pl-5 pr-5">
            <v-autocomplete
              style="width:200px"
              required
              @change="calculateTotal"
              v-model="purbil.party"
              :items="suppliers"
              color="blue"
              item-text="name"
              auto-select-first
              dense
              outlined
              hide-details
              return-object
            ></v-autocomplete>
          </v-row>
        </v-col>
        <v-col v-if="purbil.ThirdPartyBill" cols="12" sm="2">
          <v-row class="pl-5 pr-5">
            <div>BILL FOR PARTY</div>
          </v-row>
          <v-row class="pl-5 pr-5">
            <v-autocomplete
              style="width:200px"
              required
              v-model="purbil.billForparty"
              :items="suppliers"
              color="blue"
              item-text="name"
              auto-select-first
              dense
              outlined
              hide-details
              return-object
            ></v-autocomplete>
          </v-row>
        </v-col>
        <v-col cols="12" sm="2">
          <v-row class="pl-5 pr-5">DATE</v-row>
          <v-row class="pl-5 pr-5">
            <v-menu
              v-model="menu1"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  @change="calculateDueDate"
                  v-model="purbil.transdate"
                  outlined
                  dense
                  readonly
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                @change="calculateDueDate"
                v-model="purbil.transdate"
                @input="menu1 = false"
              ></v-date-picker>
            </v-menu>
          </v-row>
        </v-col>
        <v-col cols="12" sm="1">
          <v-row class="pl-5 pr-5">TERMS</v-row>
          <v-row class="pl-5 pr-5">
            <v-text-field @change="calculateDueDate" v-model="purbil.terms" outlined dense></v-text-field>
          </v-row>
        </v-col>
        <v-col cols="12" sm="2">
          <v-row class="pl-5 pr-5">DUE DATE</v-row>
          <v-row class="pl-5 pr-5">
            <v-menu
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field v-model="purbil.duedate" outlined dense readonly v-on="on"></v-text-field>
              </template>
              <v-date-picker v-model="purbil.duedate" @input="menu2 = false"></v-date-picker>
            </v-menu>
          </v-row>
        </v-col>
        <v-col cols="12" sm="3">
          <v-row class="pl-5 pr-5">REF #</v-row>
          <v-row class="pl-5 pr-5">
            <v-text-field v-model="purbil.invoiceNo" outlined dense></v-text-field>
          </v-row>
        </v-col>
      </v-row>
      <v-row dense class="pl-5 pr-5">
        <v-checkbox dense v-model="purbil.BillReceivedFromParty" label="BILL REC FROM PARTY"></v-checkbox>
        <v-checkbox dense v-model="purbil.BillGivenToConcern" label="BILL GIVEN TO CONCERN"></v-checkbox>
        <v-checkbox dense v-model="purbil.BillReceivedFromConcern" label="BILL REC FROM CONCERN"></v-checkbox>
        <v-checkbox dense v-model="purbil.ChqReceivedFromConcern" label="CHEQUE REC FROM CONCERN"></v-checkbox>
        <v-checkbox dense v-model="purbil.ChqGivenToParty" label="CHEQUE GIVEN TO PARTY"></v-checkbox>
        <v-text-field v-model="purbil.remark" outlined dense></v-text-field>
      </v-row>
    </v-card>
    <v-simple-table dense>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left"></th>
            <th class="text-left">NO</th>
            <th class="text-left">STOCK NO</th>
            <th class="text-left">STOCK ID</th>
            <th class="text-left" colspan="4">PARTICULAR</th>
            <th class="text-left">WEIGHT</th>
            <th class="text-left">RATE $</th>
            <th class="text-left">DIS</th>
            <th class="text-left">DIS 1</th>
            <th class="text-left">DIS 2</th>
            <th class="text-left">$/CT</th>
            <th class="text-left">TOTAL $</th>
            <th class="text-left">RATE</th>
            <th class="text-left">RS / CT</th>
            <th class="text-left">TOTAL RS</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(diamond, k) in diamonds" :key="k">
            <td>
              <a class="text-success" @click="addNewRow">
                <font-awesome-icon icon="plus-square" />
              </a>
            </td>
            <td>{{diamonds.indexOf(diamond)+1}}</td>
            <td>{{diamond.du.diamond && diamond.du.diamond.idForStock}}</td>
            <td class="tabledata">
              <input
                readonly
                class="form-control"
                style="width:150px"
                type="text"
                :value="diamond.du.diamond && diamond.du.diamond.stockid"
              />
            </td>
            <td style="width:300px">{{diamond.du.diamond && diamond.du.diamond.shape.shapename}}</td>
            <td
              style="width:300px"
            >{{diamond.du.diamond && diamond.du.diamond.color.colorname}}/{{diamond.du.diamond && diamond.du.diamond.clarity.clarityname}}</td>
            <td
              style="width:300px"
            >{{diamond.du.diamond && diamond.du.diamond.cut.name}}/{{diamond.du.diamond && diamond.du.diamond.polish.name}}/{{diamond.du.diamond && diamond.du.diamond.sym.name}}/{{diamond.du.diamond && diamond.du.diamond.fluro.fluroname}}</td>
            <td
              style="width:300px"
            >{{diamond.du.diamond && diamond.du.diamond.lab.labname}}/{{diamond.du.diamond && diamond.du.diamond.reportno}}</td>
            <td class="tabledata">
              <input
                readonly
                class="form-control"
                style="width:100px"
                type="text"
                v-model="diamond.du.weight"
              />
            </td>

            <td class="tabledata">
              <input
                readonly
                style="width:150px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.rap"
              />
            </td>
            <td class="tabledata">
              <input
                readonly
                style="width:80px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.dis"
              />
            </td>
            <td class="tabledata">
              <input
                readonly
                style="width:80px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.dis1"
              />
            </td>
            <td class="tabledata">
              <input
                readonly
                style="width:80px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.dis2"
              />
            </td>
            <td class="tabledata">
              <input
                readonly
                style="width:150px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.usdPerCt"
              />
            </td>
            <td class="tabledata">
              <input
                readonly
                style="width:150px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.usdTotal"
              />
            </td>
            <td class="tabledata">
              <input
                readonly
                style="width:100px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.curRate"
              />
            </td>
            <td class="tabledata">
              <input
                readonly
                style="width:150px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.inrPerCt"
              />
            </td>
            <td class="tabledata">
              <input
                readonly
                style="width:150px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.inrTotal"
              />
            </td>

            <td scope="row" class="trashIconContainer">
              <a class="text-danger" @click="deleteRow(k, diamond)">
                <font-awesome-icon icon="trash-alt" />
              </a>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
    <v-divider></v-divider>
    <v-card flat color="#D81B60">
      <v-row dense class="justify-center mb-2">
        <v-col cols="11" sm="1">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.caratTotal"
            label="WEIGHT"
            outlined
            dense
          ></v-text-field>
        </v-col>
        <v-col cols="11" sm="2">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.usdTotal"
            label="USD TOTAL"
            outlined
            dense
          ></v-text-field>
        </v-col>
        <v-col cols="11" sm="2">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.inrTotal"
            label="INR TOTAL"
            outlined
            dense
          ></v-text-field>
        </v-col>
        <v-col cols="11" sm="1">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.cgstAmt"
            label="CGST"
            outlined
            dense
          ></v-text-field>
        </v-col>
        <v-col cols="11" sm="1">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.sgstAmt"
            label="SGST"
            outlined
            dense
          ></v-text-field>
        </v-col>
        <v-col cols="11" sm="1">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.igstAmt"
            label="IGST"
            outlined
            dense
          ></v-text-field>
        </v-col>
        <v-col cols="11" sm="1">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.roundup"
            label="ROUND UP"
            outlined
            @change="this.calculateGrossTotalWithRoundUp"
            dense
          ></v-text-field>
        </v-col>
        <v-col cols="11" sm="2">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.invoiceAmt"
            label="INVOICE AMT"
            outlined
            dense
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row dense class="justify-center mb-2">
        <v-col cols="11" sm="2">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.comissionRate"
            label="COMISSION RATE"
            outlined
            dense
            @change="calculateComission(purbil)"
          ></v-text-field>
        </v-col>
        <v-col cols="11" sm="2">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.comissionAmt"
            label="COMISSION AMOUNT"
            outlined
            dense
          ></v-text-field>
        </v-col>
        <v-col cols="11" sm="2">
          <v-text-field
            hide-details
            dark
            type="number"
            v-model="purbil.netAmt"
            label="NET AMOUNT"
            outlined
            dense
          ></v-text-field>
        </v-col>
      </v-row>
    </v-card>
    <v-btn class="primary" @click="updteData">SAVE</v-btn>

    <v-divider></v-divider>
    <v-btn class="primary" @click="getReport">GET REPORT</v-btn>
    <v-card>
      <v-data-table
        v-model="selectedStones"
        :headers="headers"
        :items="purchaselists"
        :items-per-page="10"
        show-select
        class="elevation-1"
      >
      
        <template v-slot:item.BillReceivedFromParty="{ item }">
          <v-simple-checkbox :ripple="false" v-model="item.BillReceivedFromParty"></v-simple-checkbox>
        </template>
        <template v-slot:item.BillGivenToConcern="{ item }">
          <v-simple-checkbox :ripple="false" v-model="item.BillGivenToConcern"></v-simple-checkbox>
        </template>
        <template v-slot:item.BillReceivedFromConcern="{ item }">
          <v-simple-checkbox :ripple="false" v-model="item.BillReceivedFromConcern"></v-simple-checkbox>
        </template>
        <template v-slot:item.ChqReceivedFromConcern="{ item }">
          <v-simple-checkbox :ripple="false" v-model="item.ChqReceivedFromConcern"></v-simple-checkbox>
        </template>
        <template v-slot:item.ChqGivenToParty="{ item }">
          <v-simple-checkbox :ripple="false" v-model="item.ChqGivenToParty"></v-simple-checkbox>
        </template>
      </v-data-table>

    </v-card>
    <v-btn class="primary" @click="updateRecord">UPDATE</v-btn>
  </div>
</template>

<script>
import axios from "axios";
import commonVari from "../models/commonVari.js";
import PurchaseBillLine from "../models/PurchaseBillLine.js";
import PurchaseBill from "../models/PurchaseBill.js";
import {
  Page,
  Toolbar,
  ExcelExport,
  PdfExport,
  Edit
} from "@syncfusion/ej2-vue-grids";
export default {
  data() {
    return {
      headers: [
        { text: "IDN", value: "id" },
        { text: "DATE", value: "transdate" },
        { text: "PARTY NAME", value: "party.name" },
        { text: "CARAT", value: "caratTotal" },
        { text: "AMT", value: "inrTotal" },
        { text: "COMM %", value: "comissionRate" },
        { text: "COMMISSION", value: "comissionAmt" },
        { text: "NET AMT", value: "netAmt" },
        { text: "CGST", value: "cgstAmt" },
        { text: "SGST", value: "sgstAmt" },
        { text: "IGST", value: "igstAmt" },
        { text: "INV AMT", value: "invoiceAmt" },
        { text: "BILL RECEIVE FROM PARTY", value: "BillReceivedFromParty" },
        { text: "BILL GIVEN TO CONCERN", value: "BillGivenToConcern" },
        { text: "BILL RECEIVE FROM CONCERN", value: "BillReceivedFromConcern" },
        { text: "CHEQUE RECEIVE FROM CONCERN", value: "ChqReceivedFromConcern" },
        { text: "CHEQUE GIVEN TO PARTY", value: "ChqGivenToParty" },
        
        
      ],
      selectedStones: [],
      toolbar: [
        "ExcelExport",
        "PdfExport",
        "CsvExport",
        "Print",
        "Add",
        "Edit",
        "Delete",
        "Update",
        "Cancel"
      ],
      editSettings: {
        allowEditing: true,
        allowAdding: true,
        allowDeleting: true,
        mode: "Batch"
      },
      purchaselists: [],
      menu2: false,
      menu1: false,
      supplier: null,
      requiredParams: "",
      options: null,
      colorSnack: "success",
      snackbar: false,
      billingparties: [],
      text: "",
      timeout: 3000,
      API_URL: commonVari.API_URL,
      shapes: [],
      colors: [],
      clarities: [],
      cuts: [],
      fluros: [],
      labs: [],
      suppliers: [],
      entryCompany: null,
      purbil: new PurchaseBill(),
      diamonds: [
        {
          du: new PurchaseBillLine()
        }
      ]
      // headers: [
      //   { text: "TYPE", value: "paymentType" },
      //   { text: "DATE", value: "opendate" },
      //   { text: "COMPANY", value: "company.name" },
      //   { text: "PAYMENT MODE", value: "cashBank.name" },
      //   { text: "PARTY NAME", value: "party.name" },
      //   { text: "AMOUNT $", value: "usdAmt" },
      //   { text: "CUR RATE", value: "curRate" },
      //   { text: "AMOUNT RS", value: "inrAmt" },
      //   { text: "REMARK", value: "remark" }
      // ]
    };
  },
  created() {
    this.companyId = this.$route.params.companyId;
    this.companyname = this.$route.params.company;
    this.getAllCompanies();
    this.getAllSuppliers();
    this.getBillingParties();
  },
  provide: {
    grid: [PdfExport, ExcelExport, Toolbar, Page, Edit]
  },
  methods: {
    getBillingParties() {
      axios
        .get(this.API_URL + "ledger/getBillingParties")
        .then(({ data }) => (this.billingparties = data));
    },
    updateRecord(){
      console.log(this.selectedStones);
      
      
      const formData = new FormData();
      formData.append("pb", JSON.stringify(this.selectedStones));
      

      axios
        .post(this.API_URL + "purchasebill/updatePurchaseBillStatus", formData)
        .then(() => {
          this.text = "SUCCESS";
          this.snackbar = true;
          this.diamonds = [];
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });

    },
    calculateComission(purbil) {
      var billAmt = parseFloat(purbil.inrTotal);
      var billComission = parseFloat(purbil.comissionRate);
      purbil.comissionAmt = ((billAmt * billComission) / 100).toFixed(2);
      purbil.netAmt = parseFloat(billAmt) + parseFloat(purbil.comissionAmt);
    },
    getReport() {
      const formData = new FormData();
      formData.append("companyId", this.companyId);
      axios
        .post(
          this.API_URL + "purchasebill/getAllPurchaseBillByCompany",
          formData
        )
        .then(({ data }) => (console.log(data), (this.purchaselists = data)));
      console.log(this.purchaselists);
    },
    getAllCompanies() {
      const formData = new FormData();
      formData.append("name", this.companyname);
      axios
        .post(this.API_URL + "companyrelation/getCompanyByName", formData)
        .then(({ data }) => (this.entryCompany = data));
    },
    getAllSuppliers() {
      var groupList = [];
      groupList.push("SUPPLIER");
      const formData = new FormData();
      formData.append("companyId", this.companyId);
      formData.append("grouplist", groupList);

      axios
        .post(
          this.API_URL + "ledger/getLedgerByCompanyByAccountGroup",
          formData
        )
        .then(({ data }) => (this.suppliers = data));
    },
    updteData() {
      this.purbil.company = this.entryCompany;
      var diamondsToUpdate = [];
      for (var i = 0; i < this.diamonds.length; i++) {
        if (this.diamonds[i].du.diamond != null) {
          diamondsToUpdate.push(this.diamonds[i].du);
        }
      }
      const formData = new FormData();
      formData.append("pb", JSON.stringify(this.purbil));
      formData.append("pblines", JSON.stringify(diamondsToUpdate));

      axios
        .post(this.API_URL + "purchasebill/savePurchaseBill", formData)
        .then(() => {
          this.text = "SUCCESS";
          this.snackbar = true;
          this.diamonds = [];
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
    },
    deleteRow(index, diamond) {
      var idx = this.diamonds.indexOf(diamond);

      if (idx > -1) {
        this.diamonds.splice(idx, 1);
      }
      this.calculateTotal();
    },
    loadDataFromMasters() {
      if (this.options != null) {
        var option = this.options;
        var reqQuery = "";
        const formData = new FormData();
        if (option == "byStockId") {
          reqQuery = "diamond/getAllByStockId";
          formData.append("stockId", this.requiredParams);
        } else if (option == "byReportNo") {
          reqQuery = "diamond/getAllByReportNo";
          formData.append("reportno", this.requiredParams);
        } else if (option == "byStockNo") {
          reqQuery = "diamond/getPurchaseLineByStockNo";
          formData.append("stockNo", this.requiredParams);
        }

        axios
          .post(this.API_URL + reqQuery, formData)
          .then(response => {
            this.addRowQuery(response.data);
          })
          .catch(error => {
            alert(error);
          });
      } else {
        alert("Please specify valid input");
      }
    },
    async addRowQuery(results) {
      for (var j = 0; j < results.length; j++) {
        var filedata = results[j];

        var du = new PurchaseBillLine();
        du.weight = filedata.weight;
        du.rap = filedata.rapprice;
        du.dis = filedata.discount;
        du.dis1 = filedata.dis1;
        du.dis2 = filedata.dis2;
        du.usdPerCt = filedata.usdPerCt;
        du.usdTotal = filedata.usdTotal;
        du.curRate = filedata.curRate;
        du.inrPerCt = filedata.inrPerCt;
        du.inrTotal = filedata.inrtoal;

        du.diamond = filedata.diamondTracking.diamond;
        du.stockId = filedata.diamondTracking.diamond.id;

        this.diamonds.push({
          du
        });
      }
      this.calculateTotal();
    },
    calculateTotal() {
      var weighttotal;
      weighttotal = this.diamonds.reduce(function(sum, diamond) {
        var wt = 0;
        if (diamond.du.weight != null) {
          wt = parseFloat(diamond.du.weight);
        }
        var lineWeightTotal = wt;
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(weighttotal)) {
        this.purbil.caratTotal = weighttotal.toFixed(2);
      }

      var usdtotal;
      usdtotal = this.diamonds.reduce(function(sum, diamond) {
        var wt = 0;
        if (diamond.du.usdTotal != null) {
          wt = parseFloat(diamond.du.usdTotal);
        }
        var lineWeightTotal = wt;
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(usdtotal)) {
        this.purbil.usdTotal = usdtotal.toFixed(2);
      }

      var inrTotal;
      inrTotal = this.diamonds.reduce(function(sum, diamond) {
        var wt = 0;
        if (diamond.du.inrTotal != null) {
          wt = parseFloat(diamond.du.inrTotal);
        }
        var lineWeightTotal = wt;
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(inrTotal)) {
        this.purbil.inrTotal = inrTotal.toFixed(2);
      }
      this.calculateTax();
    },
    calculateTax() {
      var partyStateId = this.purbil.party.province.statecode;

      var comStateId = this.entryCompany.mainCompany.province.statecode;

      var cgst = 0;
      var sgst = 0;
      var igst = 0;

      if (partyStateId == comStateId) {
        igst = 0;
        cgst = (this.purbil.inrTotal * 0.125) / 100;
        sgst = (this.purbil.inrTotal * 0.125) / 100;

        this.purbil.cgstAmt = cgst.toFixed(2);
        this.purbil.sgstAmt = sgst.toFixed(2);
        this.purbil.igstAmt = igst.toFixed(2);
      } else {
        igst = (this.purbil.inrTotal * 0.25) / 100;
        cgst = 0;
        sgst = 0;

        this.purbil.cgstAmt = cgst.toFixed(2);
        this.purbil.sgstAmt = sgst.toFixed(2);
        this.purbil.igstAmt = igst.toFixed(2);
      }
      this.calculateGrossTotal();
    },
    calculateGrossTotalWithRoundUp() {
      var roundUp = 0;
      if (this.purbil.roundup != null) {
        roundUp = parseFloat(this.purbil.roundup);
      }

      var grossTotal =
        parseFloat(this.purbil.inrTotal) +
        parseFloat(this.purbil.cgstAmt) +
        parseFloat(this.purbil.sgstAmt) +
        parseFloat(this.purbil.igstAmt) +
        roundUp;
      var gt = Math.round(grossTotal);
      this.purbil.invoiceAmt = gt.toFixed(2);
    },
    calculateGrossTotal() {
      var grossTotal =
        parseFloat(this.purbil.inrTotal) +
        parseFloat(this.purbil.cgstAmt) +
        parseFloat(this.purbil.sgstAmt) +
        parseFloat(this.purbil.igstAmt);

      var gt = Math.round(grossTotal);
      this.purbil.invoiceAmt = gt.toFixed(0);
      this.purbil.roundup = (gt - grossTotal).toFixed(2);
    },
    calculateDueDate() {
      var days = 0;
      if (!isNaN(this.purbil.terms)) {
        days = parseInt(this.purbil.terms);
      }

      var d = new Date(this.purbil.transdate);
      var Difference_In_Time = days * (1000 * 3600 * 24);

      var timediff = d.getTime() + Difference_In_Time;

      var dateObj = new Date(timediff);

      var year = dateObj.getFullYear();
      var month = dateObj.getMonth() + 1;
      var dt = dateObj.getDate();

      if (dt < 10) {
        dt = "0" + dt;
      }
      if (month < 10) {
        month = "0" + month;
      }

      this.purbil.duedate = year + "-" + month + "-" + dt;
    },
    addNewRow() {
      this.diamonds.push({
        du: new PurchaseBillLine()
      });
    },
    getAllShapes() {
      axios
        .get(this.API_URL + "shape/getAllShapes")
        .then(({ data }) => (this.shapes = data));
    },
    getAllColors() {
      axios
        .get(this.API_URL + "color/getAllColors")
        .then(({ data }) => (this.colors = data));
    },
    getAllClarity() {
      axios
        .get(this.API_URL + "clarity/getAllClarity")
        .then(({ data }) => (this.clarities = data));
    },
    getAllCuts() {
      axios
        .get(this.API_URL + "cps/getAllcps")
        .then(({ data }) => (this.cuts = data));
    },
    getAllFluro() {
      axios
        .get(this.API_URL + "fluro/getAllFluro")
        .then(({ data }) => (this.fluros = data));
    },
    getAllLabs() {
      axios
        .get(this.API_URL + "lab/getAllLabs")
        .then(({ data }) => (this.labs = data));
    },
    toolbarClick: function(args) {
      switch (args.item.text) {
        case "PDF Export":
          this.$refs.grid.pdfExport({ fileName: "new.pdf" });
          break;
        case "Excel Export":
          this.$refs.grid.excelExport({ fileName: "new.xlsx" });
          break;
        case "CSV Export":
          this.$refs.grid.csvExport({ fileName: "new.csv" });
          break;
      }
    }
  }
};
</script>

