<template>
  <div>
    <v-snackbar v-model="snackbar" :color="color" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-row>
      <v-col cols="12" md="3">
        <v-radio-group v-model="option" row>
          <v-radio label="BY STOCK #" value="byStockId"></v-radio>
          <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
        </v-radio-group>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
      </v-col>

      <v-col cols="12" md="1">
        <v-btn outlined color="indigo" @click="selectDataInTables">GET RESULT</v-btn>
      </v-col>
    </v-row>
    <v-card
      flat
      color="pink lighten-5"
      v-for="(items, index) in this.trackingResults"
      v-bind:key="items.index"
    >
      <v-card-title>
        <v-row>
          <v-col>
            <v-chip
              class="ma-2"
              color="pink"
              label
              text-color="white"
              v-model="stocknos[index]"
            >STOCK NO : {{stocknos[index]}}</v-chip>
          </v-col>
          <v-col>
            <v-radio-group v-model="actiontaken[index]" row>
              <v-radio label="RESULT OK" value="resultOk"></v-radio>
              <v-radio label="SEND TO RECUT" value="sendToRecut"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col>
            <v-btn
              outlined
              color="indigo"
              @click="updateAction(stocknos[index],actiontaken[index])"
            >UPDATE</v-btn>
          </v-col>
        </v-row>
      </v-card-title>

      <v-card-text>
        <v-data-table
          hide-default-footer
          :headers="headers"
          :items="trackingResults[index]"
          class="elevation-1"
        ></v-data-table>
      </v-card-text>
      <v-card-text>
        <v-data-table
          hide-default-footer
          :headers="headers1"
          :items="trackingResults1[index]"
          class="elevation-1"
        ></v-data-table>
      </v-card-text>
      <v-divider />
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
import commonVari from "../models/commonVari.js";
import { DetailRow, Sort } from "@syncfusion/ej2-vue-grids";

export default {
  data() {
    return {
      color: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      requiredParams: "",
      option: null,
      actiontaken: [],
      API_URL: commonVari.API_URL,
      reportnos: [],
      stocknos: [],
      finalStocknos: [],
      trackingResults: [],
      filteredObjects: [],
      trackingResults1: [],
      filteredObjects1: [],
      purchaseline: {},
      gialine: {},
      singleResult: [],

      headers: [
        { text: "STOCK#", value: "diamond.stockid" },
        { text: "PARTICULAR", value: "trasnType" },
        { text: "COMPANY", value: "diamond.pur_lines.company[0].name" },
        { text: "SHAPE", value: "shape.shapename" },
        { text: "WEIGHT", value: "currentweight" },
        { text: "COLOR", value: "color.colorname" },
        { text: "CLARITY", value: "clarity.clarityname" },
        { text: "CUT", value: "cut.name" },
        { text: "POLISH", value: "polish.name" },
        { text: "SYM", value: "sym.name" },
        { text: "FLURO", value: "fluro.fluroname" },
        { text: "REPORT #", value: "reportno" },
        { text: "KEY-TO-SYMBOL", value: "keyToSymbol" },
        { text: "REPORT-COMMENT", value: "infoMessage" },
        { text: "INSCRIPTION", value: "inscription" },
        { text: "DEPTH %", value: "depthPt" },
        { text: "TABLE %", value: "tablePt" },
        { text: "LENGTH", value: "length" },
        { text: "TABLE", value: "keyToSymbol" }
      ],
      headers1: [
        { text: "PARTICULAR", value: "name" },
        { text: "COMPANY", value: "remark" },
        { text: "COLOR", value: "color" },
        { text: "CLARITY", value: "clarity" },
        { text: "BGM", value: "bgm" },
        { text: "SHADE", value: "shade" },
        { text: "EYE CLEAN", value: "eyeClean" },
        { text: "TABLE INC", value: "tableInc" },
        { text: "TABLE BLACK", value: "tableBlack" },
        { text: "SIDE BLACK", value: "sideBlack" },
        { text: "INC PAT", value: "incPattern" },
        { text: "INT GRAINING", value: "intGraining" },
        { text: "TABLE OPEN", value: "tableOpen" },
        { text: "CROWN OPEN", value: "crownOpen" },
        { text: "PAV OPEN", value: "pavOpen" },
        { text: "REFLECTION", value: "reflection" },
        { text: "HEART & ARROW", value: "heart" },
        { text: "SPREAD RATIO", value: "spreadRatio" }
      ]
    };
  },
  provide: {
    grid: [DetailRow, Sort]
  },
  created() {
    this.getAllShapes();
    this.getAllColors();
    this.getAllClarity();
    this.getAllCuts();
    this.getAllFluro();
    this.getAllLabs();
  },
  computed: {
    // finalStocknos1(){
    //   return this.finalStocknos;
    // },
    gotResults: function(item) {
      console.log(item);
      return item;
    }
  },

  methods: {
    updateAction(item, actiontaken) {
      console.log(action);
      console.log(item);
      var action = false;
      if (actiontaken == "resultOk") {
        action = false;
      } else if (actiontaken == "sendToRecut") {
        action = true;
      }
      const formData = new FormData();
      formData.append("stockid", item);
      formData.append("action", action);
      axios
        .post(this.API_URL + "diamond/updateResultAction", formData)
        .then(response => {
          this.text = response.data;
          this.snackbar = true;
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
    },
    getStockNo(item) {
      return item.diamond.stockid;
    },
    setPurchaseLine(item, index) {
      console.log(item);
      console.log(index);
    },
    async getResults(item) {
      this.singleResult = [];
      console.log("item", item);

      const formData = new FormData();
      formData.append("stonelist", item);
      formData.append("certList", item);
      await axios
        .post(this.API_URL + "diamondtracking/getComparisonReport", formData)
        .then(response => {
          this.singleResult = response.data;
        });
    },
    async selectusing() {
      this.trackingResults = [];
      this.filteredObjects = [];
      var reportnos = [];
      var stocknos = [];
      if (this.option == "byReportNo") {
        reportnos = this.requiredParams.split(",");
      } else if (this.option == "byStockId") {
        stocknos = this.requiredParams.split(",");
      }
      console.log(reportnos);
      this.finalStocknos = stocknos;
      console.log(this.finalStocknos);
    },
    async selectDataInTables() {
      this.trackingResults = [];
      this.trackingResults1 = [];
      this.filteredObjects = [];
      this.filteredObjects1 = [];
      var reportnos = [];
      var stocknos = [];
      if (this.option == "byReportNo") {
        reportnos = this.requiredParams.split(",");
      } else if (this.option == "byStockId") {
        stocknos = this.requiredParams.split(",");
        this.stocknos = this.requiredParams.split(",");
        
      }
      var request = [];
      var request1 = [];
      await stocknos.forEach(item => {
        const formData = new FormData();
        formData.append("stonelist", item);
        formData.append("certList", reportnos);
        request.push(
          axios.post(
            this.API_URL + "diamondtracking/getComparisonReport",
            formData
          )
        );
      });
      await stocknos.forEach(item => {
        const formData = new FormData();
        formData.append("stonelist", item);
        formData.append("certList", reportnos);
        request1.push(
          axios.post(this.API_URL + "personView/GetAllViewByDiamonds", formData)
        );
      });

      await axios
        .all(request)
        .then(
          axios.spread((...responses) => {
            for (var i = 0; i < responses.length; i++) {
              console.log(responses[i].data);
              this.filteredObjects.push(responses[i].data);
            }
          })
        )
        .catch(errors => {
          console.error(errors);
        });
      //
      // var result1  = this.filteredObjects;

      await axios
        .all(request1)
        .then(
          axios.spread((...responses) => {
            for (var i = 0; i < responses.length; i++) {
              console.log(responses[i].data);
              this.filteredObjects1.push(responses[i].data);
            }
          })
        )
        .catch(errors => {
          console.error(errors);
        });
      // var result2 = this.filteredObjects1;
      this.trackingResults = this.filteredObjects;
      this.trackingResults1 = this.filteredObjects1;
    },

    getAllShapes() {
      axios
        .get(this.API_URL + "shape/getAllShapes")
        .then(({ data }) => (this.shapes = data));
    },
    getAllColors() {
      axios
        .get(this.API_URL + "color/getAllColors")
        .then(({ data }) => (this.colors = data));
    },
    getAllClarity() {
      axios
        .get(this.API_URL + "clarity/getAllClarity")
        .then(({ data }) => (this.clarities = data));
    },
    getAllCuts() {
      axios
        .get(this.API_URL + "cps/getAllcps")
        .then(({ data }) => (this.cuts = data));
    },
    getAllFluro() {
      axios
        .get(this.API_URL + "fluro/getAllFluro")
        .then(({ data }) => (this.fluros = data));
    },
    getAllLabs() {
      axios
        .get(this.API_URL + "lab/getAllLabs")
        .then(({ data }) => (this.labs = data));
    }
  }
};
</script>

