<template>
  <div>
    <v-snackbar v-model="snackbar" :color="color" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>

    <v-container>
      <v-toolbar class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">STOCK ONLY ENTRY</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-menu
        left
        bottom
      >
        <template v-slot:activator="{ on }">
        <v-btn icon v-on="on">
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
        </template>
        <v-list>
            <v-list-item>
            <v-list-item-title @click="addShapeForm=true">SHAPE</v-list-item-title>
            </v-list-item>
            <v-list-item>
            <v-list-item-title @click="addColorForm=true">COLOR</v-list-item-title>
            </v-list-item>
            <v-list-item>
            <v-list-item-title @click="addClarityForm=true">CLARITY</v-list-item-title>
            </v-list-item>
            <v-list-item>
            <v-list-item-title @click="addCutForm=true">CUT/POLISH/SYM</v-list-item-title>
            </v-list-item>
            <v-list-item>
            <v-list-item-title @click="addFluroForm=true">FLURO</v-list-item-title>
            </v-list-item>
        </v-list>
        </v-menu>
      </v-toolbar>
    </v-container>

    <v-dialog v-model="addShapeForm" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">SHAPE</span>
          </v-card-title>
          <v-card-text>
            <shapeCreation></shapeCreation>
          </v-card-text>
          <v-spacer></v-spacer>
          <v-card-actions>
             <v-spacer></v-spacer>
            <v-btn color="red darken-1" text @click="addShapeForm = false;getAllShapes();">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
       <v-dialog v-model="addColorForm" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">COLOR</span>
          </v-card-title>
          <v-card-text>
            <colorCreation></colorCreation>
          </v-card-text>
         
          <v-card-actions>
             <v-spacer></v-spacer>
            <v-btn color="red darken-1" text @click="addColorForm = false;getAllColors();">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog v-model="addClarityForm" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">CLARITY</span>
          </v-card-title>
          <v-card-text>
            <v-container>
            <clarityCreation></clarityCreation>
            </v-container>
          </v-card-text>
          
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn outlined="" color="red darken-1" text @click="addClarityForm = false;getAllClarity();">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog v-model="addCutForm" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">CUT / POLISH / SYM</span>
          </v-card-title>
          <v-card-text>
            <cutCreation></cutCreation>
          </v-card-text>
          <v-spacer></v-spacer>
          <v-card-actions>
             <v-spacer></v-spacer>
            <v-btn color="red darken-1" text @click="addCutForm = false;getAllCuts();">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog v-model="addFluroForm" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">FLURO</span>
          </v-card-title>
          <v-card-text>
            <fluroCreation></fluroCreation>
          </v-card-text>
          <v-spacer></v-spacer>
          <v-card-actions>
             <v-spacer></v-spacer>
            <v-btn color="red darken-1" text @click="addFluroForm = false;getAllFluro();">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    <div v-if="!isEditing">
      <!-- <v-container fluid> -->
      <v-radio-group v-model="options" row style="height:30px">
        <v-radio label="BY STOCK #" value="byStockId"></v-radio>
        <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
      </v-radio-group>
      <v-row>
        <v-col cols="12" sm="6" md="4">
          <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="4">
          <v-btn outlined color="indigo" @click="loadDataFromMasters">LOAD DATA</v-btn>
        </v-col>
        <v-col cols="12" sm="12" md="3">
          <button type="button" class="btn btn-info" @click="addNewRow">Add New Row</button>
        </v-col>
      </v-row>
      <!-- <input type="file" @change="onCSVFileSelected" id="myfile" name="myfile" />
      <v-btn class="ma-2" outlined color="indigo" @click="parseCsv">Load data from file</v-btn>-->
      <v-spacer></v-spacer>
      <!-- FOR SAVING DATA -->
      <b-form @submit.prevent="savePurchase">
        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left"></th>
                <th class="text-left">NO</th>
                <th class="text-left">DATE</th>
                <th class="text-left">COMPANY</th>
                <th class="text-left">LAB</th>
                <th class="text-left" colspan="2">REPORT NO</th>
                <th class="text-left">STOCK ID</th>
                <th class="text-left">SHAPE</th>
                <th class="text-left">WEIGHT</th>
                <th class="text-left">COLOR</th>
                <th class="text-left">CLARITY</th>
                <th class="text-left">CUT</th>
                <th class="text-left">POLISH</th>
                <th class="text-left">SYM</th>
                <th class="text-left">FLURO</th>
                <!-- <th class="text-left">FLURO COLOR</th> -->
                <th class="text-left">RAP</th>
                <th class="text-left">DISCOUNT</th>
                <th class="text-left">DIS 1</th>
                <th class="text-left">DIS 2</th>
                <th class="text-left">CUR RATE</th>
                <th class="text-left">$ / CT</th>
                <th class="text-left">TOTAL $</th>
                <th class="text-left">RS / CT</th>
                <th class="text-left">TOTAL RS</th>
                <th class="text-left">EXPNESE USD</th>
                <th class="text-left">EXPNESE</th>
                <th class="text-left">GROSS RS</th>
                <th class="text-left">GROSS USD</th>
                <th class="text-left">INVOICE STATUS</th>
                <th class="text-left">PAYMENT MODE</th>
                <th class="text-left">TERM</th>
                <th class="text-left">DUE DATE</th>
                <!-- <th class="text-left">CURRENT LOCATION</th> -->
                <th class="text-left">BUY FOR LOCATION</th>
                <th class="text-left">STONE STATUS</th>
                <th class="text-left">CERT STATUS</th>

                <th class="text-left">REMARK</th>

                <th class="text-left">DELETE</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(diamond, k) in diamonds" :key="k">
                <!-- <button style="margin:20px" type="button" class="btn btn-info" @click="addNewRow">Add</button> -->
                <td>
                  <a class="text-success" @click="addNewRow">
                    <font-awesome-icon icon="plus-square" />
                  </a>
                </td>

                <td>{{diamonds.indexOf(diamond)+1}}</td>
                <td class="tabledata">
                  <input
                    required
                    class="form-control"
                    style="width:150px"
                    v-model="diamond.opendate"
                    type="date"
                    @change="calculateDueDate(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:200px"
                    required
                    v-model="diamond.buyingCompany"
                    :items="companies"
                    color="blue"
                    item-text="name"
                    solo-inverted
                    auto-select-first
                    dense
                    outlined
                    hide-details
                    return-object
                    @change="setBuyingCompany(diamond)"
                  ></v-autocomplete>
                </td>

                <td class="tabledata">
                  <v-autocomplete
                    style="width:150px"
                    required
                    v-model="diamond.lab"
                    :items="labs"
                    color="white"
                    item-text="labname"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <input class="form-control" style="width:150px" type="text" v-model="diamond.reportno" />
                </td>
                <td>
                  <a class="text-success" @click="getGiaResultreport(diamond)">
                    <font-awesome-icon icon="download" />
                  </a>
                  <!-- <button @click="getGiaResultreport(diamond)">GET</button> -->
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    style="width:150px"
                    type="text"
                    v-model="diamond.stockid"
                  />
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:150px"
                    required
                    @change="calculateRapPrice(diamond)"
                    v-model="diamond.shape"
                    :items="shapes"
                    color="white"
                    item-text="shapename"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.weight"
                    @change="calculateLineTotal(diamond); calculateRapPrice(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:150px"
                    required
                    v-model="diamond.color"
                    :items="colors"
                    color="white"
                    item-text="colorname"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    @change="calculateRapPrice(diamond)"
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:150px"
                    required
                    v-model="diamond.clarity"
                    :items="clarities"
                    color="white"
                    item-text="clarityname"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    @change="calculateRapPrice(diamond)"
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:150px"
                    required
                    v-model="diamond.cut"
                    :items="cuts"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:150px"
                    required
                    v-model="diamond.polish"
                    :items="cuts"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:150px"
                    required
                    v-model="diamond.sym"
                    :items="cuts"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:150px"
                    required
                    v-model="diamond.fluro"
                    :items="fluros"
                    color="white"
                    item-text="fluroname"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <!-- <td class="tabledata">
                <input class="form-control" type="text" v-model="diamond.flurocolor" />
                </td>-->

                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.rapprice"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    step=".01"
                    v-model="diamond.discount"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    step=".01"
                    v-model="diamond.dis1"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    step=".01"
                    v-model="diamond.dis2"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    :disabled="!diamond.isIndia"
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.currate"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    v-model="diamond.usdPerCt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.usdTotal"
                  />
                </td>

                <td class="tabledata">
                  <input
                    
                    style="width:150px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    v-model="diamond.inrPerCt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:150px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.inrTotal"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.expenseusd"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    :disabled="!diamond.isIndia"
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.expense"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:150px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.grossTotal"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:150px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.grossUsd"
                  />
                </td>
                <td class="tabledata">
                  <v-autocomplete
                     :disabled="!diamond.isIndia"
                    style="width:100px"
                    required
                    v-model="diamond.invoiceStatus"
                    auto-select-first
                    :items="statuses"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    hide-details
                    outlined
                    @change="changePaymentMode(diamond)"
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:200px"
                    :disabled="!diamond.isIndia"
                    required
                    :readonly="showPaymentMode"
                    v-model="diamond.billingParty"
                    auto-select-first
                    :items="billingparties"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step="1"
                    v-model="diamond.term"
                    @change="calculateDueDate(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    readonly
                    class="form-control"
                    style="width:150px"
                    v-model="diamond.dueDate"
                    type="date"
                  />
                </td>
                <!-- <td>
                <v-autocomplete
                  style="width:150px"
                  required
                  v-model="diamond.currentLocation"
                  :items="countries"
                  color="white"
                  item-text="countryname"
                  dense
                  solo-inverted
                  full-width
                  hide-details
                  outlined
                  return-object
                ></v-autocomplete>
                </td>-->
                <td>
                  <v-autocomplete
                    style="width:150px"
                    required
                    v-model="diamond.buyForLocation"
                    :items="countries"
                    color="white"
                    item-text="countryname"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:100px"
                    required
                    v-model="diamond.stoneStatus"
                    auto-select-first
                    :items="statuses"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    hide-details
                    outlined
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:100px"
                    required
                    v-model="diamond.certStatus"
                    auto-select-first
                    :items="statuses"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    hide-details
                    outlined
                  ></v-autocomplete>
                </td>

                <td class="tabledata">
                  <input
                    class="form-control"
                    style="width:150px"
                    type="text"
                    v-model="diamond.remark"
                  />
                </td>

                <td scope="row" class="trashIconContainer">
                  <a class="text-danger" @click="deleteRow(k, diamond)">
                    <font-awesome-icon icon="trash-alt" />
                  </a>
                </td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td colspan="2"></td>
                <td></td>
                <td></td>
                <td></td>
                <td colspan="2"></td>
                <td></td>
                <td></td>
                <td>{{invoice_weight}}</td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td>{{invoice_usdtotal}}</td>
                <td></td>
                <td>{{invoice_inrtotal}}</td>
              </tr>
            </tfoot>
          </template>
        </v-simple-table>
        <v-spacer></v-spacer>
        <v-btn
          style="margin-top:10px; margin-left:10px;"
          color="success"
          type="submit"
          class="mr-4"
        >Submit</v-btn>
      </b-form>
    </div>

    <!-- FOR EDITING DATA     -->

    <div v-if="isEditing">
      <button style="margin:20px" type="button" class="btn btn-info" @click="addNewRow">Add New Row</button>
      <v-spacer></v-spacer>
      <v-simple-table dense>
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">DATE</th>
              <th class="text-left">BUYING COMPANY</th>
              <th class="text-left">CHILD COMPANY</th>
              <th class="text-left">PARTNERSHIP RATIO</th>
              <th class="text-left">PURPOSE</th>
              <th class="text-left">supplier</th>
              <th class="text-left">STOCK ID</th>
              <th class="text-left">SHAPE</th>
              <th class="text-left">WEIGHT</th>
              <th class="text-left">COLOR</th>
              <th class="text-left">CLARITY</th>
              <th class="text-left">CUT</th>
              <th class="text-left">POLISH</th>
              <th class="text-left">SYM</th>
              <th class="text-left">FLURO</th>
              <th class="text-left">FLURO COLOR</th>
              <th class="text-left">LAB</th>
              <th class="text-left">REPORT NO</th>
              <th class="text-left">RAP</th>
              <th class="text-left">DISCOUNT</th>
              <th class="text-left">DIS 1</th>
              <th class="text-left">DIS 2</th>
              <th class="text-left">CUR RATE</th>
              <th class="text-left">$ / CT</th>
              <th class="text-left">TOTAL $</th>
              <th class="text-left">RS / CT</th>
              <th class="text-left">TOTAL RS</th>
              <th class="text-left">DELETE</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(diamond, k) in editpurchaselists.diamond" :key="k">
              <td class="tabledata">
                <input
                  class="form-control"
                  style="width:150px"
                  v-model="diamond.opendate"
                  type="date"
                />
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:200px"
                  required
                  v-model="diamond.pur_lines.buyingCompany[0]"
                  :items="companies"
                  color="blue"
                  item-text="name"
                  solo-inverted
                  dense
                  outlined
                  hide-details
                  opeque
                  return-object
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:200px"
                  required
                  v-model="diamond.pur_lines.childCompany[0]"
                  :items="companies"
                  color="white"
                  item-text="name"
                  dense
                  flat
                  solo-inverted
                  outlined
                  hide-details
                  return-object
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <input
                  style="width:120px"
                  class="form-control"
                  type="number"
                  min="0"
                  step=".01"
                  v-model="diamond.pur_lines.partnerratio"
                />
                <!-- <input  style="width:80px" class="form-control" type="number" v-model="diamond.partnerration" /></td> -->
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:200px"
                  required
                  v-model="diamond.purpose"
                  auto-select-first
                  :items="purposes"
                  color="white"
                  item-text="name"
                  dense
                  solo-inverted
                  hide-details
                  outlined
                ></v-autocomplete>
              </td>
              <td class="tabledata">
                <v-autocomplete
                  style="width:200px"
                  required
                  v-model="diamond.pur_lines.supplier[0]"
                  auto-select-first
                  :items="suppliers"
                  color="white"
                  item-text="name"
                  dense
                  solo-inverted
                  full-width
                  hide-details
                  outlined
                  return-object
