  },
  data() {
    return {
      
      addShapeForm:false,
      addColorForm:false,
      addClarityForm:false,
      addCutForm:false,
      addFluroForm:false,
      color: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      requiredParams: "",
      options: null,
      showPaymentMode: false,
      currentDate: "",
      loading: false,
      isEditing: false,
      editpurchaselists: [],
      purchaselists: [],
      API_URL: commonVari.API_URL,
      billingparties: [],
      suppliers: [],
      buyers: [],
      shapes: [],
      colors: [],
      clarities: [],
      cuts: [],
      fluros: [],
      labs: [],
      companies: [],
      trans: new Transactions(),
      menu2: false,
      purchase_head: new Purchase(),
      current_purchase_head: new Purchase(),
      diam: new Diamond(),
      purchase_line: new purchase_line(),
      plines: [],
      diamondlines: [],
      translines: [],
      invoice_weight: 0,
      invoice_usdtotal: 0,
      invoice_inrtotal: 0,
      countries: [],
      statuses: [{ name: "YES" }, { name: "NO" }, { name: "N/A" }],
      purposes: [
        { name: "COR" },
        { name: "RE-CUT" },
        { name: "STOCK" },
        { name: "LAB" }
      ],
      currentDiamonds: [],
      diamonds: [
        {
          isIndia:false,
          supplierLine:[],
          no: "",
          grossUsd: 0,
          expenseusd: 0,
          stockid: "",
          opendate: this.currentDate,
          buyingCompany: new CompanyRelation(),
          childCompany: new CompanyRelation(),
          partnerratio: 0,
          partnerAmt: 0,
          purpose: "",
          color: new Color(),
          clarity: new Clarity(),
          cut: new Cut_Pol_Sym(),
          polish: new Cut_Pol_Sym(),
          sym: new Cut_Pol_Sym(),
          fluro: new Fluro(),
          flurocolor: "",
          lab: new Lab(),
          reportno: "",
          shape: new Shape(),
          supplier: new Ledger(),
          buyer: new Ledger(),
          weight: 0,
          rapprice: 0,
          discount: 0,
          dis1: 0,
          dis2: 0,
          usdPerCt: 0,
          usdTotal: 0,
          currate: 0,
          inrPerCt: 0,
          inrTotal: 0,
          grossTotal: 0,
          purchase: this.purchase_head,
          expense: 0,
          billingParty: new Ledger(),
          term: 0,
          dueDate: this.currentDate,
          currentLocation: "",
          buyForLocation: "",
          stoneStatus: "",
          certStatus: "",
          invoiceStatus: "",
          remark: "",
          // NEW FIELDS
          length: "",
          width: "",
          depth: "",
          colorDesc: "",
          depthPt: "",
          tablePt: "",
          crAg: "",
          crHt: "",
          pavAg: "",
          pavDp: "",
          starLn: "",
          lrHalf: "",
          girdle: "",
          girdleCondition: "",
          girdlePct: "",
          culetSize: "",
          keyToSymbol: "",
          reportDate: "",
          inscription: "",
          infoMessage: "",
          clarityStatus: ""
        }
      ],
      gia: "",
      csvfile: null
    };
  },
  beforeCreate(){
      var d = new Date();
      var dateObj = new Date(d.getTime());

      var year = dateObj.getFullYear();
      var month = dateObj.getMonth() + 1;
      var dt = dateObj.getDate();

      if (dt < 10) {
        dt = "0" + dt;
      }
      if (month < 10) {
        month = "0" + month;
      }

      this.currentDate = year + "-" + month + "-" + dt;
    
  },
  created() {
    
    this.getAllShapes();
    // this.getAllLedgers();
    this.getBillingParties();
    this.getAllCompanies();
    this.getAllColors();
    this.getAllClarity();
    this.getAllCuts();
    this.getAllFluro();
    this.getAllLabs();
    this.getAllCountries();
    this.setCurrentDate();
  },
  methods: {
    loadDataFromMasters() {
      if (this.options != null) {
        var option = this.options;
        var reqQuery = "";
        const formData = new FormData();
        if (option == "byStockId") {
          reqQuery = "labData/getAllByStockId";
          formData.append("stockId", this.requiredParams);
        } else if (option == "byReportNo") {
          reqQuery = "labData/getAllByReportNo";
          formData.append("reportno", this.requiredParams);
        }

        axios
          .post(this.API_URL + reqQuery, formData)
          .then(response => {
            this.addRowQuery(response.data);
          })
          .catch(error => {
            console.log(error);
          });
      } else {
        alert("Please specify valid input");
      }
    },
    changePaymentMode(diamond) {
      if (diamond.invoiceStatus == "YES") {
        this.showPaymentMode = true;
        diamond.billingParty = null;
      } else {
        this.showPaymentMode = false;
      }
    },
    setCurrentDate() {
      var d = new Date();
      var dateObj = new Date(d.getTime());

      var year = dateObj.getFullYear();
      var month = dateObj.getMonth() + 1;
      var dt = dateObj.getDate();

      if (dt < 10) {
        dt = "0" + dt;
      }
      if (month < 10) {
        month = "0" + month;
      }

      this.currentDate = year + "-" + month + "-" + dt;
    },
    calculateDueDate(diamond) {
      var days = 0;
      if (!isNaN(diamond.term)) {
        days = parseInt(diamond.term);
      }

      var d = new Date(diamond.opendate);
      var Difference_In_Time = days * (1000 * 3600 * 24);

      var timediff = d.getTime() + Difference_In_Time;

      var dateObj = new Date(timediff);

      var year = dateObj.getFullYear();
      var month = dateObj.getMonth() + 1;
      var dt = dateObj.getDate();

      if (dt < 10) {
        dt = "0" + dt;
      }
      if (month < 10) {
        month = "0" + month;
      }

      diamond.dueDate = year + "-" + month + "-" + dt;
    },
    onCSVFileSelected() {
      this.csvfile = event.target.files[0];
    },
    parseCsv() {
      papaparse.parse(this.csvfile, {
        complete: results => {
          console.log(results.data);
          this.addRowFromCSVData(results.data);
        }
      });
    },
    async addRowQuery(results) {
      console.log(results);
      for (var j = 0; j < results.length; j++) {
        var filedata = results[j];
        console.log(filedata);

        var di = new Diamond();
        di.weight = filedata.currentweight;
        di.color = filedata.color;
        di.shape = filedata.shape;
        di.clarity = filedata.clarity;

        await this.calculateRapPrice(di).then(response => {
          var rap = response;

          this.diamonds.push({
            isIndia:false,
            supplierLine:[],
            partnerAmt: 0,
            no: "",
            grossUsd: 0,
            expenseusd: 0,
            stockid: "",
            opendate: this.currentDate,
            buyingCompany: new CompanyRelation(),
            childCompany: new CompanyRelation(),
            partnerratio: 0,
            purpose: "",
            color: filedata.color,

            clarity: filedata.clarity,
            cut: filedata.cut,
            polish: filedata.polish,
            sym: filedata.sym,
            fluro: filedata.fluro,
            lab: filedata.lab,
            reportno: filedata.reportno,
            shape: filedata.shape,
            supplier: new Ledger(),
            buyer: new Ledger(),
            weight: filedata.currentweight,
            rapprice: rap,
            discount: 0,
            dis1: 0,
            dis2: 0,
            usdPerCt: 0,
            usdTotal: 0,
            currate: 0,
            inrPerCt: 0,
            inrTotal: 0,
            term: 0,
            dueDate: this.currentDate,
            // NEW FIELDS
            length: filedata.length,
            width: filedata.width,
            depth: filedata.depth,
            colorDesc: filedata.colorDesc,
            depthPt: filedata.depthPt,
            tablePt: filedata.tablePt,
            crAg: filedata.crAg,
            crHt: filedata.crHt,
            pavAg: filedata.pavAg,
            pavDp: filedata.pavDp,
            starLn: filedata.starLn,
            lrHalf: filedata.lrHalf,
            girdle: filedata.girdle,
            girdleCondition: filedata.girdleCondition,
            girdlePct: filedata.girdlePct,
            culetSize: filedata.culetSize,
            keyToSymbol: filedata.keyToSymbol,
            reportDate: filedata.reportDate,
            inscription: filedata.inscription,
            infoMessage: filedata.infoMessage,
            clarityStatus: "",
            flurocolor: filedata.flurocolor,
            grossTotal: 0,
            purchase: this.purchase_head,
            expense: 0,
            billingParty: null,

            currentLocation: "",
            buyForLocation: "",
            stoneStatus: "",
            certStatus: "",
            invoiceStatus: "",
            remark: ""
          });
        });
      }
    },
    async addRowFromCSVData(results) {
      console.log(results);
      for (var j = 1; j < results.length - 1; j++) {
        var filedata = results[j];
        var carat = filedata[7];
        var col = null;
        this.colors.filter(shp => {
          if (shp.colorname == filedata[8]) {
            console.log(shp);
            col = shp;
          }
        });
        var flr = null;
        this.fluros.filter(shp => {
          if (shp.fluro_ref.indexOf(filedata[15]) != -1) {
            flr = shp;
          }
        });

        var ct = null;
        this.cuts.filter(shp => {
          if (shp.reference.indexOf(filedata[12]) != -1) {
            ct = shp;
          }
        });

        var pol = null;
        this.cuts.filter(shp => {
          if (shp.reference.indexOf(filedata[13]) != -1) {
            pol = shp;
          }
        });

        var symm = null;
        this.cuts.filter(shp => {
          if (shp.reference.indexOf(filedata[14]) != -1) {
            symm = shp;
          }
        });

        var shpp = null;
        this.shapes.filter(shp => {
          if (shp.shape_ref.indexOf(filedata[3]) != -1) {
            shpp = shp;
          }
        });

        var purity = null;
        this.clarities.filter(shp => {
          if (shp.clarityname == filedata[10]) {
            purity = shp;
          }
        });

        var di = new Diamond();
        di.weight = carat;
        di.color = col;
        di.shape = shpp;
        di.clarity = purity;

        await this.calculateRapPrice(di).then(response => {
          var rap = response;

          this.diamonds.push({
            isIndia:false,
            no: "",
            grossUsd: 0,
            expenseusd: 0,
            stockid: "",
            opendate: this.currentDate,
            buyingCompany: new CompanyRelation(),
            childCompany: new CompanyRelation(),
            partnerratio: 0,
            partnerAmt: 0,
            purpose: "",
            color: col,
            term: 0,
            dueDate: this.currentDate,
            clarity: purity,
            cut: ct,
            polish: pol,
            sym: symm,
            fluro: flr,
            lab: new Lab(),
            reportno: filedata[1],
            shape: shpp,
            supplier: new Ledger(),
            buyer: new Ledger(),
            weight: filedata[7],
            rapprice: rap,
            discount: 0,
            dis1: 0,
            dis2: 0,
            usdPerCt: 0,
            usdTotal: 0,
            currate: 0,
            inrPerCt: 0,
            inrTotal: 0,
            grossTotal: 0,
            purchase: this.purchase_head,
            expense: 0,
            billingParty: new Ledger(),

            currentLocation: "",
            buyForLocation: "",
            stoneStatus: "",
            certStatus: "",
            invoiceStatus: "",
            remark: "",

            // NEW FIELDS
            length: filedata[4],
            width: filedata[5],
            depth: filedata[6],
            colorDesc: filedata[9],
            depthPt: filedata[20],
            tablePt: filedata[21],
            crAg: filedata[22],
            crHt: filedata[23],
            pavAg: filedata[24],
            pavDp: filedata[25],
            starLn: filedata[26],
            lrHalf: filedata[27],
            girdle: filedata[17],
            girdleCondition: filedata[18],
            girdlePct: filedata[35],
            culetSize: filedata[19],
            keyToSymbol: filedata[31],
            reportDate: filedata[2],
            inscription: filedata[33],
            infoMessage: filedata[32],
            clarityStatus: filedata[11],
            flurocolor: filedata[16]
          });
        });
      }
    },

    getDiamondColor(color) {
      this.colors.filter(shp => {
        if (shp.colorname == color) {
          console.log(shp.colorname);
          return shp;
        }
      });
    },
    async getGiaResultreport(diamond) {
      var resultData;
      var reportno = diamond.reportno;
      var weight = diamond.weight;
      await GIAService.getAxiosreq(reportno, weight).then(data => {
        resultData = data.reportDtls.reportDtl[0];
        
        var shape = resultData.shape;
        
        var color = resultData.color;
        
        var clarity = resultData.clarity;
        var cut = resultData.finalCut;
        var polish = resultData.polish;
        var sym = resultData.symmetry;
        var fluro = resultData.fluorescenceIntensity;
        var flurocolor = resultData.fluorescenceColor;

        (diamond.length = resultData.length),
          (diamond.width = resultData.width),
          (diamond.depth = resultData.depth),
          (diamond.colorDesc = resultData.colorDesc),
          (diamond.depthPt = resultData.depthPct),
          (diamond.tablePt = resultData.tablePct),
          (diamond.crAg = resultData.crnAg),
          (diamond.crHt = resultData.crnHt),
          (diamond.pavAg = resultData.pavAg),
          (diamond.pavDp = resultData.pavDp),
          (diamond.starLn = resultData.starLn),
          (diamond.lrHalf = resultData.lrHalf),
          (diamond.girdle = resultData.girdle),
          (diamond.girdleCondition = resultData.girdleCondition),
          (diamond.girdlePct = resultData.girdlePct),
          (diamond.culetSize = resultData.culetSize),
          (diamond.keyToSymbol = resultData.keyToSymbols),
          (diamond.reportDate = resultData.reportDt),
          (diamond.inscription = resultData.inscription),
          (diamond.infoMessage = resultData.reportComments),
          (diamond.clarityStatus = resultData.clarity),
          (diamond.flurocolor = flurocolor);

        this.fluros.filter(shp => {
          if (shp.fluro_ref.indexOf(fluro) != -1) {
            diamond.fluro = shp;
          }
        });

        this.cuts.filter(shp => {
          if (shp.reference.indexOf(cut) != -1) {
            diamond.cut = shp;
          }
        });
        this.cuts.filter(shp => {
          if (shp.reference.indexOf(polish) != -1) {
            diamond.polish = shp;
          }
        });
        this.cuts.filter(shp => {
          if (shp.reference.indexOf(sym) != -1) {
            diamond.sym = shp;
          }
        });

        this.shapes.filter(shp => {
          if (shp.shape_ref.indexOf(shape) != -1) {
            console.log(shp.shapename);
            diamond.shape = shp;
          }
        });

        this.colors.filter(shp => {
          if (shp.colorname == color) {
            diamond.color = shp;
          }
        });

        this.clarities.filter(shp => {
          if (shp.clarityname == clarity) {
            diamond.clarity = shp;
          }
        });

        this.calculateRapPrice(diamond);
      });
    },

    getPurchases() {
      axios
        .get(this.API_URL + "purchase/getAllPurchase")
        .then(response => {
          this.purchaselists = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    selectPurchase(item) {
      const formData = new FormData();
      formData.append("id", item.id);
      axios
        .post(this.API_URL + "purchase/detailPurchase", formData)
        .then(response => {
          this.editpurchaselists = response.data;
          this.current_purchase_head = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    calculateRapPriceForCSVImpro() {
      var i;
      for (i = 0; i < this.diamonds.length; i++) {
        var diamond = this.diamonds[i];
        console.log(diamond);
      }
    },
    async calculateRapPrice(diamond) {
      var weight = diamond.weight;
      var shape = "",
        color = "",
        clarity = "";

      if (diamond.shape == null) {
        console.log("shape not found");
      } else {
        shape = diamond.shape.shapename;

        var n = diamond.shape.shape_ref
          .toLowerCase()
          .search("round".toLowerCase());

        if (n != -1) {
          shape = diamond.shape.shapename;
        }
      }
      // if (diamond.shape != null || typeof diamond.shape.shapename != "undefined") {
      //   console.log(diamond.shape)
      //   shape = diamond.shape.shapename;

      //   var n = diamond.shape.shape_ref
      //     .toLowerCase()
      //     .search("round".toLowerCase());

      //   if (n != -1) {
      //     shape = diamond.shape.shapename;
      //   }
      // }

      if (diamond.color !== null) {
        color = diamond.color.colorname;
      }
      if (diamond.clarity !== null) {
        clarity = diamond.clarity.clarityname;
      }

      if (shape.toUpperCase() == "ROUND") {
        shape = "ROUND";
      } else {
        shape = "PEAR";
      }
      console.log(shape);
      const formData = new FormData();
      formData.append("shape", shape);
      formData.append("weight", weight);
      formData.append("color", color);
      formData.append("clarity", clarity);
      // .then(({ data }) => (this.billingparties = data));
      return axios
        .post(this.API_URL + "rapprice/getRapPrice", formData)
        .then(response => {
          if (response.data == "") {
            diamond.rapprice = 0;
          } else {
            diamond.rapprice = response.data;
          }
          return diamond.rapprice;
        })
        .catch(error => {
          console.log(error);
          diamond.rapprice = 0.0;
          return diamond.rapprice;
        });
    },

    updatePurchase() {
      this.purchase_head.invoice_weight = this.editpurchaselists.invoice_weight;
      this.purchase_head.invoice_usdtotal = this.editpurchaselists.invoice_usdtotal;
      this.purchase_head.invoice_inrtotal = this.editpurchaselists.invoice_inrtotal;
      this.purchase_head.id = this.editpurchaselists.id;

      const formData = new FormData();
      formData.append("purchase", JSON.stringify(this.purchase_head));
      formData.append(
        "rowdiamonds",
        JSON.stringify(this.editpurchaselists.diamond)
      );
      console.log(this.purchase_head);
      console.log(JSON.stringify(this.editpurchaselists.diamond));

      console.log(formData);
      axios
        .post(this.API_URL + "purchase/updatePurchase", formData)
        .then(function(data) {
          if (data.data.error) {
            console.log(data.data.error.message);
          }
        });
    },
    savePurchase() {
      this.purchase_head.invoice_weight = this.invoice_weight;
      this.purchase_head.invoice_usdtotal = this.invoice_usdtotal;
      this.purchase_head.invoice_inrtotal = this.invoice_inrtotal;

      const formData = new FormData();
      formData.append("purchase", JSON.stringify(this.purchase_head));
      formData.append("rowdiamonds", JSON.stringify(this.diamonds));

      axios
        .post(this.API_URL + "purchase/savePurchaseStockOnly", formData)
        .then(() => {
          this.text = "SUCCESS ....";
          this.snackbar = true;
          this.diamonds = [];
          this.invoice_weight = 0;
          this.invoice_usdtotal = 0;
          this.invoice_inrtotal = 0;
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
    },
    calculateTotal() {
      var weighttotal;
      weighttotal = this.diamonds.reduce(function(sum, diamond) {
        var lineWeightTotal = parseFloat(diamond.weight);
        if (!isNaN(lineWeightTotal)) {
          return sum + lineWeightTotal;
        }
      }, 0);
      if (!isNaN(weighttotal)) {
        this.invoice_weight = weighttotal.toFixed(2);
      }

      var usdtotal;
      usdtotal = this.diamonds.reduce(function(sum, diamond) {
        var lineusdtotal = parseFloat(diamond.usdTotal);
        if (!isNaN(lineusdtotal)) {
          return sum + lineusdtotal;
        }
      }, 0);
      this.invoice_usdtotal = usdtotal.toFixed(2);

      var inrtotal;
      inrtotal = this.diamonds.reduce(function(sum, diamond) {
        var lineinrtotal = parseFloat(diamond.inrTotal);
        if (!isNaN(lineinrtotal)) {
          return sum + lineinrtotal;
        }
      }, 0);
      this.invoice_inrtotal = inrtotal.toFixed(2);
    },
    calculateLineTotal(diamond) {
      var expense = 0;
      var expenseusd = 0;
      if (!isNaN(diamond.expense)) {
        expense = parseFloat(diamond.expense);
      }
      if (!isNaN(diamond.expenseusd)) {
        expenseusd = parseFloat(diamond.expenseusd);
      }

      var usdPerCt =
        parseFloat(diamond.rapprice) +
        (parseFloat(diamond.rapprice) * parseFloat(diamond.discount)) / 100.0;
      usdPerCt = usdPerCt + (usdPerCt * parseFloat(diamond.dis1)) / 100.0;
      usdPerCt = usdPerCt + (usdPerCt * parseFloat(diamond.dis2)) / 100.0;

      if (!isNaN(usdPerCt)) {
        diamond.usdPerCt = usdPerCt.toFixed(2);
      }

      var usdTotal = parseFloat(diamond.weight) * parseFloat(diamond.usdPerCt);
      if (!isNaN(usdTotal)) {
        diamond.usdTotal = usdTotal.toFixed(2);
      }

      var inrPerCt = parseFloat(diamond.currate) * usdPerCt;
      var inrTotal = inrPerCt * parseFloat(diamond.weight);
      if (!isNaN(inrPerCt)) {
        diamond.inrPerCt = inrPerCt.toFixed(2);
      }
      if (!isNaN(inrTotal)) {
        diamond.inrTotal = inrTotal.toFixed(0);
      }
      // var partnerRation = 0;
      var partnershipAmt = 0;
      if (!isNaN(diamond.partnerratio)) {
        partnershipAmt = (diamond.partnerratio * inrTotal) / 100;
        diamond.partnerAmt = partnershipAmt.toFixed(0);
      }

      var gt = inrTotal + expense;
      if (!isNaN(gt)) {
        diamond.grossTotal = gt.toFixed(0);
      }

      var gtUsd = usdTotal + expenseusd;
      if (!isNaN(gtUsd)) {
        diamond.grossUsd = gtUsd.toFixed(2);
      }

      this.calculateTotal();
    },
    deleteRow(index, diamond) {
      var idx = this.diamonds.indexOf(diamond);
      console.log(idx, index);
      if (idx > -1) {
        this.diamonds.splice(idx, 1);
      }
      this.calculateTotal();
    },
    deleteRowFromUpdate(index, diamond) {
      var idx = this.editpurchaselists.diamond.indexOf(diamond);
      console.log(idx, index);
      if (idx > -1) {
        this.editpurchaselists.diamond.splice(idx, 1);
      }
      this.calculateTotal();
    },

    addNewRow() {
      this.diamonds.push({
        isIndia:false,
        no: "",
        grossUsd: 0,
        expenseusd: 0,
        stockid: "",
        opendate: this.currentDate,
        buyingCompany: new CompanyRelation(),
        childCompany: new CompanyRelation(),
        partnerratio: 0,
        purpose: "",
        color: new Color(),
        clarity: new Clarity(),
        cut: new Cut_Pol_Sym(),
        polish: new Cut_Pol_Sym(),
        sym: new Cut_Pol_Sym(),
        fluro: new Fluro(),
        flurocolor: "",
        lab: new Lab(),
        reportno: "",
        shape: new Shape(),
        supplier: new Ledger(),
        buyer: new Ledger(),
        weight: 0,
        rapprice: 0,
        discount: 0,
        dis1: 0,
        dis2: 0,
        usdPerCt: 0,
        usdTotal: 0,
        currate: 0,
        inrPerCt: 0,
        inrTotal: 0,
        grossTotal: 0,
        purchase: this.purchase_head,
        expense: 0,
        billingParty: new Ledger(),
        term: 0,
        dueDate: this.currentDate,
        currentLocation: "",
        buyForLocation: "",
        stoneStatus: "",
        certStatus: "",
        invoiceStatus: "",
        remark: "",
        // NEW FIELDS
        length: "",
        width: "",
        depth: "",
        colorDesc: "",
        depthPt: "",
        tablePt: "",
        crAg: "",
        crHt: "",
        pavAg: "",
        pavDp: "",
        starLn: "",
        lrHalf: "",
        girdle: "",
        girdleCondition: "",
        girdlePct: "",
        culetSize: "",
        keyToSymbol: "",
        reportDate: "",
        inscription: "",
        infoMessage: "",
        clarityStatus: ""
      });
    },
    getAllShapes() {
      axios
        .get(this.API_URL + "shape/getAllShapes")
        .then(({ data }) => (this.shapes = data));
    },
    getBillingParties() {
      axios
        .get(this.API_URL + "ledger/getBillingParties")
        .then(({ data }) => (this.billingparties = data));
    },
    async getAllSupplier(diamond) {
      if(diamond.buyingCompany.mainCompany.country.countryname == "INDIA"){
        diamond.isIndia = true;
        console.log(diamond.buyingCompany.mainCompany.country.countryname);
      }else{
        diamond.isIndia = false;
      }
      console.log(diamond.buyingCompany);
      return await axios
        .post(this.API_URL + "ledger/getSupplierByCompany", diamond.buyingCompany)
        .then(data => {
          diamond.supplierLine = data.data;
        });
    },
    getAllCompanies() {
      axios
        .get(this.API_URL + "companyrelation/getAllCompany")
        .then(({ data }) => (this.companies = data));
    },
    getAllColors() {
      axios
        .get(this.API_URL + "color/getAllColors")
        .then(({ data }) => (this.colors = data));
    },
    getAllClarity() {
      axios
        .get(this.API_URL + "clarity/getAllClarity")
        .then(({ data }) => (this.clarities = data));
    },
    getAllCuts() {
      axios
        .get(this.API_URL + "cps/getAllcps")
        .then(({ data }) => (this.cuts = data));
    },
    getAllFluro() {
      axios
        .get(this.API_URL + "fluro/getAllFluro")
        .then(({ data }) => (this.fluros = data));
    },
    getAllLabs() {
      axios
        .get(this.API_URL + "lab/getAllLabs")
        .then(({ data }) => (this.labs = data));
    },
    getAllCountries() {
      axios
        .get(this.API_URL + "country/getAllCountry")
        .then(({ data }) => (this.countries = data));
    },
    setBuyingCompany(diamond) {
      diamond.currentLocation = diamond.buyingCompany.mainCompany.country;
    }
  }
};
</script>

