<template>
  <!-- basic temple -->
  <div id="basic">
    <v-container>
      <v-toolbar dense elevation="3">
        <v-toolbar-title elevation="3">USER CREATION</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon>
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </v-toolbar>

      <v-spacer></v-spacer>

      <!-- FORM FOR SAVING DATA     -->
      <div v-if="!isEditing">
        <b-form @submit.prevent="onSubmit" @reset="onReset">
          <div class="row">
            <b-form-group
              class="column"
              id="input-group-2"
              label="Email address:"
              label-for="email"
            >
              <b-form-input
                id="email"
                v-model="user.emailAddress"
                type="email"
                required
                placeholder="Enter email"
              ></b-form-input>
            </b-form-group>

            <b-form-group class="column" id="input-group-2" label="User name:" label-for="username">
              <b-form-input id="username" v-model="user.username" required placeholder="Enter name"></b-form-input>
            </b-form-group>
          </div>
          <div class="row">
            <b-form-group
              class="column"
              id="input-group-2"
              label="First name:"
              label-for="firstname"
            >
              <b-form-input
                id="firstname"
                v-model="user.firstname"
                required
                placeholder="First name"
              ></b-form-input>
            </b-form-group>
            <b-form-group class="column" id="input-group-2" label="Last name:" label-for="lastname">
              <b-form-input id="lastname" v-model="user.lastname" required placeholder="Last name"></b-form-input>
            </b-form-group>
          </div>
          <div class="row">
            <b-form-group class="column" id="input-group-2" label="Position:" label-for="position">
              <b-form-input id="position" v-model="user.position" required placeholder="Position"></b-form-input>
            </b-form-group>
            <b-form-group class="column" id="input-group-2" label="Location:" label-for="location">
              <b-form-input id="location" v-model="user.location" required placeholder="Location"></b-form-input>
            </b-form-group>
          </div>
          <div class="row">
            <b-form-group class="column" id="input-group-2" label="Mobile:" label-for="mobile">
              <b-form-input
                type="number"
                id="mobile"
                v-model="user.mobileno"
                required
                placeholder="Mobile Number"
              ></b-form-input>
            </b-form-group>
            <b-form-group class="column" id="input-group-2" label="Remark:" label-for="remark">
              <b-form-input id="remark" v-model="user.remark" required placeholder="Remark"></b-form-input>
            </b-form-group>
          </div>

          <b-form-group id="input-group-2" label="Password:" label-for="password">
            <b-form-input
              id="password"
              v-model="user.password"
              type="password"
              required
              placeholder="Enter Password"
            ></b-form-input>
          </b-form-group>

          <label for="myfile">Select a file:</label>
          <input type="file" @change="onFileSelected" id="myfile" name="myfile" />
          <br />
          <br />

          <!-- <input v-model="files" type="submit"> -->

          <b-button type="submit" variant="primary">Submit</b-button>
           <v-divider class="mx-4" vertical></v-divider>
          <b-button type="reset" @click="onReset" variant="danger">Reset</b-button>
        </b-form>
      </div>

      <!-- FORM FOR EDITING DATA     -->
      <div v-if="isEditing">
        <b-form @submit.prevent="updateUser" @reset="onReset">
          <div class="row">
            <b-form-group
              class="column"
              id="input-group-1"
              label="Email address:"
              label-for="email"
            >
              <b-form-input
                id="email"
                v-model="currentUser.emailAddress"
                type="email"
                required
                placeholder="Enter email"
              ></b-form-input>
            </b-form-group>

            <b-form-group class="column" id="input-group-2" label="User name:" label-for="username">
              <b-form-input
                id="username"
                v-model="currentUser.username"
                required
                placeholder="Enter name"
              ></b-form-input>
            </b-form-group>
          </div>
          <div class="row">
            <b-form-group class="column" label="First name:" label-for="firstname">
              <b-form-input
                id="firstname"
                v-model="currentUser.firstname"
                required
                placeholder="First name"
              ></b-form-input>
            </b-form-group>
            <b-form-group class="column" label="Last name:" label-for="lastname">
              <b-form-input
                id="lastname"
                v-model="currentUser.lastname"
                required
                placeholder="Last name"
              ></b-form-input>
            </b-form-group>
          </div>
          <div class="row">
            <b-form-group class="column" id="input-group-2" label="Position:" label-for="position">
              <b-form-input
                id="position"
                v-model="currentUser.position"
                required
                placeholder="Position"
              ></b-form-input>
            </b-form-group>
            <b-form-group class="column" id="input-group-2" label="Location:" label-for="location">
              <b-form-input
                id="location"
                v-model="currentUser.location"
                required
                placeholder="Location"
              ></b-form-input>
            </b-form-group>
          </div>
          <div class="row">
            <b-form-group class="column" id="input-group-2" label="Mobile:" label-for="mobile">
              <b-form-input
                type="number"
                id="mobile"
                v-model="currentUser.mobileno"
                required
                placeholder="Mobile Number"
              ></b-form-input>
            </b-form-group>
            <b-form-group class="column" id="input-group-2" label="Remark:" label-for="remark">
              <b-form-input id="remark" v-model="currentUser.remark" required placeholder="Remark"></b-form-input>
            </b-form-group>
          </div>

          <b-form-group id="input-group-2" label="Password:" label-for="password">
            <b-form-input
              id="password"
              v-model="currentUser.password"
              type="password"
              required
              placeholder="Enter Password"
            ></b-form-input>
          </b-form-group>

          <b-button type="submit" variant="primary">Update</b-button>
          <v-divider class="mx-4" vertical></v-divider>
          <b-button type="reset" variant="danger">Reset</b-button>
        </b-form>
      </div>
      <!-- TABLE START HERE -->

      <div class="table-responsive" style="overflow-x:auto;">
        <table class="table table-condensed">
          <thead>
            <tr>
              <th>First Name</th>
              <th>Last Name</th>
              <th>User Name</th>
              <th>Email</th>
              <th>Position</th>
              <th>Location</th>
              <th>Mobile</th>
              <th>Remark</th>
              <th>Edit</th>
              <th>Delete</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in users" v-bind:key="item.id">
              <td>{{item.firstname}}</td>
              <td>{{item.lastname}}</td>
              <td>{{item.username}}</td>
              <td>{{item.emailAddress}}</td>
              <td>{{item.position}}</td>
              <td>{{item.location}}</td>
              <td>{{item.mobileno}}</td>
              <td>{{item.remark}}</td>
              <td>
                <a href="#" class="text-success" @click="isEditing=true; selectUser(item)">
                  <font-awesome-icon icon="edit" />
                </a>
              </td>
              <td>
                <a class="text-danger" @click="dialog=true ; selectUser(item)">
                  <font-awesome-icon icon="trash-alt" />
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <v-dialog v-model="dialog" persistent max-width="400">
        <v-card>
          <v-card-title class="headline">Are you sure want to delete ?</v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" text @click="dialog = false">No</v-btn>
            <v-btn color="green darken-1" text @click="dialog = false; deleteUser(currentUser)">YES</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </div>
</template>

<script>
import UserService from "../services/user.service";
import User from "../models/User";
import axios from "axios";
import commonVari from "../models/commonVari.js";
// const API_URL = "http://localhost:8081/";

export default {
  data() {
    return {
      API_URL: commonVari.API_URL,
      dialog: false,
      files: [],
      // user: new User("", "", "", "", "", "", "", "", ""),
      user: new User("", "", "", "", "", "", "", "", ""),
      users: [],
      isEditing: false,
      currentUser: new User("", "", "", "", "", "", "", "", ""),
      aadharFileLocation: "",
      passportFileLoaction: "",
      resumeFileLocation: "",
      file: null,
      filename: "",
      passfilename: "",
      resumefilename: ""
    };
  },
  created() {
    this.getAllUser();
  },
  methods: {
    onFileSelected(event) {
      this.file = event.target.files[0];
    },
    getAllUser() {
      axios
        .get(this.API_URL + "user/getAllUser")
        .then(({ data }) => (this.users = data));
    },
    selectUser(user) {
      this.currentUser = user;
    },

    onSubmit() {
      // this.file = this.files[0];
      if (this.file != null) {
        this.filename = this.file.name;
      }
      if (this.file != null) {
        this.passfilename = this.file.name;
      }
      if (this.file != null) {
        this.resumefilename = this.file.name;
      }

      const formData = new FormData();
      if (this.file != null) {
        formData.append("file", this.file);
        formData.append("passportfile", this.file);
        formData.append("resumefile", this.file);
      } else {
        formData.append("file", null);
        formData.append("passportfile", null);
        formData.append("resumefile", null);
      }

      formData.append("user", JSON.stringify(this.user));

      axios
        .post(this.API_URL + "user/add", formData)
        .then(data => {
          console.log(data);
          this.getAllUser();
          this.user = new User();
        })
        .catch(error => {
          console.log(error);
        });
    },
    uploadFile() {
      const formData = new FormData();
      formData.append("file", this.file, this.file.name);
      formData.append("passportfile", this.file, this.file.name);
      formData.append("resumefile", this.file, this.file.name);
      axios.post(this.API_URL + "user/upload", formData).then(data => {
        console.log(data);
      });
    },
    updateUser() {
      this.currentUser.aadharFileLocation = this.aadharFileLocation;
      this.currentUser.resumeFileLocation = this.resumeFileLocation;
      this.currentUser.passportFileLoaction = this.passportFileLoaction;
      console.log(this.currentUser);

      axios.post(this.API_URL + "user/update", this.currentUser).then(data => {
        console.log(data);
        this.isEditing = false;
        this.getAllUser();
      });
    },
    deleteUser(user) {
      console.log(user);
      this.currentUser = user;
      console.log(this.currentUser);

      axios.post(this.API_URL + "delete", this.currentUser).then(data => {
        console.log(data);
        this.isEditing = false;
        this.getAllUser();
      });
    },

    loadData() {
      UserService.findAllUser().then(data => {
        self.users = data.data;
      });
    },
    onReset() {
      // Reset our User values
      this.isEditing = false;
      this.user = new User();
      this.getAllUser();
    }
  }
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}
#input-group-2 {
  padding: 1px;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  /* padding: 5px; */
  content: "";
  display: table;
  clear: both;
}

b-form-input {
  text-transform: capitalize;
}
.table-responsive {
  margin-top: 20px;
}

b-button {
  margin: 5px;
}
</style>