<template>
  <div>
    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">{{this.$route.params.company}} | SALE REPORTS</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-container>
    <v-container>
      <v-row>
        <v-col cols="12" md="2">
          <v-autocomplete
            required
            v-model="selectedCompany"
            :items="companies"
            color="blue"
            item-text="name"
            solo
            dense
            outlined
            hide-details
            opeque
            return-object
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="2">
          <v-btn outlined color="indigo" @click="getPurchase()">Retrive Report</v-btn>
        </v-col>
      </v-row>
    </v-container>

    <v-divider></v-divider>
    <v-card-title>
      <v-text-field
      outlined
      dense
        v-model="search"
        label="Search"
        append-icon="mdi-magnify"
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="purchaselists"
      :items-per-page="10"
      dense
      calculate-widths
      mobile-breakpoint
      :search="search"
      class="elevation-1"
    >
      <template v-slot:item="row">
        <tr v-bind:key="row.item.id">
          <!-- <tr v-for="item in purchaselists" v-bind:key="item.id"> -->
          <td>{{row.item.transdate}}</td>
          <td>{{row.item.term}}</td>
          <td>{{row.item.dueDate}}</td>
          <td>{{row.item.buyer[0].name}}</td>
          <td>{{row.item.diamondTracking.buyForLocation.countryname}}</td>
          <td>{{row.item.diamondTracking.diamond.stockid}}</td>
          <td>{{row.item.diamondTracking.shape.shapename}}</td>
          <td>{{row.item.diamondTracking.originalweight}}</td>
          <td>{{row.item.diamondTracking.color.colorname}}</td>
          <td>{{row.item.diamondTracking.clarity.clarityname}}</td>
          <td>{{row.item.diamondTracking.cut.name}}</td>
          <td>{{row.item.diamondTracking.polish.name}}</td>
          <td>{{row.item.diamondTracking.sym.name}}</td>
          <td>{{row.item.diamondTracking.fluro.fluroname}}</td>
          <td>{{row.item.diamondTracking.flurocolor}}</td>
          <td>{{row.item.diamondTracking.lab.labname}}</td>
          <td>{{row.item.diamondTracking.reportno}}</td>
          <td>{{row.item.rapprice}}</td>
          <td>{{row.item.discount}}</td>
          <td>{{row.item.dis1}}</td>
          <td>{{row.item.dis2}}</td>
          <td>{{row.item.usdPerCt}}</td>
          <td>{{row.item.usdTotal}}</td>
          <td>{{row.item.curRate}}</td>
          <td>{{row.item.inrPerCt}}</td>
          <td>{{row.item.inrtoal}}</td>
          <td>{{row.item.diamondTracking.remark}}</td>
          <td>{{row.item.profit}}</td>
          <td>{{row.item.profitPt}}</td>
        </tr>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import CompanyRelation from "../models/CompanyRelation";
import commonVari from "../models/commonVari.js";
import axios from "axios";
export default {
  
  data() {
    return {
      search: '',
      API_URL: commonVari.API_URL,
      purchaselists: [],
      companies: [],
      selectedCompany: new CompanyRelation(),
      headers: [
        
        { text: "DATE", value: "transdate" },
        { text: "TERM", value: "term" },
         { text: "DUE DATE", value: "dueDate" },
        { text: "CUSTOMER NAME", value: "diamondTracking.purpose" },
        {
          text: "TO LOCATION",
          value: "diamondTracking.buyForLocation.countryname"
        },
        { text: "STOCK #", value: "diamondTracking.diamond.stockid" },
        { text: "SHAPE", value: "diamondTracking.shape.shapename" },
        { text: "WEIGHT", value: "diamondTracking.originalweight" },
        { text: "COLOR", value: "diamondTracking.color.colorname" },
        { text: "CLARITY", value: "diamondTracking.clarity.clarityname" },
        { text: "CUT", value: "diamondTracking.cut.name" },
        { text: "POLISH", value: "diamondTracking.polish.name" },
        { text: "SYM", value: "diamondTracking.sym.name" },
        { text: "FLURO", value: "diamondTracking.fluro.fluroname" },
        { text: "FLURO COLOR", value: "diamondTracking.flurocolor" },
        { text: "LAB", value: "diamondTracking.lab.labname" },
        { text: "REPORT #", value: "item.diamondTracking.reportno" },
        { text: "RAP", value: "rapprice" },
        { text: "DISCOUNT", value: "discount" },
        { text: "DIS 1", value: "dis1" },
        { text: "DIS 2", value: "dis2" },
        { text: "$/CT", value: "usdPerCt" },
        { text: "TOTAL $", value: "usdTotal" },
        { text: "CUR RATE", value: "curRate" },
        { text: "RS / CT", value: "inrPerCt" },
        { text: "TOTAL RS", value: "inrtoal" },
        { text: "REMARK", value: "diamondTracking.remark" },
        { text: "PROFIT", value: "profit" },
        { text: "PROFIT %", value: "profitPt" }
      ]
    };
  },
  created() {
    console.log("created")
    this.company = this.$route.params.company;
    this.getAllCompanies(this.company);
  },

  methods: {
    getAllCompanies(company) {
      const formData = new FormData();
      formData.append("company", company);
      axios
        .post(
          this.API_URL + "companyrelation/getCompanyByMainCompany",
          formData
        )
        .then(({ data }) => (this.companies = data));
    },
    getPurchase() {
      axios
        .post(this.API_URL + "saleline/getSaleByCompany", this.selectedCompany)
        .then(({ data }) => (this.purchaselists = data));
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(26, 172, 216);
  color: white;
}
.container {
  max-width: 1185px;
  min-width: -webkit-fill-available;
}
</style>