<template>
  <div>
    <v-snackbar v-model="snackbar" :color="colorSnack" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-toolbar class="toolbase" elevation="3" dense>
      <v-toolbar-title elevation="3">DIAMOND PRICE UPDATE</v-toolbar-title>
    </v-toolbar>
    <v-radio-group v-model="options" row style="height:30px">
      <v-radio label="BY STOCK #" value="byStockId"></v-radio>
      <v-radio label="BY STOCK NO." value="byStockNo"></v-radio>
      <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
    </v-radio-group>
    <v-row>
      <v-col cols="12" sm="6" md="4">
        <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
      </v-col>
      <v-col cols="12" sm="6" md="4">
        <v-btn outlined color="indigo" @click="loadDataFromMasters">LOAD DATA</v-btn>
      </v-col>
      <v-col cols="12" sm="12" md="3">
        <button type="button" class="btn btn-info" @click="addNewRow">Add New Row</button>
      </v-col>
    </v-row>
    <v-simple-table dense>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left"></th>
            <th class="text-left">NO</th>
            <th class="text-left">ID</th>
            <th class="text-left">STOCK NO</th>
            <th class="text-left">STOCK ID</th>
            <th class="text-left">WEIGHT</th>
            <th class="text-left">SHAPE</th>
            <th class="text-left">COLOR</th>
            <th class="text-left">CLARITY</th>
            <th class="text-left">CUT</th>
            <th class="text-left">POLISH</th>
            <th class="text-left">SYM</th>
            <th class="text-left">FLURO</th>
            <th class="text-left">FLURO COLOR</th>
            <th class="text-left">LAB</th>
            <th class="text-left">REPORT NO</th>
            <th class="text-left">MEMBER COMMENT</th>
            <th class="text-left">SALE DIS</th>
            <th class="text-left">RAP PRICE</th>
            <th class="text-left">SALE $/CT</th>
            <th class="text-left">TOTAL $</th>
            <th class="text-left">REMARK</th>
            <th class="text-left">LENGTH</th>
            <th class="text-left">WIDTH</th>
            <th class="text-left">DEPTH</th>
            <th class="text-left">DEPTH %</th>
            <th class="text-left">TABLE %</th>
            <th class="text-left">KEY TO SYMBOL</th>
            <th class="text-left">INFO MESSAGE</th>
            <th class="text-left">TYPE 2A</th>
            <th class="text-left">SHADE</th>
            <th class="text-left">SEAL</th>
           

          </tr>
        </thead>
        <tbody>
          <tr v-for="(diamond, k) in diamonds" :key="k">
            <td>
              <a class="text-success" @click="addNewRow">
                <font-awesome-icon icon="plus-square" />
              </a>
            </td>
            <td>{{diamonds.indexOf(diamond)+1}}</td>
            <td>{{diamond.du.id}}</td>
            <td class="tabledata">
              <input
                readonly
                class="form-control"
                style="width:100px"
                type="text"
                v-model="diamond.du.idForStock"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.stockid"
              />
            </td>
            <td class="tabledata">
              <input
                style="width:80px"
                class="form-control text-right"
                type="number"
                min="0"
                step=".01"
                v-model="diamond.du.currentweight"
              />
            </td>
            <td class="tabledata">
              <v-autocomplete
                style="width:150px"
                required
                v-model="diamond.du.shape"
                :items="shapes"
                color="blue"
                item-text="shapename"
                dense
                hide-details
                outlined
                return-object
              ></v-autocomplete>
            </td>
            <td class="tabledata">
              <v-autocomplete
                style="width:150px"
                required
                v-model="diamond.du.color"
                :items="colors"
                color="blue"
                item-text="colorname"
                dense
                hide-details
                outlined
                return-object
              ></v-autocomplete>
            </td>
            <td class="tabledata">
              <v-autocomplete
                style="width:150px"
                required
                v-model="diamond.du.clarity"
                :items="clarities"
                color="blue"
                item-text="clarityname"
                dense
                hide-details
                outlined
                return-object
              ></v-autocomplete>
            </td>
            <td class="tabledata">
              <v-autocomplete
                style="width:150px"
                required
                v-model="diamond.du.cut"
                :items="cuts"
                color="blue"
                item-text="name"
                dense
                hide-details
                outlined
                return-object
              ></v-autocomplete>
            </td>
            <td class="tabledata">
              <v-autocomplete
                style="width:150px"
                required
                v-model="diamond.du.polish"
                :items="cuts"
                color="blue"
                item-text="name"
                dense
                hide-details
                outlined
                return-object
              ></v-autocomplete>
            </td>
            <td class="tabledata">
              <v-autocomplete
                style="width:150px"
                required
                v-model="diamond.du.sym"
                :items="cuts"
                color="blue"
                item-text="name"
                dense
                hide-details
                outlined
                return-object
              ></v-autocomplete>
            </td>
            <td class="tabledata">
              <v-autocomplete
                style="width:150px"
                required
                v-model="diamond.du.fluro"
                :items="fluros"
                color="blue"
                item-text="fluroname"
                dense
                hide-details
                outlined
                return-object
              ></v-autocomplete>
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.flurocolor"
              />
            </td>

            <td class="tabledata">
              <v-autocomplete
                style="width:150px"
                required
                v-model="diamond.du.lab"
                :items="labs"
                color="blue"
                item-text="labname"
                dense
                hide-details
                outlined
                return-object
              ></v-autocomplete>
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.reportno"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.memberComment"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="number"
                v-model="diamond.du.saleDis"
                @change="calculateValueFromDiscount(diamond.du)"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="number"
                v-model="diamond.du.rapprice"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="number"
                v-model="diamond.du.salePerCarat"
                @change="calculateValueFromPerCarat(diamond.du)"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="number"
                v-model="diamond.du.totalUsd"
                @change="ReverseCalculation(diamond.du)"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.remark"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.length"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.width"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.depth"
              />
            </td>
            
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.depthPt"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.tablePt"
              />
            </td>
            
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.keyToSymbol"
              />
            </td>
            
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.infoMessage"
              />
            </td>
            
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.type2a"
              />
            </td>
            <td class="tabledata">
              <input
                class="form-control"
                style="width:150px"
                type="text"
                v-model="diamond.du.shade"
              />
            </td>
            <td class="tabledata"><input class="form-control" style="width:150px" type="text" v-model="diamond.du.seal"/></td>



            <td scope="row" class="trashIconContainer">
              <a class="text-danger" @click="deleteRow(k, diamond)">
                <font-awesome-icon icon="trash-alt" />
              </a>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
    <v-btn @click="updteData">UPDATE DATA</v-btn>
  </div>
</template>

<script>
import axios from "axios";
import commonVari from "../models/commonVari.js";
import DiamondUpdate from "../models/DiamondUpdate.js";
export default {
  data() {
    return {
      requiredParams: "",
      options: null,
      colorSnack: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      API_URL: commonVari.API_URL,
      shapes: [],
      colors: [],
      clarities: [],
      cuts: [],
      fluros: [],
      labs: [],
      diamonds: [
        {
          du: new DiamondUpdate()
        }
      ]
    };
  },
  created() {
    this.getAllShapes();
    this.getAllColors();
    this.getAllClarity();
    this.getAllCuts();
    this.getAllFluro();
    this.getAllLabs();
  },
  methods: {
     calculateValueFromPerCarat(diamond) {
      var PerCt = parseFloat(diamond.salePerCarat); 
      var rap = parseFloat(diamond.rapprice);
      var weight = parseFloat(diamond.currentweight);
      
      var total = PerCt * weight;
      diamond.totalUsd = total.toFixed(2);

      var dis = ((PerCt - rap) * 100) / rap;
      diamond.saleDis = dis.toFixed(2);
    },
    ReverseCalculation(diamond) {
      var total = parseFloat(diamond.totalUsd);
      var rap = parseFloat(diamond.rapprice);
      var weight = parseFloat(diamond.currentweight);
      var PerCt = total / weight;
      diamond.salePerCarat = PerCt.toFixed(2);

      var dis = ((PerCt - rap) * 100) / rap;
      diamond.saleDis = dis.toFixed(2);
    },
    calculateValueFromDiscount(diamond) {
      var rap = parseFloat(diamond.rapprice);
      var dis = parseFloat(diamond.saleDis);

      var PerCt = rap + (rap * dis) / 100;
      diamond.salePerCarat = PerCt;

      var total = parseFloat(diamond.currentweight) * PerCt;
      diamond.totalUsd = total;
    },
    updteData() {
      var diamondsToUpdate = [];
      for (var i = 0; i < this.diamonds.length; i++) {
        if (this.diamonds[i].du.id != null) {
          diamondsToUpdate.push(this.diamonds[i].du);
        }
      }
      console.log(diamondsToUpdate);
      const formData = new FormData();
      formData.append("diamondString", JSON.stringify(diamondsToUpdate));

      axios
        .post(this.API_URL + "diamond/updatePrice", formData)
        .then(() => {
          this.text = "SUCCESS";
          this.snackbar = true;
          this.diamonds = [];
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
    },
    deleteRow(index, diamond) {
      var idx = this.diamonds.indexOf(diamond);
      console.log(idx, index);
      if (idx > -1) {
        this.diamonds.splice(idx, 1);
      }
    },
    loadDataFromMasters() {
      if (this.options != null) {
        var option = this.options;
        var reqQuery = "";
        const formData = new FormData();
        if (option == "byStockId") {
          reqQuery = "diamond/getAllByStockId";
          formData.append("stockId", this.requiredParams);
        } else if (option == "byReportNo") {
          reqQuery = "diamond/getAllByReportNo";
          formData.append("reportno", this.requiredParams);
        } else if (option == "byStockNo") {
          reqQuery = "diamond/getAllByStockNo";
          formData.append("stockNo", this.requiredParams);
        }

        axios
          .post(this.API_URL + reqQuery, formData)
          .then(response => {
            console.log(response.data);
            this.addRowQuery(response.data);
          })
          .catch(error => {
            console.log(error);
          });
      } else {
        alert("Please specify valid input");
      }
    },
    async addRowQuery(results) {
      for (var j = 0; j < results.length; j++) {
        var filedata = results[j];

        console.log(filedata);

        var du = new DiamondUpdate();
        du.id = filedata.id;
        du.idForStock = filedata.idForStock;
        du.stockid = filedata.stockid;
        du.currentweight = filedata.currentweight;
        du.shape = filedata.shape;
        du.color = filedata.color;
        du.clarity = filedata.clarity;
        du.cut = filedata.cut;
        du.polish = filedata.polish;
        du.sym = filedata.sym;
        du.fluro = filedata.fluro;
        du.flurocolor = filedata.fluroColor;
        du.lab = filedata.lab;
        du.reportno = filedata.reportno;
        du.saleDis = filedata.saleDis;
        du.salePerCarat = filedata.salePerCarat;
        du.remark = filedata.remark;
        du.length = filedata.length;
        du.width = filedata.width;
        du.depth = filedata.depth;
        du.colorDesc = filedata.colorDesc;
        du.depthPt = filedata.depthPt;
        du.tablePt = filedata.tablePt;
        du.crAg = filedata.crAg;
        du.crHt = filedata.crHt;
        du.pavAg = filedata.pavAg;
        du.pavDp = filedata.pavDp;
        du.starLn = filedata.starLn;
        du.lrHalf = filedata.lrHalf;
        du.girdle = filedata.girdle;
        du.girdleCondition = filedata.girdleCondition;
        du.girdlePct = filedata.girdlePct;
        du.culetSize = filedata.culetSize;
        du.keyToSymbol = filedata.keyToSymbol;
        du.inscription = filedata.inscription;
        du.infoMessage = filedata.infoMessage;
        du.clarityStatus = filedata.clarityStatus;
        du.laserinscription = filedata.laserinscription;
        du.type2a = filedata.type2a;
        du.image = filedata.image;
        du.plotting = filedata.plotting;
        du.viedo = filedata.viedo;
        du.sarin = filedata.sarin;
        du.rapspec = filedata.rapspec;
        du.recutremark = filedata.recutremark;
        du.bgm = filedata.bgm;
        du.shade = filedata.shade;
        du.eyeClean = filedata.eyeClean;
        du.tableInclusion = filedata.tableInclusion;
        du.tableBlack = filedata.tableBlack;
        du.sideBlack = filedata.sideBlack;
        du.incPattern = filedata.incPattern;
        du.intGraining = filedata.intGraining;
        du.tableOpen = filedata.tableOpen;
        du.crownOpen = filedata.crownOpen;
        du.pavOpen = filedata.pavOpen;
        du.reflection = filedata.reflection;
        du.heartAndArrow = filedata.heartAndArrow;
        du.spreadRatio = filedata.spreadRatio;
        du.dnaColor = filedata.dnaColor;
        du.dnaClarity = filedata.dnaClarity;
        du.rapprice = filedata.rapprice;
        du.memberComment = filedata.memberComment;
        du.stockListAvail = filedata.stockListAvail;
        du.rapnetAvail = filedata.rapnetAvail;
        du.stateName = filedata.stateName;
        du.cityName = filedata.cityName;
        du.fancyColor = filedata.fancyColor;
        du.fancyColorIntensity = filedata.fancyColorIntensity;
        du.fancyColorOvertone = filedata.fancyColorOvertone;
        du.matchPair = filedata.matchPair;
        du.pairStock = filedata.pairStock;
        du.allowRapLinkFeed = filedata.allowRapLinkFeed;
        du.parcelStone = filedata.parcelStone;
        du.reportFileName = filedata.reportFileName;
        du.tradeShow = filedata.tradeShow;
        du.reportType = filedata.reportType;
        du.labLocation = filedata.labLocation;
        du.brand = filedata.brand;
        du.sellerSpec = filedata.sellerSpec;
        du.treatment = filedata.treatment;
        du.painting = filedata.painting;
        du.proportion = filedata.proportion;
        du.paintComment = filedata.paintComment;
        du.syntheticIndicator = filedata.syntheticIndicator;
        du.roughOrigin = filedata.roughOrigin;
        du.laserGiaReportNo = filedata.laserGiaReportNo;
        du.additionalLaser = filedata.additionalLaser;
        du.seal = filedata.seal;

        var totalusd = (du.rapprice + (du.rapprice * du.saleDis) / 100) * du.currentweight;
        du.totalUsd = totalusd;

        this.diamonds.push({
          du
        });
      }
    },
    addNewRow() {
      this.diamonds.push({
        du: new DiamondUpdate()
      });
    },
    getAllShapes() {
      axios
        .get(this.API_URL + "shape/getAllShapes")
        .then(({ data }) => (this.shapes = data));
    },
    getAllColors() {
      axios
        .get(this.API_URL + "color/getAllColors")
        .then(({ data }) => (this.colors = data));
    },
    getAllClarity() {
      axios
        .get(this.API_URL + "clarity/getAllClarity")
        .then(({ data }) => (this.clarities = data));
    },
    getAllCuts() {
      axios
        .get(this.API_URL + "cps/getAllcps")
        .then(({ data }) => (this.cuts = data));
    },
    getAllFluro() {
      axios
        .get(this.API_URL + "fluro/getAllFluro")
        .then(({ data }) => (this.fluros = data));
    },
    getAllLabs() {
      axios
        .get(this.API_URL + "lab/getAllLabs")
        .then(({ data }) => (this.labs = data));
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(136, 235, 235);
}
.tabledata {
  border-color: brown;
  padding: 1px;
  outline: black;
  max-height: 30px;
  /* font-weight: bold; */
}
</style>