<template>
  <div class="container">
    <div class="row">
<!-- TABLE DATA         -->
      <div class="column" style="width:50%">
        <div class="table-responsive" style="overflow-x:auto;">
          <table class="table table-condensed">
            <thead>
              <tr>
                <th>Lab</th>
                
                <th>Edit</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in labs" v-bind:key="item.id">
                <td>{{item.labname}}</td>
                
                <td><a class="text-success" variant="primary" @click="isEditing=true; selectLab(item)"><font-awesome-icon icon="edit"/></a></td>
                    <td><a class="text-danger" variant="primary" @click="deleteLab(item)"><font-awesome-icon icon="trash-alt"/></a>
                  <!-- <td><b-button variant="primary" @click="isEditing=true; selectLab(item)">Edit</b-button></td>
                    <td><b-button variant="primary" @click="deleteLab(item)">Delete</b-button> -->
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <v-divider vertical></v-divider>
<!-- FOR SAVING DATA -->
      <b-form class="column" style="width:40%"  @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="Lab Name :" label-for="labname">
          <b-form-input
            id="labname"
            v-model="lab.labname"
            required
            placeholder="Enter Lab name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Lab Display Order :" label-for="Labdo">
          <b-form-input
            type="number"
            id="Labdo"
            v-model="lab.orderId"
            required
            placeholder="Enter Lab Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
<!-- FOR EDITING DATA -->

      <b-form class="column" style="width:40%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="Lab Name :" label-for="Labname">
          <b-form-input
            id="Labname"
            v-model="currentLab.labname"
            required
            placeholder="Enter Lab name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Lab Display Order :" label-for="Labdo">
          <b-form-input
            type="number"
            id="Labdo"
            v-model="currentLab.orderId"
            required
            placeholder="Enter Lab Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
import Lab from "../models/Lab";
import axios from "axios";
import commonVari from "../models/commonVari.js";

export default {
  name: "Lab",
  data() {
    return {
      API_URL: commonVari.API_URL,
      lab: new Lab(),
      isEditing: false,
      labs: [], 
      currentLab: new Lab(),
    };
  },
  created() {
    this.getAllLabs();
  },
  methods: {
      selectLab(Lab){
          this.currentLab = Lab;
      },
    getAllLabs() {
      axios
        .get(this.API_URL + "lab/getAllLabs")
        .then(({ data }) => (this.labs = data));
    },
    saveData() {
      axios.post(this.API_URL + "lab/saveLab", this.lab).then(data => {
        console.log(data);
        this.lab.Labname = "";
        this.lab.Lab_ref = "";
        this.lab.orderId = "";
        this.getAllLabs();
      });
    },
    updateData() {
      axios.post(this.API_URL + "lab/updateLab", this.currentLab).then(data => {
        console.log(data);
        this.isEditing = false;
        this.Lab.Labname = "";
        this.Lab.Lab_ref = "";
        this.Lab.orderId = "";
        this.getAllLabs();
      });
    }, 
    deleteLab(Lab){
         
      this.currentLab = Lab;
      axios.post(this.API_URL + "lab/deleteLab", this.currentLab).then(data => {
        console.log(data);
        this.isEditing = false;
        this.getAllLabs();
      });
    }

  }
};
</script>

<style scoped>
.container {
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}

</style>