<template>
  <div>
    <v-toolbar shaped class="toolbase" elevation="3" dense>
      <v-toolbar-title elevation="3">
        <v-chip dense>{{this.$route.params.company}}</v-chip>PURCHASE LEDGER REPORT
      </v-toolbar-title>
    </v-toolbar>
    <v-row dense>
      <v-col cols="12" md="3">
        <v-autocomplete
          required
          @change="resetLedgers"
          v-model="accgroup"
          :items="accountgroups"
          color="white"
          item-text="groupName"
          label="SELECT ACCOUNT GROUP"
          solo
          dense
          return-object
        ></v-autocomplete>
      </v-col>
      <v-col>
        <v-btn @click="getReport">GET ACCOUNTS</v-btn>
      </v-col>
      <v-col>{{accgroup.groupName}}</v-col>
    </v-row>
    <v-row>
      <v-col cols="12" sm="3" md="3">
        ACCOUNT SUMMARY
        <v-btn @click="getSelected">GET SELECTED REPORT</v-btn>
        <v-data-table
          v-model="selectedcertLists"
          :headers="headers"
          show-select
          hide-default-footer
          :items="ledgers"
          :items-per-page="50"
          class="elevation-1"
        >
          <template v-slot:item.balance="{ item }">{{SetCrDb(item.balance)}}</template>
        </v-data-table>
      </v-col>
      <v-col cols="12" sm="9" md="9">
        ACCOUNT DETAILS
        <ejs-grid
          ref="grid"
          :dataSource="transactions"
          rowHeight="25"
          :allowSorting="true"
          :allowFiltering="true"
          :toolbar="toolbar"
          :toolbarClick="toolbarClick"
          :allowExcelExport="true"
          :allowPdfExport="true"
          :queryCellInfo="queryCellInfo"
        >
          <e-columns>
            <e-column field="transdate" headerText="Date" textAlign="Left" width="90"></e-column>
            <e-column field="transType" headerText="PARTICULAR" textAlign="Left" width="90"></e-column>
            <e-column field="account" headerText="PARTICULAR" textAlign="Left" width="90"></e-column>
            <e-column field="description" headerText="DESCRIPTION" textAlign="Left" width="300"></e-column>
            <e-column field="crAmtInr" headerText="CREDIT INR" textAlign="Left" width="90"></e-column>
            <e-column field="dbAmtInr" headerText="DEBIT INR" textAlign="Left" width="90"></e-column>
            <e-column field="balanceInr" headerText="BALANCE INR" textAlign="Left" width="90"></e-column>
          </e-columns>
        </ejs-grid>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from "axios";
import commonVari from "../models/commonVari.js";
import {
  Page,
  Toolbar,
  ExcelExport,
  PdfExport
} from "@syncfusion/ej2-vue-grids";
export default {
  data() {
    return {
      selectedcertLists: [],
      toolbar: ["ExcelExport", "PdfExport", "CsvExport", "Print"],
      API_URL: commonVari.API_URL,
      accountgroups: [],
      transactions: [],
      accgroup: "",
      ledgers: [],
      balanceInr: 0,
      headers: [
        { text: "ACCOUNT", value: "name" },
        { text: "BALANCE", value: "balance" }
      ]
    };
  },
  provide: {
    grid: [PdfExport, ExcelExport, Toolbar, Page]
  },
  created() {
    this.companyId = this.$route.params.companyId;
    this.companyname = this.$route.params.company;
    this.getAllAccGroups();
  },
  methods: {
    getSelected() {
      this.balanceInr = 0.0;
      const formData = new FormData();
      formData.append("ledgerId", this.selectedcertLists[0].ledgerId);

      axios
        .post(this.API_URL + "trans/getTransByLedgerDetails", formData)
        .then(({ data }) => ((this.transactions = data)));
    },
    SetCrDb(item) {
      var amt = parseFloat(item);
      if (amt > 0) {
        return amt + " CR";
      }
      if (amt < 0) {
        return amt * -1 + " DB";
      }
    },
    resetLedgers() {
      this.ledgers = [];
    },
    queryCellInfo(args) {
      var crAcc = args.data.crAcc.name;
      var dbAcc = args.data.dbAcc.name;
      var crInr = 0;
      var dbInr = 0;
      if (args.column.field == "description") {
        
        var description = "";
        if (args.data.purchaselines != null) {
          var stockid = args.data.purchaselines.diamondTracking.diamond.stockid;
          var wt = args.data.purchaselines.diamondTracking.originalweight;
          var shape = args.data.purchaselines.diamondTracking.shape.shapename;
          var color = args.data.purchaselines.diamondTracking.color.colorname;
          var clarity =
            args.data.purchaselines.diamondTracking.clarity.clarityname;
          var lab = args.data.purchaselines.diamondTracking.lab.labname;
          var labno = args.data.purchaselines.diamondTracking.reportno;
          var isCollect = args.data.purchaselines.diamondTracking.stoneStatus;

          description =
            stockid +
            " " +
            wt +
            " " +
            shape +
            " " +
            color +
            " " +
            clarity +
            " " +
            lab +
            " " +
            labno +
            " Collect: " +
            isCollect;
        } else if (args.data.salelines != null) {
          var stockid1 = args.data.salelines.diamondTracking.diamond.stockid;
          var wt1 = args.data.salelines.diamondTracking.originalweight;
          var shape1 = args.data.salelines.diamondTracking.shape.shapename;
          var color1 = args.data.salelines.diamondTracking.color.colorname;
          var clarity1 =
            args.data.salelines.diamondTracking.clarity.clarityname;
          var lab1 = args.data.salelines.diamondTracking.lab.labname;
          var labno1 = args.data.salelines.diamondTracking.reportno;
          var isCollect1 = args.data.salelines.diamondTracking.stoneStatus;

          description =
            stockid1 +
            " " +
            wt1 +
            " " +
            shape1 +
            " " +
            color1 +
            " " +
            clarity1 +
            " " +
            lab1 +
            " " +
            labno1 +
            " Collect: " +
            isCollect1;
        }
        args.cell.innerText = description;
      }
      if (
        args.column.field == "account" ||
        args.column.field == "crAmtInr" ||
        args.column.field == "dbAmtInr"
      ) {
        var account = "";

        if (crAcc == this.ledgers[0].name) {
          account = dbAcc;
          crInr = args.data.crAmtInr;
          dbInr = 0;
        } else if (dbAcc == this.ledgers[0].name) {
          account = crAcc;
          dbInr = args.data.dbAmtInr;
          crInr = 0;
        }

        args.cell.innerText = account;
        if (args.column.field == "crAmtInr") {
          args.cell.innerText = crInr;
        }
        if (args.column.field == "dbAmtInr") {
          args.cell.innerText = dbInr;
        }
      }
      if (args.column.field == "balanceInr") {
        if (crAcc == this.ledgers[0].name) {
          account = dbAcc;
          crInr = args.data.crAmtInr;
          if (crInr != null) {
            this.balanceInr = this.balanceInr + parseFloat(crInr);
          } else {
            this.balanceInr = this.balanceInr + 0;
          }
        } else if (dbAcc == this.ledgers[0].name) {
          account = crAcc;
          dbInr = args.data.dbAmtInr;
          if (dbInr !== null) {
            this.balanceInr = this.balanceInr - parseFloat(dbInr);
          } else {
            this.balanceInr = this.balanceInr - 0;
          }
        }

        var balanceString = "";
        if (this.balanceInr > 0) {
          balanceString = this.balanceInr.toFixed(2) + " CR";
        }
        if (this.balanceInr < 0) {
          balanceString = this.balanceInr.toFixed(2) * -1 + " DB";
        }
        args.cell.innerText = balanceString;
      }
    },
    toolbarClick: function(args) {
      switch (args.item.text) {
        case "PDF Export":
          this.$refs.grid.pdfExport({ fileName: "new.pdf" });
          break;
        case "Excel Export":
          this.$refs.grid.excelExport({ fileName: "new.xlsx" });
          break;
        case "CSV Export":
          this.$refs.grid.csvExport({ fileName: "new.csv" });
          break;
      }
    },
    getReport() {
      const formData = new FormData();
      formData.append("ledger", this.accgroup.groupName);

      axios
        .post(this.API_URL + "ledger/getClosingBalanceByGroup", formData)
        .then(({ data }) => ((this.ledgers = data)));
    },
    getAllAccGroups() {
      axios
        .get(this.API_URL + "accountgroup/getAllAccountGroup")
        .then(({ data }) => (this.accountgroups = data));
    }
  }
};
</script>

