            <v-text-field dense v-model="ChildCompany.companyCode" label="ChildCompany Code" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="1" md="12">
            <v-text-field dense  v-model="ChildCompany.address" label="ChildCompany Address" outlined required></v-text-field>
