        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.city" label="City" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.pin" label="Pin" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-autocomplete
              v-model="country"
              :items="countries"
