              item-text="statename"
              label="State"
              solo
              dense
              return-object
            ></v-autocomplete>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.phone" label="Phone" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.mobile" label="Mobile" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.email" label="Email" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.gstno" label="GSTN" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.panno" label="PAN" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.bankaccname" label="Bank A/C Name" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field  dense v-model="ChildCompany.bankname" label="Bank Name" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field dense v-model="ChildCompany.accno" label="Bank A/C No" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.ifsc" label="IFS Code" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.iban" label="IBAN" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-text-field dense  v-model="ChildCompany.bankaddress" label="Bank Address" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.remark1" label="Remark 1" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field dense  v-model="ChildCompany.remark2" label="Remark 2" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-textarea
              label="Terms and Conditions"
              v-model="ChildCompany.terms"
              auto-grow
              outlined
              rows="3"
              row-height="25"
            ></v-textarea>
          </v-col>
        </v-row>
        <v-btn color="success" class="mr-4" @click="saveData">Submit</v-btn>
      </v-container>
    </v-form>

    <v-form v-if="isEditing">
      <v-container>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.name" label="ChildCompany Name" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.companyCode" label="ChildCompany Code" outlined required></v-text-field>
          </v-col>
          
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-text-field v-model="currentChildCompany.address" label="ChildCompany Address" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.city" label="City" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.pin" label="Pin" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-autocomplete
              v-model="currentChildCompany.country"
              :items="countries"
              color="white"
              item-text="countryname"
              label="Country"
              @change="fillStates"
              solo
              dense
              return-object
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4">
            <v-autocomplete
              v-model="currentChildCompany.province"
              :items="constates"
              color="white"
              item-text="statename"
              label="State"
              solo
              dense
              return-object
            ></v-autocomplete>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.phone" label="Phone" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.mobile" label="Mobile" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.email" label="Email" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.gstno" label="GSTN" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.panno" label="PAN" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.bankaccname" label="Bank A/C Name" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.bankname" label="Bank Name" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.accno" label="Bank A/C No" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.ifsc" label="IFS Code" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.iban" label="IBAN" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-text-field v-model="currentChildCompany.bankaddress" label="Bank Address" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.remark1" label="Remark 1" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentChildCompany.remark2" label="Remark 2" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-textarea
              label="Terms and Conditions"
              v-model="currentChildCompany.terms"
              auto-grow
              outlined
              rows="3"
              row-height="25"
            ></v-textarea>
          </v-col>
        </v-row>
        <v-btn color="success" class="mr-4" @click="updateData">Update</v-btn>
      </v-container>
    </v-form>


    <v-expansion-panels>
      <v-expansion-panel v-for="item in companies" v-bind:key="item.id">
        <v-expansion-panel-header>
          <v-col cols="15">{{item.name}}</v-col>
          <v-col cols="15">{{item.companyCode}}</v-col>
          <v-col cols="1">
            <a href="#" class="text-success" @click="isEditing=true; selectChildCompany(item)">
              <font-awesome-icon icon="edit" />
            </a>
          </v-col>
          <v-col cols="1">
            <a class="text-danger" @click="dialog=true ; selectChildCompany(item)">
              <font-awesome-icon icon="trash-alt" />
            </a>
          </v-col>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row no-gutters>
            <v-col cols="15">Address : {{item.address}}</v-col>
          </v-row>
          <v-row justify="space-around" no-gutters>
            <v-col cols="3">City : {{item.city}}</v-col>
            <v-col cols="3">Pin : {{item.pin}}</v-col>
            <v-col cols="3">State : {{item.province.statename}}</v-col>
            <v-col cols="3">Country : {{item.country.countryname}}</v-col>
          </v-row>
          <v-row justify="space-around" no-gutters>
            <v-col cols="3">Email : {{item.email}}</v-col>
            <v-col cols="3">Phone : {{item.phone}}</v-col>
            <v-col cols="3">Mobile : {{item.mobile}}</v-col>
            <v-col cols="3">GSTIN : {{item.gstno}}</v-col>
          </v-row>
          <v-row justify="space-around" no-gutters>
            <v-col cols="3">PAN : {{item.pan}}</v-col>
            <v-col cols="3">BANK A/C NAME : {{item.bankaccname}}</v-col>
            <v-col cols="3">BANK NAME : {{item.bankname}}</v-col>
            <v-col cols="3">BANK ADDRESS : {{item.bankaddress}}</v-col>
          </v-row>
          <v-row justify="space-around" no-gutters>
            <v-col cols="3">A/C NO : {{item.accno}}</v-col>
            <v-col cols="3">IFSC : {{item.ifsc}}</v-col>
            <v-col cols="3">IBAN : {{item.iban}}</v-col>
            <v-col cols="3"></v-col>
          </v-row>
          <v-row no-gutters>
            <v-col cols="15">REMARK 1 : {{item.remark1}}</v-col>
          </v-row>
          <v-row no-gutters>
            <v-col cols="15">REMARK 2 : {{item.remark2}}</v-col>
          </v-row>
          <v-row no-gutters>
            <v-col cols="15">TERMS & CONDITIONS : {{item.terms}}</v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>
<script>
import CountryState from "../models/CountryState";
import Country from "../models/Country";
import ChildCompany from "../models/ChildCompany";
import axios from "axios";
import commonVari from "../models/commonVari.js"

export default {
  data() {
    return {
      API_URL : commonVari.API_URL,
      dialog:false,
      ChildCompany: new ChildCompany(),
      isEditing: false,
      country: new Country(),
      curcountry: null,
      countries: [],
      search: null,
      constate: new CountryState(),
      constates: [],
      headers: [
        { text: "ChildCompany Name", value: "name" },
        { text: "Address", value: "address" },
        { text: "Action", value: "" }
      ],
      companies: [],
      currentChildCompany : new ChildCompany()
    };
  },
  created() {
    this.getAllCompanies();
    this.getAllCountries();
  },
  methods: {
    selectChildCompany(ChildCompany){
      this.currentChildCompany = ChildCompany;
    },
    deleteChildCompany(ChildCompany){
       this.currentChildCompany = ChildCompany;
      axios.post(this.API_URL + "childcompany/deleteCompany", this.currentChildCompany).then(data => {
        console.log(data);
        this.isEditing = false;
        this.getAllCompanies();
      });
    },
    getAllCompanies() {
      axios
        .get(this.API_URL + "childcompany/getAllCompany")
        .then(({ data }) => (this.companies = data));
    },
    getAllCountries() {
      axios
        .get(this.API_URL + "country/getAllCountry")
        .then(({ data }) => (this.countries = data));
    },
    saveData() {
      this.ChildCompany.province = this.constate;
      this.ChildCompany.country = this.country;
      const formData = new FormData();
      formData.append("company", JSON.stringify(this.ChildCompany));
      axios
        .post(this.API_URL + "childcompany/saveCompany", formData)
        .then(function(data) {
          if (data.data.error) {
            console.log(data.data.error.message);
          }
        });
      this.ChildCompany = new ChildCompany();
      this.getAllCompanies;
    },
    fillStates: function() {
      axios
        .post(this.API_URL + "states/stateByCountry", this.country)
        .then(({ data }) => (this.constates = data));
    },
    updateData() {
      // this.currentChildCompany.province = this.currentChildCompany.province;
      // this.currentChildCompany.country = this.currentChildCompany.country;
      const formData = new FormData();
      formData.append("company", JSON.stringify(this.currentChildCompany));
      axios
        .post(this.API_URL + "childcompany/updateCompany", formData)
        .then(function(data) {
          if (data.data.error) {
            console.log(data.data.error.message);
          }
        });
        this.isEditing = false;
      this.ChildCompany = new ChildCompany();
    },
  },
  
    
  
};
</script>
<style scoped>
.col-md-4 {
  padding-top: 0px;
  padding-bottom: 0px;
}
</style>