// import axios from 'axios';
class GIAService {

  getAxiosreq(a, b) {
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");

    var raw = JSON.stringify({
      header: { ipAddress: "43.240.10.101" },
      body: {
        reportDtls: {
          reportDtl: [{ reportNo: a, reportWeight: b }]
        }
      }
    });

    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: raw,
      redirect: "follow"
    };

    return (
      fetch(
        "https://labws.gia.edu/ReportCheck/processrequest.do",
        requestOptions
      )
        .then(response => (this.message = response.text()))
        // .then(response => console.log("response", response))
        .then(result => {
          return JSON.parse(result);
        })
        .catch(error => console.log("error", error))
    );
  }

  downLoadPdf(a, b) {
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");

    var raw = JSON.stringify({
      header: { ipAddress: "43.240.10.101" },
      body: {
        reportDtls: {
          reportDtl: [{ reportNo: a, reportWeight: b }]
        }
      }
    });

    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: raw,
      redirect: "follow"
    };

    return (
      fetch(
        "https://labws.gia.edu/ReportCheck/downloadpdfreport.do",
        requestOptions
      )
        .then(response => (this.message = response.text()))
        // .then(response => console.log("response", response))
        .then(result => {
          return JSON.parse(result);
        })
        .catch(error => console.log("error", error))
    );
  }

  getGiaResult(a, b) {

    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");

    var raw = JSON.stringify({
      header: { ipAddress: "43.240.10.101" },
      body: {
        reportDtls: {
          reportDtl: [{ reportNo: a, reportWeight: b }]
        }
      }
    });

    var requestOptions = {
      method: "POST",
      headers: myHeaders,
      body: raw,
      redirect: "follow"
    };

    fetch(
      "https://labws.gia.edu/ReportCheck/processrequest.do",
      requestOptions
    )
      .then(response => (response.text()))
      .then(result => console.log((JSON.parse(result))))
      .catch(error => console.log("error", error));


    return 'digesh';
  }

}
export default new GIAService();