export default class PaymentReceipt {
    constructor(id, date, company, paymentamt, receiptamt,cashBank,partyname, paymentusdamt, receiptusdamt, currate, remark){
        this.id = id;
        this.date = new Date().toISOString().substr(0, 10);
        this.company = company;
        this.cashBank = cashBank;
        this.partyname = partyname;
        this.paymentusdamt = paymentusdamt;
        this.receiptusdamt = receiptusdamt;
        this.currate = currate;
        this.paymentamt = paymentamt;
        this.receiptamt = receiptamt;
        this.remark = remark;
        

    }
}