<template>
  <div>
    <v-btn class="ma-2" outlined color="indigo" @click="importRapPrice">Import ROUND Rap Price</v-btn>
    {{message}}
    <v-btn class="ma-2" outlined color="indigo" @click="importPearRapPrice">Import PEAR Rap Price</v-btn>

    {{message}}
    <v-radio-group v-model="options" row style="height:30px">
      <v-radio label="AUTO" value="auto"></v-radio>
      <v-radio label="KEEP DISCOUNT SAME" value="discount"></v-radio>
      <v-radio label="KEEP $/CT SAME" value="usdPerCt"></v-radio>
    </v-radio-group>
    <v-btn class="ma-2" outlined color="indigo" @click="updateStock">UPDATE STOCK</v-btn>
  <v-divider></v-divider>
  <v-btn class="ma-2" outlined color="indigo" @click="retriveStock">UPDATE STOCK</v-btn>
  <v-data-table
      :headers="headers"
      :items="purchaselists"
      :items-per-page="10"
      class="elevation-1"
    >
     <template v-slot:item.oldTotal="{ item }">{{calculateOldTotal(item)}}</template>
     <template v-slot:item.newTotal="{ item }">{{calculateNewTotal(item)}}</template>
    </v-data-table>
  </div>
  

</template>

<script>
import commonVari from "../models/commonVari.js";

import axios from "axios";
export default {

  data() {
    return {
      file: null,
      API_URL: commonVari.API_URL,
      purchaselists: [],
      message: "",
      message1: {},
      message2: {},
      options: null,
       headers: [
        {
          text: "FOR LOCATION",
          value: "pur_lines.company[0].name"
        },
        { text: "STOCK #", value: "stockid" },
        { text: "SHAPE", value: "shape.shapename" },
        { text: "WEIGHT", value: "currentweight" },
        { text: "COLOR", value: "color.colorname" },
        { text: "CLARITY", value: "clarity.clarityname" },
        { text: "CUT", value: "cut.name" },
        { text: "POLISH", value: "polish.name" },
        { text: "SYM", value: "sym.name" },
        { text: "FLURO", value: "fluro.fluroname" },
        { text: "LAB", value: "lab.labname" },
        { text: "REPORT #", value: "reportno" },
        { text: "OLD RAP", value: "oldRapprice" },
        { text: "RAP", value: "rapprice" },
        { text: "OLD DIS", value: "oldSaleDis" },
        { text: "DIS", value: "saleDis" },
        { text: "OLD $/CT", value: "oldSalePerCarat" },
        { text: "$/CT", value: "salePerCarat" },
        { text: "OLD TOTAL", value: "oldTotal" },
        { text: "CURRENT TOTAL", value: "newTotal" },
        
        
      ]
    };
  },
  methods: {
    calculateOldTotal(item){
      var wt = parseFloat(item.currentweight);
      var perCt = parseFloat(item.oldSalePerCarat);
      var total = wt * perCt;
      return total.toFixed(2);
    },
    calculateNewTotal(item){
      var wt = parseFloat(item.currentweight);
      var perCt = parseFloat(item.salePerCarat);
      var total = wt * perCt;
      return total.toFixed(2);
    },
    retriveStock(){
      axios
        .get(this.API_URL + "diamond/getAllCurrentStock")
        .then(({ data }) => (console.log(data), this.purchaselists = data));
    },
    updateStock() {
      var option = this.options;
      var reqQuery = "";

      if (option == "auto") {
        reqQuery = "rapprice/updateStock";
      } else if (option == "discount") {
        reqQuery = "rapprice/updateStockKeepDiscountSame";
      } else if (option == "usdPerCt") {
        reqQuery = "rapprice/updateStockKeepPriceSame";
      }

      axios
        .post(this.API_URL + reqQuery)
        .then(data => {
          this.message = data.data;
        })
        .catch(error => {
          this.message = error.data;
        });
    },
    importRapPrice() {
      axios
        .post(this.API_URL + "rapprice/saveAllRound")
        .then(data => {
          this.message = data.data;
        })
        .catch(error => {
          this.message = error.data;
        });
    },
    importPearRapPrice() {
      axios
        .post(this.API_URL + "rapprice/saveAllPear")
        .then(data => {
          this.message = data.data;
        })
        .catch(error => {
          this.message = error.data;
        });
    },
    onFileSelected(event) {
      this.file = event.target.files[0];
    }
  }
};
</script>

