export default class Company {
    constructor(id, name, address, city, pin, country, province, email, phone, mobile, 
        gstno, panno, bankaccname, bankname, bankaddress, accno, ifsc, iban,terms, remark1, remark2, 
        passlocation, documentlocation, companyCode){

            this.companyCode = companyCode;
            this.id = id;
            this.name = name;
            this.address = address;
            this.city = city;
            this.pin = pin;
            this.country = country;
            this.province = province;
            this.email = email;
            this.phone = phone;
            this.mobile = mobile;
            this.gstno = gstno;
            this.panno = panno;
            this.bankaccname = bankaccname;
            this.bankname = bankname;
            this.bankaddress = bankaddress;
            this.accno = accno;
            this.ifsc = ifsc;
            this.iban = iban;
            this.terms = terms;
            this.remark1 = remark1;
            this.remark2 = remark2;
            this.passlocation = passlocation;
            this.documentlocation = documentlocation;

    }
}