<template>

  <div class="container">
    <v-alert v-model="success" type="success" dismissible>
      {{messageFromServer}}
    </v-alert>

    <div class="row">

<!-- FOR EDITING DATA -->

      <b-form class="column" style="width:40%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="Account Group Name :" label-for="groupname">
          <b-form-input
            id="groupname"
            v-model="currentaccountgroup.groupName"
            required
            placeholder="Enter Group name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Group Category :" label-for="category">
          <b-form-select v-model="selected" :options="options"></b-form-select>
          <!-- <b-form-input
            id="category"
            v-model="currentaccountgroup.category"
            required
            placeholder="Enter Clarity Reference like ( ,ROUND, ,RD, ,RB, )"
          ></b-form-input>-->
        </b-form-group>

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>


<!-- FOR SAVING DATA -->
      <b-form class="column" style="width:40%" @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="Account Group Name :" label-for="groupname">
          <b-form-input
            id="groupname"
            v-model="accountgroup.groupName"
            required
            placeholder="Enter Group name only"
          ></b-form-input>
        </b-form-group>

        <b-form-group id="input-group" label="Group Category :" label-for="category">
          <b-form-select v-model="selected" :options="options"></b-form-select>
        </b-form-group>

        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
      <div class="column" style="width:60%">
      <v-data-table
        :headers="headers"
        :items="accountgroups"
        :items-per-page="10"
        class="elevation-1"
      >
        <template v-slot:item="row">
          <tr v-bind:key="row.item.id">
            <td>{{row.item.groupName}}</td>
            <td>{{row.item.category}}</td>
             <td>
              <a class="text-success" @click="isEditing=true; selectAccGroup(row.item)"><font-awesome-icon icon="edit"/></a>
              &emsp;
              <a class="text-danger" @click="deleteAccGroup(row.item)"><font-awesome-icon icon="trash-alt"/></a>
            </td>
          </tr>
        </template>
      </v-data-table>
      </div>


    </div>
  </div>
</template>

<script>
import AccountGroup from "../models/accoutGroup";
import axios from "axios";
import commonVari from "../models/commonVari.js"
export default {
  name: "accoutGroup",
  
  data() {
    return {
      API_URL : commonVari.API_URL,
      success : false,
      messageFromServer:'',
      headers: [
        { text: "Account Group Name", value: "groupName" },
        { text: "Category", value: "category" },
        { text: "Action", value: "" }
       
      ],
      accountgroup: new AccountGroup(),
      isEditing: false,
      accountgroups: [],
      currentaccountgroup: new AccountGroup(),
      selected: "INCOME",
      options: [
        { value: "INCOME", text: "INCOME" },
        { value: "EXPENSES", text: "EXPENSES" },
        { value: "ASSETS", text: "ASSETS" },
        { value: "LIABILITIES", text: "LIABILITIES" }
      ]
    };
  },
  created() {
    this.getAllAccGroups();
  },
  methods: {
    selectAccGroup(accountgroup) {
      this.currentaccountgroup = accountgroup;
    },
    getAllAccGroups() {
      axios
        .get(this.API_URL + "accountgroup/getAllAccountGroup")
        .then(({ data }) => (this.accountgroups = data));
    },
    saveData() {
      this.accountgroup.category = this.selected;
      axios.post(this.API_URL + "accountgroup/saveAccountGroup", this.accountgroup).then(data => {
        if(data.data.error){
          console.log(data.error);
          this.messageFromServer= "Not Saved ... ";
          this.success=true;
        }else{
          this.messageFromServer= "Saved !!!!";
          this.success=true;
        }
        this.accountgroup.groupName = "";
        this.accountgroup.category = "";
        this.getAllAccGroups();
      });
    },
    updateData() {
      this.currentaccountgroup.category = this.selected;
      axios
        .post(this.API_URL + "accountgroup/updateAccountGroup", this.currentaccountgroup)
        .then(data => {
          console.log(data);
          this.isEditing = false;
          this.accountgroup.groupName = "";
          this.accountgroup.category = "";
          this.getAllAccGroups();
        });
    },
    deleteAccGroup(accountgroup) {
      this.currentaccountgroup = accountgroup;
      axios
        .post(this.API_URL + "accountgroup/deleteAccountGroup", this.currentaccountgroup)
        .then(data => {
          console.log(data);
          this.isEditing = false;
          this.getAllAccGroups();
        });
    }
  }
};
</script>
<style scoped>
.container {
  margin: 18px;
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}
</style>