<template>
  <div>
    <v-dialog v-model="dialog" persistent max-width="400">
      <v-card>
        <v-card-title class="headline">Are you sure want to delete ?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false">No</v-btn>
          <v-btn color="green darken-1" text @click="dialog = false; deleteledger(curledger)">YES</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-container>
      <v-app-bar color="deep-purple accent-4" dense dark>
        <v-toolbar-title elevation="3">{{this.$route.params.company}} ACCOUNT CREATION</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-menu left bottom>
          <template v-slot:activator="{ on }">
            <v-btn icon v-on="on">
              <v-icon>mdi-dots-vertical</v-icon>
            </v-btn>
