      <v-spacer></v-spacer>
      <!-- ACCOUNT GROUP CREATION       -->
      <v-dialog v-model="addGroupForm" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">Account Group</span>
          </v-card-title>
