          <v-spacer></v-spacer>
          <v-card-actions>
            <v-btn color="blue darken-1" text @click="addGroupForm = false;getAllAccGroups();">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <!-- COUNTRY CREATION       -->
      <v-dialog v-model="addCountryForm" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">Account Group</span>
          </v-card-title>
