            ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.firstname" label="First Name" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.lastname" label="Last Name" outlined></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="3">
            <v-text-field type="email" v-model="curledger.email" label="Email *" outlined ></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.mobile" label="Mobile *" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
