            <v-text-field v-model="curledger.registrationtype" label="Registration Type" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.accmanager" label="A/C Manager" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.brokername" label="Broker Name" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.delperson" label="Delivery Person" outlined></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="9">
            <v-text-field v-model="curledger.colladdress" label="Collection Address" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.comment" label="Comment" outlined></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.flatno" label="Flat No." outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.buildingname" label="Building Name" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.area" label="Area" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.street" label="Street" outlined></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.landmark" label="Landmark." outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.city" label="City" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.pincode" label="Pin" outlined></v-text-field>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="12" md="3">
            <v-autocomplete
              required
              v-model="country"
              :items="countries"
              color="white"
              item-text="countryname"
              label="Country"
              @change="fillStates"
              solo
              dense
              return-object
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="3">
            <v-autocomplete
