              color="white"
              item-text="statename"
              label="State"
              solo
              dense
              return-object
            ></v-autocomplete>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.officeno" label="Office No." outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.rapnetid" label="Rapnet Id" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.skypeid" label="Skype" outlined></v-text-field>
          </v-col>
          <v-col cols="12" md="3">
            <v-text-field v-model="curledger.otherid" label="Other Id" outlined></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="3">
            <v-menu
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="opendate"
                  label="Opening Balance on"
