    <!-- DATA TABLE      -->
    <div class="table">
      <v-data-table :headers="headers" :items="ledgers" :items-per-page="100" class="elevation-1">
        <template v-slot:item="row">
          <tr v-bind:key="row.item.id">
            <td>{{row.item.name}}</td>
            <td v-if="row.item.company != null">{{row.item.company.name}}</td>
            <td v-else></td>
            <td>{{row.item.contactperson}}</td>
            <td>{{row.item.firstname}}</td>
            <td>{{row.item.lastname}}</td>
            <td>{{row.item.email}}</td>
            <td>{{row.item.mobile}}</td>
            <td v-if="row.item.accgroup != null">{{row.item.accgroup.groupName}}</td>
            <td v-else></td>
            <td v-if="row.item.country != null">{{row.item.country.countryname}}</td>
            <td v-else></td>
            <td v-if="row.item.province != null">{{row.item.province.statename}}</td>
            <td v-else></td>
            <td>{{row.item.opendate}}</td>
            <td>{{row.item.usdAmt}}</td>
            <td>{{row.item.currate}}</td>
            <td>{{row.item.inrAmt}}</td>
            <td>{{row.item.cd}}</td>

            <td>
              <a class="text-success" @click="isEditing=true; selectledger(row.item)">
                <font-awesome-icon icon="edit" />
              </a>
              &emsp;
              <a
                class="text-danger"
                @click="dialog=true; selectledger(row.item)"
              >
                <font-awesome-icon icon="trash-alt" />
              </a>
            </td>
          </tr>
        </template>
