</template>
<script>
import accountGroupCreation from "../components/accoutGroup";
import countryCreation from "../components/country";
import stateCreation from "../components/countrystates";
import CountryState from "../models/CountryState";
import AccountGroup from "../models/accoutGroup";
import Country from "../models/Country";
import Company from "../models/Company";
import Ledger from "../models/Ledger";
import axios from "axios";
import commonVari from "../models/commonVari.js";

export default {
  components: {
    accountGroupCreation,
    countryCreation,
    stateCreation
  },
  data() {
    return {
      currency: ['USD','INR','HKD'],
      com:null,
      addStateForm: false,
      addCountryForm: false,
      addGroupForm: false,
      valid: true,
      opendate: new Date().toISOString().substr(0, 10),
      date: new Date().toISOString().substr(0, 10),
      menu2: false,
      API_URL: commonVari.API_URL,
      accgroup: new AccountGroup(),
      accgroups: [],
      companies: [],
      company: new Company(),
      dialog: false,
      ledger: new Ledger(),
      ledgers: [],
      curledger: new Ledger(),
      isEditing: false,
      country: new Country(),
      curcountry: null,
      countries: [],
      search: null,
      constate: new CountryState(),
      constates: [],
      headers: [
        { text: "ledger Name", value: "name" },
        { text: "COMPANY NAME", value: "company.name" },
        { text: "Contact Person", value: "address" },
        { text: "First Name", value: "firstname" },
        { text: "Last Name", value: "lastname" },
        { text: "Email", value: "email" },
        { text: "Mobile", value: "mobile" },
        { text: "Group", value: "accgroup" },
        { text: "Country", value: "country" },
        { text: "State", value: "province" },
        { text: "Opening Date", value: "opendate" },
        { text: "USD Amount", value: "usdAmt" },
        { text: "Cur Rate", value: "currate" },
        { text: "INR Amount", value: "inrAmt" },
        { text: "CR/DB", value: "cd" },
        { text: "Action", value: "" }
      ],
      // companies: [],
      currentledger: new Ledger(),
      currate: 0.0,
      usdAmt: 0.0
    };
  },
  computed: {
    inrAmt: {
      get: function() {
        let total = 0.0;
        total = (this.usdAmt * this.currate).toFixed(2) || 0.0;
        if (isNaN(total)) {
          total = 0.0;
        }
        return total;
      },
      set: function() {
        this.inrAmt = (this.usdAmt * this.currate).toFixed(2) || 0.0;
      }
    }
  },
  watch: {},
  created() {
    this.company = this.$route.params.company;
    this.getAllAccGroups();
    this.getAllLedgers();
    this.getAllCountries();
    this.getAllCompanies();
  },
  methods: {
    getAllCompanies() {
      const formData = new FormData();
      formData.append("companyId", this.$route.params.companyId);
      axios
        .post(this.API_URL + "company/getCompanyById", formData)
        .then(({ data }) => (this.ledger.company = data, this.com = data));
    },
    getAllLedgers() {
      const formData = new FormData();
      formData.append("companyId", this.$route.params.companyId);
      axios
        .post(this.API_URL + "ledger/getAllLedgerByCompany", formData)
        .then(({ data }) => (this.ledgers = data));
    },
    selectledger(ledger) {
      // this.inrAmt = ledger.inrAmt;
      this.country = ledger.country;
      this.constate = ledger.province;
      this.opendate = ledger.opendate;
      this.currate = ledger.currate;
      this.usdAmt = ledger.usdAmt;
      this.curledger = ledger;
      
    },
    deleteledger(ledger) {
      this.curledger = ledger;
      axios
        .post(this.API_URL + "ledger/deleteLedger", this.curledger)
        .then(() => {
          
          this.isEditing = false;
          this.getAllLedgers();
        });
    },
    // getAllCompanies() {
    //   axios
    //     .get(this.API_URL + "ledger/getAllledger")
    //     .then(({ data }) => (this.companies = data));
    // },
    getAllCountries() {
      axios
        .get(this.API_URL + "country/getAllCountry")
        .then(({ data }) => (this.countries = data));
    },

    saveData() {
      if (this.country != null || this.country.length != 0) {
        this.ledger.country = this.country;
      } else {
        this.ledger.country = null;
      }
      if (this.constate != null || this.constate.length != 0) {
        this.ledger.province = this.constate;
      } else {
        this.ledger.province = null;
      }

      this.ledger.opendate = this.date;
      this.ledger.usdAmt = this.usdAmt;
      this.ledger.currate = this.currate;
      this.ledger.inrAmt = this.inrAmt;
      this.ledger.cd = "CR";
      this.ledger.company = this.com;

      const formData = new FormData();

      formData.append("ledger", JSON.stringify(this.ledger));
      axios
        .post(this.API_URL + "ledger/saveLedger", formData)
        .then(function(data) {
          if (data.data.error) {
            alert(data.data.error.message);
          }
        });
      this.ledger = new Ledger();
      this.ledger.company = this.com;
      this.getAllLedgers();
    },
    fillStates: function() {
      axios
        .post(this.API_URL + "states/stateByCountry", this.country)
        .then(({ data }) => (this.constates = data));
    },
    updateData() {
      if (this.country != null || this.country.length != 0) {
        this.curledger.country = this.country;
      } else {
        this.curledger.country = null;
      }
      if (this.constate != null || this.constate.length != 0) {
        this.curledger.province = this.constate;
      } else {
        this.curledger.province = null;
      }

      this.curledger.opendate = this.date;
      this.curledger.usdAmt = this.usdAmt;
      this.curledger.currate = this.currate;
      this.curledger.inrAmt = this.inrAmt;
      this.curledger.cd = "CR";
      this.curledger.company = this.com;

      const formData = new FormData();
      formData.append("ledger", JSON.stringify(this.curledger));
      axios
        .post(this.API_URL + "ledger/updateLedger", formData)
        .then(function(data) {
          if (data.data.error) {
            alert(data.data.error.message);
          }
        });
      this.isEditing = false;
      this.ledger = new Ledger();
    },
    getAllAccGroups() {
      axios
        .get(this.API_URL + "accountgroup/getAllAccountGroup")
        .then(({ data }) => (this.accgroups = data));
    }
  }
};
</script>
