export default class Diamond {
    constructor(originalweight, currentweight, pur_lines, purchase_head,id, weight, shape, color, clarity){
        this.originalweight = originalweight;
		this.currentweight = currentweight;
		this.pur_lines = pur_lines;
        this.purchase_head = purchase_head;
        this.id = id;
        this.weight = weight;
        this.shape = shape;
        this.color = color;
        this.clarity = clarity;
    }
    
}