<template>
  <div>
    <v-card flat color="#D81B60">
      <b-form @submit.prevent="retriveDetails">
        <v-row>
          <v-col cols="12" sm="2">
            <v-img
              src="https://www.gia.edu/assets/img/global-header/desktop/gia-logo.svg"
              lazy-src
              aspect-ratio="1.4"
              contain
              class="white lighten-2"
              max-width="250"
              max-height="40"
            >
              <template v-slot:placeholder>
                <v-row class="fill-height ma-0" align="center" justify="center">
                  <v-progress-circular indeterminate color="black lighten-5"></v-progress-circular>
                </v-row>
              </template>
            </v-img>
          </v-col>
          <v-col cols="12" sm="2">
            <v-text-field
              hide-details
              dark
              type="number"
              v-model="reportno"
              label="REPORT NO"
              outlined
              dense
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="2">
            <v-text-field hide-details dark v-model="weight" label="WEIGHT" outlined dense></v-text-field>
          </v-col>

          <v-col cols="12" sm="1">
            <v-btn dark type="submit" outlined>RESULT</v-btn>
          </v-col>
          <v-col cols="12" sm="1">
            <v-btn @click="downLoadPdf" dark type="submit" outlined>PDF</v-btn>
          </v-col>

          <v-spacer />
        </v-row>
      </b-form>
    </v-card>

    <v-card flat color="#E3F2FD">
      <v-row>
        <v-col cols="20" sm="1">
          <v-text-field readonly v-model="shape" label="SHAPE" outlined dense></v-text-field>
        </v-col>
        <v-col cols="20" sm="1">
          <v-text-field readonly v-model="color" label="COLOR" outlined dense></v-text-field>
        </v-col>
        <v-col cols="20" sm="1">
          <v-text-field readonly v-model="clarity" label="CLARITY" outlined dense></v-text-field>
        </v-col>
        <v-col cols="20" sm="1">
          <v-text-field readonly v-model="cps" label="CUT-POL-SYM" outlined dense></v-text-field>
        </v-col>
        <v-col cols="20" sm="1">
          <v-text-field readonly v-model="fluro" label="FLR-COLOR" outlined dense></v-text-field>
        </v-col>
        <v-col cols="20" sm="2">
          <v-text-field readonly v-model="measurement" label="MEASUREMENT" outlined dense></v-text-field>
        </v-col>
        <v-col cols="20" sm="4">
          <v-text-field readonly v-model="kts" label="KEY TO SYMBOL" outlined dense></v-text-field>
        </v-col>
        <v-col cols="20" sm="4">
          <v-text-field readonly v-model="reportComment" label="REPORT COMMENT" outlined dense></v-text-field>
        </v-col>
        <v-col cols="20" sm="1">
          <v-text-field readonly v-model="reportDt" label="REPORT DATE" outlined dense></v-text-field>
        </v-col>
      </v-row>
    </v-card>

    <v-row v-if="show" align="center" justify="center">
      <v-col cols="12" sm="6">
        <v-img
          :src="plotImage"
          lazy-src
          aspect-ratio="1"
          contain
          class="white lighten-2"
          max-width="800"
          max-height="400"
        >
          <template v-slot:placeholder>
            <v-row class="fill-height ma-0" align="center" justify="center">
              <v-progress-circular indeterminate color="black lighten-5"></v-progress-circular>
            </v-row>
          </template>
        </v-img>
      </v-col>

      <v-col cols="12" sm="6">
        <v-img
          :src="propImage"
          lazy-src
          aspect-ratio="1.7"
          contain
          class="white lighten-2"
          max-width="800"
          max-height="800"
        >
          <template v-slot:placeholder>
            <v-row class="fill-height ma-0" align="center" justify="center">
              <v-progress-circular indeterminate color="black lighten-5"></v-progress-circular>
            </v-row>
          </template>
        </v-img>
      </v-col>
    </v-row>
  </div>
</template>

<script>
// import axios from "axios";
import GIAService from "../services/GIAService";
export default {
  data() {
    return {
      show: false,
      weight: "",
      reportno: "",
      plotImage: "",
      propImage: "",
      shape: "",
      color: "",
      message: "",
      clarity: "",
      cps: "",
      fluro: "",
      measurement: "",
      kts: "",
      reportComment: "",
      reportDt: ""
    };
  },

  methods: {
    async downLoadPdf() {
      console.log("you are here");
      var pdfreport;
      var reportno = this.reportno;
      var weight = this.weight;
      await GIAService.downLoadPdf(reportno, weight).then(data => {
        pdfreport = data;
        this.base64ToArrayBuffer(pdfreport);
      });
    },
    async base64ToArrayBuffer(base64) {
      var binaryString = window.atob(base64);
      var binaryLen = binaryString.length;
      var bytes = new Uint8Array(binaryLen);
      for (var i = 0; i < binaryLen; i++) {
        var ascii = binaryString.charCodeAt(i);
        bytes[i] = ascii;
      }
      this.saveByteArray(this.reportno, bytes);
    },
    async saveByteArray(reportName, byte) {
      var blob = new Blob([byte], { type: "application/pdf" });
      var link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      var fileName = reportName;
      link.download = fileName;
      link.click();
    },

    async getGiaResultreport() {
      var resultData;
      var reportno = this.reportno;
      var weight = this.weight;
      await GIAService.getAxiosreq(reportno, weight).then(data => {
        resultData = data.reportDtls.reportDtl[0];
        console.log(resultData);
        this.message = resultData.message;
        if (this.message.length > 0) {
          alert(this.message);
        }
        this.shape = resultData.shape;
        this.color = resultData.color;

        this.clarity = resultData.clarity;
        this.cps =
          resultData.finalCut +
          " - " +
          resultData.polish +
          " - " +
          resultData.symmetry;
        this.fluro =
          resultData.fluorescenceIntensity +
          " - " +
          resultData.fluorescenceColor;
        this.measurement =
          resultData.length +
          " - " +
          resultData.width +
          " - " +
          resultData.depth;
        this.kts = resultData.keyToSymbols;
        this.reportComment = resultData.reportComments;
        this.reportDt = resultData.reportDt;

        // (length = resultData.length),
        //   (width = resultData.width),
        //   (depth = resultData.depth),
        //   (colorDesc = resultData.colorDesc),
        //   (depthPt = resultData.depthPct),
        //   (tablePt = resultData.tablePct),
        //   (crAg = resultData.crnAg),
        //   (crHt = resultData.crnHt),
        //   (pavAg = resultData.pavAg),
        //   (pavDp = resultData.pavDp),
        //   (starLn = resultData.starLn),
        //   (lrHalf = resultData.lrHalf),
        //   (girdle = resultData.girdle),
        //   (girdleCondition = resultData.girdleCondition),
        //   (girdlePct = resultData.girdlePct),
        //   (culetSize = resultData.culetSize),
        //   (keyToSymbol = resultData.keyToSymbols),
        //   (reportDate = resultData.reportDt),
        //   (inscription = resultData.inscription),
        //   (infoMessage = resultData.reportComments),
        //   (clarityStatus = resultData.clarity),
        //   (flurocolor = flurocolor);
      });
    },
    retriveDetails() {
      this.show = true;
      var plotImagefirstPart =
        "https://myapps.gia.edu/DigitalImageClient/getDigitalImage.do?imgType=PLOTIMG&";
      var porpImgefirstPart =
        "https://myapps.gia.edu/DigitalImageClient/getDigitalImage.do?imgType=PROPIMG&";

      this.plotImage =
        plotImagefirstPart +
        "reportNo=" +
        this.reportno +
        "&IPAddress=43.240.10.101";

      // var img = porpImgefirstPart.concat("reportNO=",this.reportno,"&IPAddress=43.240.10.101");
      this.propImage =
        porpImgefirstPart +
        "reportNo=" +
        this.reportno +
        "&IPAddress=43.240.10.101";

      this.getGiaResultreport();
    }
  }
};
</script>

<style scoped>
v-text-field {
  font-weight: bold;
}
.col-sm-1.col-20 {
  height: 60px;
}
.col-sm-2.col-20 {
  height: 60px;
}
.col-sm-4.col-20 {
  height: 60px;
}
.v-input {
  font-weight: bold;
}

.v-card.v-sheet.theme--light {
  padding-left: 10px;
  padding-right: 10px;
}
</style>