<template>
  <div>
    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">{{this.$route.params.company}} | DAILY REPORT</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-container>

    <v-container>
      <v-row>
        <v-col cols="12" md="2">
          <v-autocomplete
            required
            v-model="selectcompany"
            :items="companies"
            color="blue"
            item-text="name"
            solo
            dense
            outlined
            hide-details
            opeque
            return-object
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="2">
          <v-menu
            v-model="menu2"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            min-width="290px"
            dense
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="opendate"
                label="SELECT DATE"
                outlined
                readonly
                dense
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="opendate" @input="menu2 = false"></v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols="12" md="2">
          <v-btn outlined color="indigo" @click="getPurchase()">Retrive Report</v-btn>
        </v-col>
      </v-row>
    </v-container>

    <v-divider></v-divider>
    <v-data-table
      :headers="headers"
      :items="purchaselists"
      :items-per-page="10"
      class="elevation-1"
    >
      <template v-slot:item="row">
        <tr v-bind:key="row.item.id">
          <!-- <tr v-for="item in purchaselists" v-bind:key="item.id"> -->
          <td>{{row.item.trasnType}}</td>
          <td>{{row.item.diamond.stockid}}</td>
          <td>{{row.item.originalweight}}</td>
          <td>{{row.item.shape.shapename}}</td>
          <td>{{row.item.color.colorname}}</td>
          <td>{{row.item.clarity.clarityname}}</td>
          <td>{{row.item.cut.name}}</td>
          <td>{{row.item.polish.name}}</td>
          <td>{{row.item.sym.name}}</td>
          <td>{{row.item.fluro.fluroname}}</td>
          <td>{{row.item.flurocolor}}</td>
          <td>{{row.item.lab.labname}}</td>
          <td>{{row.item.reportno}}</td>
        </tr>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import CompanyRelation from "../models/CompanyRelation";
import commonVari from "../models/commonVari.js";
import axios from "axios";
export default {
  data() {
    return {
      menu2: false,
      opendate: new Date().toISOString().substr(0, 10),
      API_URL: commonVari.API_URL,
      purchaselists: [],
      companies: [],
      selectedCompanies: [],
      selectcompany: new CompanyRelation(),
      company: new CompanyRelation(),
      headers: [
        { text: "ENTRY TYPE", value: "trasnType" },
        { text: "STOCK #", value: "diamond.stockid" },
        { text: "WEIGHT", value: "originalweight" },
        { text: "SHAPE", value: "shape.shapename" },
        { text: "COLOR", value: "color.colorname" },
        { text: "CLARITY", value: "clarity.clarityname" },
        { text: "CUT", value: "cut.name" },
        { text: "POLISH", value: "polish.name" },
        { text: "SYM", value: "sym.name" },
        { text: "FLURO", value: "fluro.fluroname" },
        { text: "FLURO COLOR", value: "flurocolor" },
        { text: "LAB", value: "lab.labname" },
        { text: "REPORT #", value: "reportno" }
      ]
    };
  },
  created() {
    this.company = this.$route.params.company;
    this.getAllCompanies(this.company);
  },
  methods: {
    getAllCompanies(company) {
      const formData = new FormData();
      formData.append("company", company);
      axios
        .post(
          this.API_URL + "companyrelation/getCompanyByMainCompany",
          formData
        )
        .then(({ data }) => (this.companies = data));
    },
    getPurchase() {
      this.selectedCompanies.push(this.selectcompany);
      
      const formData = new FormData();
      
      formData.append("date", this.opendate);
      axios
        .post(this.API_URL + "diamondtracking/getAllDate", formData)
        .then(({ data }) => (console.log(data), this.purchaselists = data));
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(26, 172, 216);
  color: white;
}
.container {
  max-width: 1185px;
  min-width: -webkit-fill-available;
}
</style>