<template>
  <div class="container">
      
    <div class="row">
        
<!-- TABLE DATA         -->
      <div class="column" style="width:60%">
        <div class="table-responsive" style="overflow-x:auto;">
          <table class="table table-condensed">
            <thead>
              <tr>
                <th>fluro</th>
                <th>fluro Reference</th>
                <th>Display Order</th>
                <th>Edit</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in fluros" v-bind:key="item.id">
                <td>{{item.fluroname}}</td>
                <td>{{item.fluro_ref}}</td>
                <td>{{item.orderId}}</td>
                <td><a class="text-success" variant="primary" @click="isEditing=true; selectfluro(item)"><font-awesome-icon icon="edit"/></a></td>
                    <td><a class="text-danger" variant="primary" @click="deletefluro(item)"><font-awesome-icon icon="trash-alt"/></a>
                  <!-- <td><a class="text-success" variant="primary" @click="isEditing=true; selectfluro(item)"><font-awesome-icon icon="edit"/></a></td>
                    <td><a class="text-danger" variant="primary" @click="deletefluro(item)"><font-awesome-icon icon="trash-alt"/></a> -->
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
        <v-divider vertical></v-divider>
<!-- FOR SAVING DATA -->
      <b-form class="column" style="width:30%"  @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="fluro Name :" label-for="fluroname">
          <b-form-input
            id="fluroname"
            v-model="fluro.fluroname"
            required
            placeholder="Enter fluro name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="fluro Reference :" label-for="fluroref">
          <b-form-input
            id="fluroref"
            v-model="fluro.fluro_ref"
            required
            placeholder="Enter fluro Reference like ( ,ROUND, ,RD, ,RB, )"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Color Display Order :" label-for="flurodo">
          <b-form-input
            type="number"
            id="flurodo"
            v-model="fluro.orderId"
            required
            placeholder="Enter fluro Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
<!-- FOR EDITING DATA -->

      <b-form class="column" style="width:30%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="fluro Name :" label-for="fluroname">
          <b-form-input
            id="fluroname"
            v-model="currentfluro.fluroname"
            required
            placeholder="Enter fluro name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="fluro Reference :" label-for="fluroref">
          <b-form-input
            id="fluroref"
            v-model="currentfluro.fluro_ref"
            required
            placeholder="Enter fluro Reference like ( ,ROUND, ,RD, ,RB, )"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="fluro Display Order :" label-for="flurodo">
          <b-form-input
            type="number"
            id="flurodo"
            v-model="currentfluro.orderId"
            required
            placeholder="Enter fluro Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
import Fluro from "../models/Fluro";
import axios from "axios";
import commonVari from "../models/commonVari.js"
export default {
  name: "Fluro",
  data() {
    return {
      API_URL: commonVari.API_URL,
      fluro: new Fluro(),
      isEditing: false,
      fluros: [], 
      currentfluro: new Fluro(),
    };
  },
  created() {
    this.getAllfluros();
  },
  methods: {
      selectfluro(fluro){
          this.currentfluro = fluro;
      },
    getAllfluros() {
      axios
        .get(this.API_URL + "fluro/getAllFluro")
        .then(({ data }) => (this.fluros = data));
    },
    saveData() {
      axios.post(this.API_URL + "fluro/saveFluro", this.fluro).then(data => {
        console.log(data);
        this.fluro.fluroname = "";
        this.fluro.fluro_ref = "";
        this.fluro.orderId = "";
        this.getAllfluros();
      });
    },
    updateData() {
      axios.post(this.API_URL + "fluro/updateFluro", this.currentfluro).then(data => {
        console.log(data);
        this.isEditing = false;
        this.fluro.fluroname = "";
        this.fluro.fluro_ref = "";
        this.fluro.orderId = "";
        this.getAllfluros();
      });
    }, 
    deletefluro(fluro){
         
      this.currentfluro = fluro;
      axios.post(this.API_URL + "fluro/deleteFluro", this.currentfluro).then(data => {
        console.log(data);
        this.isEditing = false;
        this.getAllfluros();
      });
    }

  }
};
</script>
<style scoped>
.container {
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}
</style>