import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
import axios from "axios";
import commonVari from "../models/commonVari.js";
import User from "../models/User";

export default new Vuex.Store({
  state: {
    loader: false,
    API_URL: commonVari.API_URL,
    user: new User(),
    token: localStorage.getItem('access_token') || null,
    processList: [
      // { "name": 'SEND TO INDIA', "purpose": 'RE-CUT' },
      { "name": 'COLLECT FROM HK', "purpose": 'RE-CUT' },
      { "name": 'SEND FOR RECUT', "purpose": 'RE-CUT' },
      { "name": 'RECEIVE FROM RECUT', "purpose": 'RE-CUT' },
      { "name": 'SEND TO LAB', "purpose": 'FOR LAB' },
      { "name": 'RECEIVE FROM LAB', "purpose": 'FOR LAB' },
      { "name": 'QC CHECK FROM LAB', "purpose": 'FOR QC' },
      { "name": 'DNA', "purpose": 'FOR DNA' },
      { "name": 'PRICING', "purpose": 'FOR PRICING' }],
    // { "name": 'SEND TO HK', "purpose": 'FOR STOCK' }],
    // { "name": 'COLLECT FROM INDIA', "purpose": 'FOR STOCK' }],
    processListForShine: [
      { "name": 'SEND TO INDIA', "purpose": 'RE-CUT' },
      // { "name": 'COLLECT FROM HK', "purpose": 'RE-CUT' },
      // { "name": 'SEND FOR RECUT', "purpose": 'RE-CUT' },
      // { "name": 'RECEIVE FROM RECUT', "purpose": 'RE-CUT' },
      { "name": 'SEND TO LAB', "purpose": 'FOR LAB' },
      { "name": 'RECEIVE FROM LAB', "purpose": 'FOR LAB' },
      { "name": 'QC CHECK FROM LAB', "purpose": 'FOR QC' },
      { "name": 'DNA', "purpose": 'FOR DNA' },
      { "name": 'PRICING', "purpose": 'FOR PRICING' },
      // { "name": 'SEND TO HK', "purpose": 'FOR STOCK' },
      { "name": 'COLLECT FROM INDIA', "purpose": 'FOR STOCK' }],

  },
  getters:{
    loggedIn(state){
      return state.token != null
    }

  },
  mutations: {
    LOADER(state, payload) {
      state.loader = payload;
    },
    retriveToken(state, token) {
      state.token = token;
    },
    destroyToken(state){
      state.token = null
    }

  },
  actions: {
    destroyToken(context){
      if(context.getters.loggedIn){
        return new Promise((resolve, reject) => {
          axios
            .post(this.state.API_URL + "user/logout")
            .then(response => {
              localStorage.removeItem('access_token')
              context.commit('destroyToken')
              resolve(response)
    
            })
            .catch(error => {
              
              localStorage.removeItem('access_token')
              context.commit('destroyToken')
              reject(error)
            });
          })
      }
    },
    async retriveToken(context, credentials) {

      this.state.user.username = credentials.username;
      this.state.user.password = credentials.password;

      return new Promise((resolve, reject) => {
      axios
        .post(this.state.API_URL + "user/login", this.state.user)
        .then(response => {
          const token = response.data.token;
          localStorage.setItem('access_token', token)
          context.commit('retriveToken', token)
          resolve(response)

        })
        .catch(error => {
          console.log(error);
          reject(error)
        });
      })
    }
  },
  modules: {
  }
})
