import axios from 'axios';
import {BehaviorSubject} from 'rxjs';

const API_URL = 'http://localhost:8081/user/';

const currentSubject = new BehaviorSubject(JSON.parse(localStorage.getItem('currentUser')));
class UserService{

    get currentUserValue(){
        return currentSubject.value;
    }
    get currentUser(){
        return currentSubject.asObservable();
    }
    async login(user){
        const headers = {
            authorization: 'Basic ' +  btoa(user.username + ':' + user.password)
        };
        const response = await axios.get(API_URL + 'login', { headers: headers });
        localStorage.setItem('currentUser', JSON.stringify(response.data));
        currentSubject.next(response.data);
    }
    async logout(){
        await axios.post(API_URL + 'logout', {});
        localStorage.removeItem('currentUser');
        currentSubject.next(null);
    }
    register(user){
        return axios.post(API_URL+'add', JSON.stringify(user),
        {headers :{'Content-Type': 'application/json; charset=UTF-8'}});
    }
    

}
export default new UserService();