<template>
  <div class="container">
      
    <div class="row">
        
            
        
<!-- TABLE DATA         -->
      <div class="column" style="width:60%">
        <div class="table-responsive" style="overflow-x:auto;">
          <table class="table table-condensed">
            <thead>
              <tr>
                <th>Shape</th>
                <th>Shape Reference</th>
                <th>Display Order</th>
                <th>Edit</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in shapes" v-bind:key="item.id">
                <td>{{item.shapename}}</td>
                <td>{{item.shape_ref}}</td>
                <td>{{item.orderId}}</td>
                  <td><a class="text-success" variant="primary" @click="isEditing=true; selectShape(item)"><font-awesome-icon icon="edit"/></a></td>
                    <td><a class="text-danger" variant="primary" @click="deleteShape(item)"><font-awesome-icon icon="trash-alt"/></a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
  <v-divider vertical></v-divider>
<!-- FOR SAVING DATA -->
      <b-form class="column" style="width:30%"  @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="Shape Name :" label-for="shapename">
          <b-form-input
            id="shapename"
            v-model="shape.shapename"
            required
            placeholder="Enter Shape name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Shape Reference :" label-for="shaperef">
          <b-form-input
            id="shaperef"
            v-model="shape.shape_ref"
            required
            placeholder="Enter Shape Reference like ( ,ROUND, ,RD, ,RB, )"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Color Display Order :" label-for="shapedo">
          <b-form-input
            type="number"
            id="shapedo"
            v-model="shape.orderId"
            required
            placeholder="Enter Shape Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
<!-- FOR EDITING DATA -->

      <b-form class="column" style="width:40%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="Shape Name :" label-for="shapename">
          <b-form-input
            id="shapename"
            v-model="currentShape.shapename"
            required
            placeholder="Enter Shape name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Shape Reference :" label-for="shaperef">
          <b-form-input
            id="shaperef"
            v-model="currentShape.shape_ref"
            required
            placeholder="Enter Shape Reference like ( ,ROUND, ,RD, ,RB, )"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Shape Display Order :" label-for="shapedo">
          <b-form-input
            type="number"
            id="shapedo"
            v-model="currentShape.orderId"
            required
            placeholder="Enter Shape Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
import Shape from "../models/Shape";
import axios from "axios";
import commonVari from "../models/commonVari.js"
export default {
  name: "Shape",
  data() {
    return {
      API_URL: commonVari.API_URL,
      shape: new Shape(),
      isEditing: false,
      shapes: [], 
      currentShape: new Shape(),
    };
  },
  created() {
    this.getAllShapes();
  },
  methods: {
      selectShape(shape){
          this.currentShape = shape;
      },
    getAllShapes() {
      axios
        .get(this.API_URL + "shape/getAllShapes")
        .then(({ data }) => (this.shapes = data));
    },
    saveData() {
      axios.post(this.API_URL + "shape/saveShape", this.shape).then(data => {
        console.log(data);
        this.shape.shapename = "";
        this.shape.shape_ref = "";
        this.shape.orderId = "";
        this.getAllShapes();
      });
    },
    updateData() {
      axios.post(this.API_URL + "shape/updateShape", this.currentShape).then(data => {
        console.log(data);
        this.isEditing = false;
        this.shape.shapename = "";
        this.shape.shape_ref = "";
        this.shape.orderId = "";
        this.getAllShapes();
      });
    }, 
    deleteShape(shape){
         
      this.currentShape = shape;
      axios.post(this.API_URL + "shape/deleteShape", this.currentShape).then(data => {
        console.log(data);
        this.isEditing = false;
        this.getAllShapes();
      });
    }

  }
};
</script>
<style scoped>
.container {
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}
</style>