<template>
  <div class="container">
      
    <div class="row">
        
            
        
<!-- TABLE DATA         -->
      <div class="column" style="width:60%">
        <div class="table-responsive" style="overflow-x:auto;">
          <table class="table table-condensed">
            <thead>
              <tr>
                <th>Clarity</th>
                <th>Clarity Reference</th>
                <th>Display Order</th>
                <th>Edit</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in clarities" v-bind:key="item.id">
                <td>{{item.clarityname}}</td>
                <td>{{item.clarity_ref}}</td>
                <td>{{item.orderId}}</td>

                <td><a class="text-success" variant="primary" @click="isEditing=true; selectClarity(item)"><font-awesome-icon icon="edit"/></a></td>
                    <td><a class="text-danger" variant="primary" @click="deleteClarity(item)"><font-awesome-icon icon="trash-alt"/></a>
                  <!-- <td><b-button variant="primary" @click="isEditing=true; selectClarity(item)">Edit</b-button></td>
                    <td><b-button variant="primary" @click="deleteClarity(item)">Delete</b-button> -->
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
       <v-divider vertical></v-divider>
<!-- FOR SAVING DATA -->
      <b-form class="column" style="width:30%"  @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="Clarity Name :" label-for="clarityname">
          <b-form-input
            id="clarityname"
            v-model="clarity.clarityname"
            required
            placeholder="Enter Clarity name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Clarity Reference :" label-for="shaperef">
          <b-form-input
            id="shaperef"
            v-model="clarity.clarity_ref"
            required
            placeholder="Enter Clarity Reference like ( ,ROUND, ,RD, ,RB, )"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Color Display Order :" label-for="shapedo">
          <b-form-input
            type="number"
            id="shapedo"
            v-model="clarity.orderId"
            required
            placeholder="Enter Clarity Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
<!-- FOR EDITING DATA -->

      <b-form class="column" style="width:30%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="Clarity Name :" label-for="clarityname">
          <b-form-input
            id="clarityname"
            v-model="currentClarity.clarityname"
            required
            placeholder="Enter Clarity name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Clarity Reference :" label-for="shaperef">
          <b-form-input
            id="shaperef"
            v-model="currentClarity.clarity_ref"
            required
            placeholder="Enter Clarity Reference like ( ,ROUND, ,RD, ,RB, )"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Clarity Display Order :" label-for="shapedo">
          <b-form-input
            type="number"
            id="shapedo"
            v-model="currentClarity.orderId"
            required
            placeholder="Enter Clarity Display Order like (1, 2, 3)"
          ></b-form-input>
        </b-form-group>

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
import Clarity from "../models/Clarity";
import axios from "axios";
import commonVari from "../models/commonVari.js"
export default {
  name: "Clarity",
  data() {
    return {
      API_URL: commonVari.API_URL,
      clarity: new Clarity(),
      isEditing: false,
      clarities: [], 
      currentClarity: new Clarity(),
    };
  },
  created() {
    this.getAllShapes();
  },
  methods: {
      selectClarity(clarity){
          this.currentClarity = clarity;
      },
    getAllShapes() {
      axios
        .get(this.API_URL + "clarity/getAllClarity")
        .then(({ data }) => (this.clarities = data));
    },
    saveData() {
      axios.post(this.API_URL + "clarity/saveClarity", this.clarity).then(() => {
        
        this.clarity.clarityname = "";
        this.clarity.clarity_ref = "";
        this.clarity.orderId = "";
        this.getAllShapes();
      });
    },
    updateData() {
      axios.post(this.API_URL + "clarity/updateClarity", this.currentClarity).then(() => {
        
        this.isEditing = false;
        this.clarity.clarityname = "";
        this.clarity.clarity_ref = "";
        this.clarity.orderId = "";
        this.getAllShapes();
      });
    }, 
    deleteClarity(clarity){
         
      this.currentClarity = clarity;
      axios.post(this.API_URL + "clarity/deleteClarity", this.currentClarity).then(() => {
        
        this.isEditing = false;
        this.getAllShapes();
      });
    }

  }
};
</script>
<style scoped>
.container {
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}
</style>