export default class Purchase{
    
    constructor(id, invoice_weight, invoice_usdtotal, invoice_inrtotal, trans, plines, diamond){
        this.invoice_weight = invoice_weight;
		this.invoice_usdtotal = invoice_usdtotal;
        this.invoice_inrtotal = invoice_inrtotal;
        
		this.trans = trans;
		this.plines = plines;
        this.diamond = diamond;
        this.id = id;
    }
}