<template>
  <div class="container">
      
    <div class="row">
        
            
        
<!-- TABLE DATA         -->
      <div class="column" style="width:60%">
        <div class="table-responsive" style="overflow-x:auto;">
          <table class="table table-condensed">
            <thead>
              <tr>
                <th>Cut/Pol/Sym</th>
                <th>Reference</th>
                <th>Edit</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in cpss" v-bind:key="item.id">
                <td>{{item.name}}</td>
                <td>{{item.reference}}</td>
                 <td><a class="text-success" variant="primary" @click="isEditing=true; selectCps(item)"><font-awesome-icon icon="edit"/></a></td>
                    <td><a class="text-danger" variant="primary" @click="deleteCps(item)"><font-awesome-icon icon="trash-alt"/></a>
                  <!-- <td><b-button variant="primary" @click="isEditing=true; selectCps(item)">Edit</b-button></td>
                    <td><b-button variant="primary" @click="deleteCps(item)">Delete</b-button> -->
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
       <v-divider vertical></v-divider>
<!-- FOR SAVING DATA -->
      <b-form class="column" style="width:30%"  @submit.prevent="saveData" v-if="!isEditing">
        <b-form-group id="input-group" label="Cut_Pol_Sym Name :" label-for="name">
          <b-form-input
            id="name"
            v-model="cps.name"
            required
            placeholder="Enter Cut/Pol/Sym name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Cut_Pol_Sym Reference :" label-for="reference">
          <b-form-input
            id="reference"
            v-model="cps.reference"
            required
            placeholder="Enter Cut_Pol_Sym Reference like ( ,EX, ,EXCELLENT, ,RB, )"
          ></b-form-input>
        </b-form-group>
        

        <b-button type="submit" variant="primary">Submit</b-button>
      </b-form>
<!-- FOR EDITING DATA -->

      <b-form class="column" style="width:30%" @submit.prevent="updateData" v-if="isEditing">
        <b-form-group id="input-group" label="Cut_Pol_Sym Name :" label-for="name">
          <b-form-input
            id="name"
            v-model="currentCps.name"
            required
            placeholder="Enter Cut/Pol/Sym name only"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="input-group" label="Cut_Pol_Sym Reference :" label-for="reference">
          <b-form-input
            id="reference"
            v-model="currentCps.reference"
            required
            placeholder="Enter Cut/Pol/Sym Reference like ( ,EX, ,EXCELLENT)"
          ></b-form-input>
        </b-form-group>
       

        <b-button type="submit" variant="primary">Update</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
import Cut_Pol_Sym from "../models/Cut_Pol_Sym";
import axios from "axios";
import commonVari from "../models/commonVari.js"
export default {
  name: "CPS",
  data() {
    return {
      API_URL: commonVari.API_URL,
      cps: new Cut_Pol_Sym(),
      isEditing: false,
      cpss: [], 
      currentCps: new Cut_Pol_Sym(),
    };
  },
  created() {
    this.getAllCps();
  },
  methods: {
      selectCps(cps){
          this.currentCps = cps;
      },
    getAllCps() {
      axios
        .get(this.API_URL + "cps/getAllcps")
        .then(({ data }) => (this.cpss = data));
    },
    saveData() {
      axios.post(this.API_URL + "cps/savecps", this.cps).then(() => {
        
        this.cps.name = "";
        this.cps.reference = "";
        
        this.getAllCps();
      });
    },
    updateData() {
      axios.post(this.API_URL + "cps/updatecps", this.currentCps).then(() => {
        
        this.isEditing = false;
        this.cps.name = "";
        this.cps.reference = "";
        
        this.getAllCps();
      });
    }, 
    deleteCps(cps){
         
      this.currentCps = cps;
      axios.post(this.API_URL + "cps/deletecps", this.currentCps).then(() => {
        
        this.isEditing = false;
        this.getAllCps();
      });
    }

  }
};
</script>
<style scoped>
.container {
  height: 300px;
}
#input-group {
  font-size: 15px;
  font-weight: bold;
}
.column {
  float: left;
  width: 50%;
  padding: 10px;
}
.row:after {
  padding: 5px;
  content: "";
  display: table;
  clear: both;
}
</style>