export default class purchase_line {
    constructor(id, diamond, description, purchase_head, trans,
        weight,
        rapprice,
        discount, dis1,
        dis2, usdPerCt,
        usdTotal,
        curRate,
        inrPerCt,
        inrtoal) {
        this.id = id;
        this.diamond = diamond;
        this.description = description;
        this.purchase_head = purchase_head;
        this.trans = trans;
        this.weight = weight;
        this.rapprice = rapprice;
        this.discount = discount;
        this.dis1 = dis1;
        this.dis2 = dis2;
        this.usdPerCt = usdPerCt;
        this.usdTotal = usdTotal;
        this.curRate = curRate;
        this.inrPerCt = inrPerCt;
        this.inrtoal = inrtoal;
    }
}