<template>
  <div>
     <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">STOCK REPORTS</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-container>
    
    <v-container>
      <div>
        <v-row>
          <v-col cols="12" md="2">
            <v-autocomplete
              style="width:200px"
              required
              v-model="company"
              :items="companies"
              color="blue"
              item-text="name"
              solo
              dense
              outlined
              hide-details
              opeque
              return-object
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="2">
            <v-btn outlined color="indigo" @click="getPurchase()">Retrive Report</v-btn>
          </v-col>

          <v-col cols="12" md="2">
            <v-autocomplete
              style="width:200px"
              required
              v-model="action"
              :items="actions"
              color="blue"
              item-text="name"
              solo
              dense
              outlined
              hide-details
              opeque
              return-object
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="2">
            <v-btn outlined color="indigo" @click="filterPurchase()">Filter Report</v-btn>
          </v-col>
        </v-row>
      </div>
    </v-container>
    <!-- group-by="diamondTracking.nextProcess" -->
    <v-data-table
      :headers="headers"
      :items="purchaselists"
      :items-per-page="10"
      class="elevation-1"
    >
      
    </v-data-table>
  </div>
</template>

<script>
import CompanyRelation from "../models/CompanyRelation";
import commonVari from "../models/commonVari.js";
import axios from "axios";
export default {
  data() {
    return {
      expanded: [],
      API_URL: commonVari.API_URL,
      purchaselists: [],
      companies: [],
      company: new CompanyRelation(),
      action: "",
      actions: [
        { name: "SALE ENTRY" },
        { name: "EXPORT TO HONG KONG" },
        { name: "SEND FOR RECUT" },
        { name: "EXPORT TO INDIA" },
        { name: "PRICING" }
      ],
      headers: [
        { text: "ACTION REQUIRED", value: "processActionRequire" },
        { text: "ACTION PURPOSE", value: "actionPurpose" },
        { text: "PURPOSE", value: "purpose" },
        {
          text: "FOR LOCATION",
          value: "pur_lines.company[0].name"
        },
        { text: "STOCK #", value: "stockid" },
        { text: "SHAPE", value: "shape.shapename" },
        { text: "WEIGHT", value: "currentweight" },
        { text: "COLOR", value: "color.colorname" },
        { text: "CLARITY", value: "clarity.clarityname" },
        { text: "CUT", value: "cut.name" },
        { text: "POLISH", value: "polish.name" },
        { text: "SYM", value: "sym.name" },
        { text: "FLURO", value: "fluro.fluroname" },
        { text: "FLURO COLOR", value: "flurocolor" },
        { text: "LAB", value: "lab.labname" },
        { text: "REPORT #", value: "reportno" },
        { text: "RAP", value: "rapprice" },
        
      ]
    };
  },

  created() {
    this.getAllCompanies();
    this.getPurchase();
  },
  methods: {
    async filterPurchase() {
      var filteredList = [];
      await this.purchaselists.filter(plist => {
        console.log(this.action);
        console.log(plist.diamondTracking.nextProcess);

        if (plist.diamondTracking.nextProcess == this.action.name) {
          filteredList.push(plist);
        }
        this.purchaselists = filteredList;
      });
    },
    getAllCompanies() {
      axios
        .get(this.API_URL + "companyrelation/getAllCompany")
        .then(({ data }) => (this.companies = data));
    },

    getPurchase() {
      axios
        .get(this.API_URL + "diamond/getAllCurrentStock")
        .then(({ data }) => (this.purchaselists = data));
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(26, 172, 216);
  color: white;
}
.container {
  max-width: 1185px;
  min-width: -webkit-fill-available;
}
</style>