          <v-col cols="12" md="4">
            <v-text-field v-model="company.name" label="Company Name" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.companyCode" label="Company Code" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-text-field v-model="company.address" label="Company Address" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.city" label="City" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.pin" label="Pin" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-autocomplete
              v-model="country"
