              return-object
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4">
            <v-autocomplete
              v-model="constate"
              :items="constates"
              color="white"
              item-text="statename"
              label="State"
              solo
              dense
              return-object
            ></v-autocomplete>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.phone" label="Phone" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.mobile" label="Mobile" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.email" label="Email" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.gstno" label="GSTN" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.panno" label="PAN" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.bankaccname" label="Bank A/C Name" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.bankname" label="Bank Name" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.accno" label="Bank A/C No" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.ifsc" label="IFS Code" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.iban" label="IBAN" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-text-field v-model="company.bankaddress" label="Bank Address" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.remark1" label="Remark 1" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.remark2" label="Remark 2" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-textarea
              label="Terms and Conditions"
              v-model="company.terms"
              auto-grow
              outlined
              rows="3"
              row-height="25"
            ></v-textarea>
          </v-col>
        </v-row>
        <v-btn color="success" class="mr-4" @click="saveData">Submit</v-btn>
      </v-container>
    </v-form>

    <v-form v-if="isEditing">
      <v-container>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.name" label="Company Name" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="company.companyCode" label="Company Code" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-text-field v-model="currentCompany.address" label="Company Address" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.city" label="City" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.pin" label="Pin" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-autocomplete
              v-model="currentCompany.country"
              :items="countries"
              color="white"
              item-text="countryname"
              label="Country"
              @change="fillStates"
              solo
              dense
              return-object
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4">
            <v-autocomplete
              v-model="currentCompany.province"
              :items="constates"
              color="white"
              item-text="statename"
              label="State"
              solo
              dense
              return-object
            ></v-autocomplete>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.phone" label="Phone" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.mobile" label="Mobile" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.email" label="Email" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.gstno" label="GSTN" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.panno" label="PAN" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.bankaccname" label="Bank A/C Name" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.bankname" label="Bank Name" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.accno" label="Bank A/C No" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.ifsc" label="IFS Code" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.iban" label="IBAN" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-text-field v-model="currentCompany.bankaddress" label="Bank Address" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.remark1" label="Remark 1" outlined required></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field v-model="currentCompany.remark2" label="Remark 2" outlined required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-textarea
              label="Terms and Conditions"
              v-model="currentCompany.terms"
              auto-grow
              outlined
              rows="3"
              row-height="25"
            ></v-textarea>
          </v-col>
        </v-row>
        <v-btn color="success" class="mr-4" @click="updateData">Update</v-btn>
      </v-container>
    </v-form>


    <v-expansion-panels>
      <v-expansion-panel v-for="item in companies" v-bind:key="item.id">
        <v-expansion-panel-header>
          <v-col cols="15">{{item.name}}</v-col>
          <v-col cols="15">{{item.name}}</v-col>
          <v-col cols="1">
            <a href="#" class="text-success" @click="isEditing=true; selectCompany(item)">
              <font-awesome-icon icon="edit" />
            </a>
          </v-col>
          <v-col cols="1">
            <a class="text-danger" @click="dialog=true ; selectCompany(item)">
              <font-awesome-icon icon="trash-alt" />
            </a>
          </v-col>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row no-gutters>
            <v-col cols="15">Address : {{item.address}}</v-col>
          </v-row>
          <v-row justify="space-around" no-gutters>
            <v-col cols="3">City : {{item.city}}</v-col>
            <v-col cols="3">Pin : {{item.pin}}</v-col>
            <v-col cols="3">State : {{item.province.statename}}</v-col>
            <v-col cols="3">Country : {{item.country.countryname}}</v-col>
          </v-row>
          <v-row justify="space-around" no-gutters>
            <v-col cols="3">Email : {{item.email}}</v-col>
            <v-col cols="3">Phone : {{item.phone}}</v-col>
            <v-col cols="3">Mobile : {{item.mobile}}</v-col>
            <v-col cols="3">GSTIN : {{item.gstno}}</v-col>
          </v-row>
          <v-row justify="space-around" no-gutters>
            <v-col cols="3">PAN : {{item.pan}}</v-col>
            <v-col cols="3">BANK A/C NAME : {{item.bankaccname}}</v-col>
            <v-col cols="3">BANK NAME : {{item.bankname}}</v-col>
            <v-col cols="3">BANK ADDRESS : {{item.bankaddress}}</v-col>
          </v-row>
          <v-row justify="space-around" no-gutters>
            <v-col cols="3">A/C NO : {{item.accno}}</v-col>
            <v-col cols="3">IFSC : {{item.ifsc}}</v-col>
            <v-col cols="3">IBAN : {{item.iban}}</v-col>
            <v-col cols="3"></v-col>
          </v-row>
          <v-row no-gutters>
            <v-col cols="15">REMARK 1 : {{item.remark1}}</v-col>
          </v-row>
          <v-row no-gutters>
            <v-col cols="15">REMARK 2 : {{item.remark2}}</v-col>
          </v-row>
          <v-row no-gutters>
            <v-col cols="15">TERMS & CONDITIONS : {{item.terms}}</v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>
<script>
import CountryState from "../models/CountryState";
import Country from "../models/Country";
import Company from "../models/Company";
import axios from "axios";
import commonVari from "../models/commonVari.js"

export default {
  data() {
    return {
      API_URL : commonVari.API_URL,
      dialog:false,
      company: new Company(),
      isEditing: false,
      country: new Country(),
      curcountry: null,
      countries: [],
      search: null,
      constate: new CountryState(),
      constates: [],
      headers: [
        { text: "Company Name", value: "name" },
        { text: "Address", value: "address" },
        { text: "Action", value: "" }
      ],
      companies: [],
      currentCompany : new Company()
    };
  },
  created() {
    this.getAllCompanies();
    this.getAllCountries();
  },
  methods: {
    selectCompany(company){
      this.currentCompany = company;
    },
    deleteCompany(company){
       this.currentCompany = company;
      axios.post(this.API_URL + "company/deleteCompany", this.currentCompany).then(data => {
        console.log(data);
        this.isEditing = false;
        this.getAllCompanies();
      });
    },
    getAllCompanies() {
      axios
        .get(this.API_URL + "company/getAllCompany")
        .then(({ data }) => (this.companies = data));
    },
    getAllCountries() {
      axios
        .get(this.API_URL + "country/getAllCountry")
        .then(({ data }) => (this.countries = data));
    },
    saveData() {
      this.company.province = this.constate;
      this.company.country = this.country;
      const formData = new FormData();
      formData.append("company", JSON.stringify(this.company));
      axios
        .post(this.API_URL + "company/saveCompany", formData)
        .then(function(data) {
          if (data.data.error) {
            console.log(data.data.error.message);
          }
        });
      this.company = new Company();
      this.getAllCompanies();
    },
    fillStates: function() {
      axios
        .post(this.API_URL + "states/stateByCountry", this.country)
        .then(({ data }) => (this.constates = data));
    },
    updateData() {
      
      const formData = new FormData();
      formData.append("company", JSON.stringify(this.currentCompany));
      axios
        .post(this.API_URL + "company/updateCompany", formData)
        .then(function(data) {
          if (data.data.error) {
            console.log(data.data.error.message);
          }
        });
        this.isEditing = false;
      this.company = new Company();
    },
  },
  
    
  
};
</script>
<style scoped>
.col-md-4 {
  padding-top: 0px;
  padding-bottom: 0px;
}
</style>