export default class PurchaseBillLine {
    constructor(weight, rap, dis, dis1, dis2, usdPerCt, usdTotal, curRate, inrPerCt, inrTotal, diamond,stockId) {
        this.weight = weight
        this.rap = rap
        this.dis = dis
        this.dis1 = dis1
        this.dis2 = dis2
        this.usdPerCt = usdPerCt
        this.usdTotal = usdTotal
        this.curRate = curRate
        this.inrPerCt = inrPerCt
        this.inrTotal = inrTotal
        this.diamond = diamond
        this.stockId = stockId;


    }
}