<template>
  <div>
    <v-snackbar v-model="snackbar" :color="color" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>

    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title elevation="3">SALE ENTRY</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-menu left bottom>
          <template v-slot:activator="{ on }">
            <v-btn icon v-on="on">
              <v-icon>mdi-dots-vertical</v-icon>
            </v-btn>
          </template>
          <v-list>
            <v-list-item>
              <v-list-item-title @click="addShapeForm=true">SHAPE</v-list-item-title>
            </v-list-item>
            <v-list-item>
              <v-list-item-title @click="addColorForm=true">COLOR</v-list-item-title>
            </v-list-item>
            <v-list-item>
              <v-list-item-title @click="addClarityForm=true">CLARITY</v-list-item-title>
            </v-list-item>
            <v-list-item>
              <v-list-item-title @click="addCutForm=true">CUT/POLISH/SYM</v-list-item-title>
            </v-list-item>
            <v-list-item>
              <v-list-item-title @click="addFluroForm=true">FLURO</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-toolbar>
    </v-container>

    <div v-if="!isEditing">
      <!-- <v-container fluid> -->
      <v-radio-group v-model="options" row style="height:30px">
        <v-radio label="BY STOCK #" value="byStockId"></v-radio>
        <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
      </v-radio-group>
      <v-row>
        <v-col cols="12" sm="6" md="4">
          <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="4">
          <v-btn outlined color="indigo" @click="loadDataFromMasters">LOAD DATA</v-btn>
        </v-col>
        <v-col cols="12" sm="12" md="3">
          <button type="button" class="btn btn-info" @click="addNewRow">Add New Row</button>
        </v-col>
      </v-row>
      <!-- <input type="file" @change="onCSVFileSelected" id="myfile" name="myfile" />
      <v-btn class="ma-2" outlined color="indigo" @click="parseCsv">Load data from file</v-btn>-->
      <v-spacer></v-spacer>
      <!-- FOR SAVING DATA -->
      <b-form @submit.prevent="saveSale">
        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left"></th>
                <th class="text-left">NO</th>
                <th class="text-left">DATE</th>
                <th class="text-left">STOCK #</th>
                <th class="text-left" colspan="4">DESCRIPTION OF DIAMOND</th>
                <th class="text-left">WEIGHT</th>
                <th class="text-left">RAP</th>
                <th class="text-left">DISCOUNT</th>
                <th class="text-left">DIS 1</th>
                <th class="text-left">CUR RATE</th>
                <th class="text-left">$ / CT</th>
                <th class="text-left">TOTAL $</th>
                <th class="text-left">RS / CT</th>
                <th class="text-left">TOTAL RS</th>
                <th class="text-left">EXPNESE USD</th>
                <th class="text-left">EXPNESE INR</th>
                <th class="text-left">GROSS RS</th>
                <th class="text-left">GROSS USD</th>
                <th class="text-left">SALES EXECUTIVE</th>
                <th class="text-left">SOLD TO</th>
                <th class="text-left">TERM</th>
                <th class="text-left">DUE DATE</th>
                <th class="text-left">PAYMENT MODE</th>
                <th class="text-left">LOCATION</th>
                <th class="text-left">INVOICE STATUS</th>
                <th class="text-left">STONE STATUS</th>
                <th class="text-left">CERT STATUS</th>
                <th class="text-left">REMARK</th>
                <th class="text-left">FROM COMPANY</th>

                <th class="text-left">PURCHAE TOTAL (INR)</th>
                <th class="text-left">PROFIT RS (INR)</th>
                <th class="text-left">PROFIT % (INR)</th>
                <th class="text-left">PURCHAE TOTAL (USD)</th>
                <th class="text-left">PROFIT RS (USD)</th>
                <th class="text-left">PROFIT % (USD)</th>
                <th class="text-left">DELETE</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(diamond, k) in diamonds" :key="k">
                <!-- <button style="margin:20px" type="button" class="btn btn-info" @click="addNewRow">Add</button> -->
                <td>
                  <a class="text-success" @click="addNewRow">
                    <font-awesome-icon icon="plus-square" />
                  </a>
                </td>

                <td>{{diamonds.indexOf(diamond)+1}}</td>
                <td class="tabledata">
                  <input
                    required
                    class="form-control"
                    style="width:150px"
                    v-model="diamond.opendate"
                    type="date"
                    @change="calculateDueDate(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    style="width:150px"
                    type="text"
                    v-model="diamond.stockid"
                    @change="loadManualStockId(diamond)"
                  />
                </td>
                <!-- <td style="width:500px"> -->
                <td style="width:100px">{{diamond.shape.shapename}}</td>
                <td style="width:80px">{{diamond.color.colorname}}/{{diamond.clarity.clarityname}}</td>
                <td
                  style="width:80px"
                >{{diamond.cut.name}}/{{diamond.polish.name}}/{{diamond.sym.name}}/{{diamond.fluro.fluroname}}</td>
                <td style="width:80px">{{diamond.lab.labname}}/{{diamond.reportno}}</td>

                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.weight"
                    @change="calculateLineTotal(diamond); calculateRapPrice(diamond)"
                  />
                </td>

                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.rapprice"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    step=".01"
                    v-model="diamond.discount"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    step=".01"
                    v-model="diamond.dis1"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.currate"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    v-model="diamond.usdPerCt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.usdTotal"
                  />
                </td>

                <td class="tabledata">
                  <input
                    style="width:150px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    v-model="diamond.inrPerCt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:150px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.inrTotal"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.expenseusd"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.expense"
                    @change="calculateLineTotal(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:150px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.grossTotal"
                  />
                </td>
                <td class="tabledata">
                  <input
                    style="width:150px"
                    readonly
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step=".01"
                    v-model="diamond.grossUsd"
                  />
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:200px"
                    required
                    v-model="diamond.saleperson"
                    auto-select-first
                    :items="salepersons"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:200px"
                    required
                    v-model="diamond.buyer"
                    auto-select-first
                    :items="diamond.buyersLine"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <input
                    style="width:80px"
                    class="form-control text-right"
                    type="number"
                    min="0"
                    step="1"
                    v-model="diamond.term"
                    @change="calculateDueDate(diamond)"
                  />
                </td>
                <td class="tabledata">
                  <input
                    readonly
                    class="form-control"
                    style="width:150px"
                    v-model="diamond.dueDate"
                    type="date"
                  />
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:200px"
                    required
                    :readonly="showPaymentMode"
                    v-model="diamond.billingParty"
                    auto-select-first
                    :items="billingparties"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:150px"
                    required
                    v-model="diamond.buyForLocation"
                    :items="countries"
                    color="white"
                    item-text="countryname"
                    dense
                    solo-inverted
                    full-width
                    hide-details
                    outlined
                    return-object
                  ></v-autocomplete>
                </td>

                <td class="tabledata">
                  <v-autocomplete
                    style="width:100px"
                    required
                    v-model="diamond.invoiceStatus"
                    auto-select-first
                    :items="statuses"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    hide-details
                    outlined
                    @change="changePaymentMode(diamond)"
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:100px"
                    required
                    v-model="diamond.stoneStatus"
                    auto-select-first
                    :items="statuses"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    hide-details
                    outlined
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <v-autocomplete
                    style="width:100px"
                    required
                    v-model="diamond.certStatus"
                    auto-select-first
                    :items="statuses"
                    color="white"
                    item-text="name"
                    dense
                    solo-inverted
                    hide-details
                    outlined
                  ></v-autocomplete>
                </td>
                <td class="tabledata">
                  <input
                    class="form-control"
                    style="width:150px"
                    type="text"
                    v-model="diamond.remark"
                  />
                </td>

                <td class="tabledata">
                  <v-autocomplete
                    style="width:200px"
                    required
                    v-model="diamond.buyingCompany"
                    :items="companies"
                    color="blue"
                    item-text="name"
                    solo-inverted
                    dense
                    outlined
                    hide-details
                    opeque
                    return-object
                  ></v-autocomplete>
                </td>

                <td class="tabledata">
                  <input
                    disabled
                    class="form-control"
                    style="width:100px"
                    type="number"
                    v-model="diamond.grosspurchase"
                  />
                </td>
                <td class="tabledata">
                  <input
                    disabled
                    class="form-control"
                    style="width:50px"
                    type="number"
                    v-model="diamond.profit"
                  />
                </td>
                <td class="tabledata">
                  <input
                    disabled
                    class="form-control"
                    style="width:30px"
                    type="number"
                    v-model="diamond.profitPt"
                  />
                </td>
                <td class="tabledata">
                  <input
                    disabled
                    class="form-control"
                    style="width:100px"
                    type="number"
                    v-model="diamond.grosspurchaseusd"
                  />
                </td>
                <td class="tabledata">
                  <input
                    disabled
                    class="form-control"
                    style="width:50px"
                    type="number"
                    v-model="diamond.profitusd"
                  />
                </td>
                <td class="tabledata">
                  <input
                    disabled
                    class="form-control"
                    style="width:30px"
                    type="number"
                    v-model="diamond.profitptusd"
                  />
                </td>
                <td scope="row" class="trashIconContainer">
                  <a class="text-danger" @click="deleteRow(k, diamond)">
                    <font-awesome-icon icon="trash-alt" />
                  </a>
                </td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td colspan="2"></td>
                <td></td>
                <td></td>
                <td></td>
                <td colspan="2"></td>
                <td></td>
                <td></td>
                <!-- <td>{{invoice_weight}}</td> -->
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <!-- <td>{{invoice_usdtotal}}</td> -->
                <td></td>
                <!-- <td>{{invoice_inrtotal}}</td> -->
              </tr>
            </tfoot>
          </template>
        </v-simple-table>
        <v-spacer></v-spacer>
        <v-btn
          style="margin-top:10px; margin-left:10px;"
          color="success"
          type="submit"
          class="mr-4"
        >Submit</v-btn>
      </b-form>
    </div>

    <div v-if="isEditing">
      <v-divider></v-divider>
      <button style="margin:20px" type="button" class="btn btn-info" @click="getPurchases">
        <i class="fas fa-plus-circle"></i>
        RETRIVE SALES
      </button>

      <!-- select data -->

      <v-simple-table dense>
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-right">ID</th>
              <th class="text-right">CARATS</th>
              <th class="text-right">USD TOTAL</th>
              <th class="text-right">INR TOTAL</th>
              <th class="text-right">EDIT</th>
            </tr>
          </thead>
          <tbody class="text-right">
            <tr v-for="(item, k) in purchaselists" :key="k">
              <td>{{item.id}}</td>
              <td>{{item.invoice_weight}}</td>
              <td>{{item.invoice_usdtotal}}</td>
              <td>{{item.invoice_inrtotal}}</td>
              <td>
                <a href="#" class="text-success" @click="isEditing=true; selectPurchase(item)">
                  <font-awesome-icon icon="edit" />
                </a>
              </td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
          </tfoot>
        </template>
      </v-simple-table>
    </div>
    <!-- edit data -->
  </div>
</template>

<script>
import GIAService from "../services/GIAService";
import purchase_line from "../models/purchase_line";
import Cut_Pol_Sym from "../models/Cut_Pol_Sym";
import Fluro from "../models/Fluro";
import Color from "../models/Color";
import Clarity from "../models/Clarity";
import Shape from "../models/Shape";
import Ledger from "../models/Ledger";
import Sale from "../models/Sale";
// import Purchase from "../models/Purchase";
import Transactions from "../models/Transactions";
import Diamond from "../models/Diamond";

import axios from "axios";
import commonVari from "../models/commonVari.js";
import CompanyRelation from "../models/CompanyRelation";
import Lab from "../models/Lab";
import papaparse from "papaparse";
// import Datepicker from 'vuejs-datepicker';

export default {
  data() {
    return {
      addShapeForm: false,
      addColorForm: false,
      addClarityForm: false,
      addCutForm: false,
      addFluroForm: false,
      color: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      requiredParams: "",
      options: null,
      showPaymentMode: false,
      currentDate: "",
      loading: false,
      isEditing: false,
      editpurchaselists: [],
      salelists: [],
      API_URL: commonVari.API_URL,
      billingparties: [],
      suppliers: [],
      buyers: [],
      shapes: [],
      colors: [],
      clarities: [],
      cuts: [],
      fluros: [],
      labs: [],
      companies: [],
      trans: new Transactions(),
      menu2: false,
      diam: new Diamond(),
      purchase_head: new Sale(),
      purchase_line: new purchase_line(),
      plines: [],
      diamondlines: [],
      translines: [],
      invoice_weight: 0,
      invoice_usdtotal: 0,
      invoice_inrtotal: 0,
      countries: [],
      salepersons: [],
      statuses: [{ name: "YES" }, { name: "NO" }, { name: "N/A" }],
      purposes: [
        { name: "COR" },
        { name: "RE-CUT" },
        { name: "STOCK" },
        { name: "LAB" }
      ],
      currentDiamonds: [],
      diamonds: [
        {
          no: "",
          diamondId: "",
          grossUsd: 0,
          expenseusd: 0,
          stockid: "",
          opendate: this.currentDate,
          // fromCompany: new CompanyRelation(),
          partnerratio: 0,
          partnerAmt: 0,
          color: new Color(),
          clarity: new Clarity(),
          cut: new Cut_Pol_Sym(),
          polish: new Cut_Pol_Sym(),
          sym: new Cut_Pol_Sym(),
          fluro: new Fluro(),
          flurocolor: "",
          lab: new Lab(),
          reportno: "",
          shape: new Shape(),
          buyer: new Ledger(),
          saleperson: new Ledger(),
          weight: 0,
          rapprice: 0,
          discount: 0,
          dis1: 0,
          usdPerCt: 0,
          usdTotal: 0,
          currate: 0,
          inrPerCt: 0,
          inrTotal: 0,
          grossTotal: 0,
          expense: 0,
          billingParty: new Ledger(),
          term: 0,
          dueDate: this.currentDate,
          buyForLocation: "",
          stoneStatus: "",
          certStatus: "",
          invoiceStatus: "",
          remark: "",
          grosspurchase: 0,
          profit: 0,
          profitPt: 0,
          grosspurchaseusd: 0,
          profitusd: 0,
          profitptusd: 0
        }
      ],
      gia: "",
      csvfile: null
    };
  },
  beforeCreate() {
    var d = new Date();
    var dateObj = new Date(d.getTime());

    var year = dateObj.getFullYear();
    var month = dateObj.getMonth() + 1;
    var dt = dateObj.getDate();

    if (dt < 10) {
      dt = "0" + dt;
    }
    if (month < 10) {
      month = "0" + month;
    }

    this.currentDate = year + "-" + month + "-" + dt;
  },
  created() {
    this.getAllShapes();
    this.getAllLedgers(); // sales person
    this.getBillingParties();
    this.getAllCompanies();
    this.getAllColors();
    this.getAllClarity();
    this.getAllCuts();
    this.getAllFluro();
    this.getAllLabs();
    this.getAllCountries();
    this.setCurrentDate();
  },
  methods: {
    async loadManualStockId(diamond) {
      const formData = new FormData();
      var results = [];
      formData.append("stockId", diamond.stockid);
      await axios
        .post(this.API_URL + "diamond/getAllByStockId", formData)
        .then(response => {
          results = response.data;
        })
        .catch(error => {
          console.log(error);
        });

      if (results.length != 0) {
        var filedata = results[0];
        var di = new Diamond();
        di.weight = filedata.currentweight;
        di.color = filedata.color;
        di.shape = filedata.shape;
        di.clarity = filedata.clarity;

        var buyersLine = [];
        await this.getAllBuyers(filedata.pur_lines.company[0]).then(data => {
          buyersLine = data.data;
        });

        await this.calculateRapPrice(di).then(response => {
          var rap = response;

          (diamond.grosspurchaseusd = filedata.pur_lines.grossUsd),
            (diamond.buyersLine = buyersLine);
          (diamond.profitusd = 0),
            (diamond.profitptusd = 0),
            (diamond.grosspurchase = filedata.pur_lines.grossTotal),
            // grosspurchase:filedata.pur_lines.grossTotal,
            (diamond.diamondId = filedata.id),
            (diamond.partnerAmt = 0),
            (diamond.no = ""),
            (diamond.grossUsd = 0),
            (diamond.expenseusd = 0),
            (diamond.stockid = filedata.stockid),
            (diamond.opendate = this.currentDate),
            (diamond.buyingCompany = filedata.pur_lines.company[0]),
            (diamond.childCompany = new CompanyRelation()),
            (diamond.partnerratio = 0),
            (diamond.purpose = ""),
            (diamond.color = filedata.color),
            (diamond.clarity = filedata.clarity),
            (diamond.cut = filedata.cut),
            (diamond.polish = filedata.polish),
            (diamond.sym = filedata.sym),
            (diamond.fluro = filedata.fluro),
            (diamond.lab = filedata.lab),
            (diamond.reportno = filedata.reportno),
            (diamond.shape = filedata.shape),
            (diamond.supplier = new Ledger()),
            (diamond.buyer = new Ledger()),
            (diamond.weight = filedata.currentweight),
            (diamond.rapprice = rap),
            (diamond.discount = 0),
            (diamond.dis1 = 0),
            (diamond.dis2 = 0),
            (diamond.usdPerCt = 0),
            (diamond.usdTotal = 0),
            (diamond.currate = 0),
            (diamond.inrPerCt = 0),
            (diamond.inrTotal = 0),
            (diamond.term = 0),
            (diamond.dueDate = this.currentDate),
            (diamond.length = filedata.length),
            (diamond.width = filedata.width),
            (diamond.depth = filedata.depth),
            (diamond.colorDesc = filedata.colorDesc),
            (diamond.depthPt = filedata.depthPt),
            (diamond.tablePt = filedata.tablePt),
            (diamond.crAg = filedata.crAg),
            (diamond.crHt = filedata.crHt),
