        clarity: new Clarity(),
        cut: new Cut_Pol_Sym(),
        polish: new Cut_Pol_Sym(),
        sym: new Cut_Pol_Sym(),
        fluro: new Fluro(),
        flurocolor: "",
        lab: new Lab(),
        reportno: "",
        shape: new Shape(),
        supplier: new Ledger(),
        buyer: new Ledger(),
        weight: 0,
        rapprice: 0,
        discount: 0,
        dis1: 0,
        dis2: 0,
        usdPerCt: 0,
        usdTotal: 0,
        currate: 0,
        inrPerCt: 0,
        inrTotal: 0,
        grossTotal: 0,
        purchase: this.purchase_head,
        expense: 0,
        billingParty: new Ledger(),
        term: 0,
        dueDate: this.currentDate,
        currentLocation: "",
        buyForLocation: "",
        stoneStatus: "",
        certStatus: "",
        invoiceStatus: "",
        remark: "",
        // NEW FIELDS
        length: "",
        width: "",
        depth: "",
        colorDesc: "",
        depthPt: "",
        tablePt: "",
        crAg: "",
        crHt: "",
        pavAg: "",
        pavDp: "",
        starLn: "",
        lrHalf: "",
        girdle: "",
        girdleCondition: "",
        girdlePct: "",
        culetSize: "",
        keyToSymbol: "",
        reportDate: "",
        inscription: "",
        infoMessage: "",
        clarityStatus: ""
      });
    },
    getAllShapes() {
      axios
        .get(this.API_URL + "shape/getAllShapes")
        .then(({ data }) => (this.shapes = data));
    },
    getBillingParties() {
      axios
        .get(this.API_URL + "ledger/getBillingParties")
        .then(({ data }) => (this.billingparties = data));
    },
    async getAllBuyers(diamond) {
      return axios
        .post(this.API_URL + "ledger/getBuyerByCompany", diamond)
        .then(data => {
          console.log(data);
          return data;
        });
    },
    async getAllLedgers() {
      await axios
        .get(this.API_URL + "ledger/getSalePersons")
        .then(({ data }) => (this.salepersons = data));
    },

    getAllCompanies() {
      axios
        .get(this.API_URL + "companyrelation/getAllCompany")
        .then(({ data }) => (this.companies = data));
    },
    getAllColors() {
      axios
        .get(this.API_URL + "color/getAllColors")
        .then(({ data }) => (this.colors = data));
    },
    getAllClarity() {
      axios
        .get(this.API_URL + "clarity/getAllClarity")
        .then(({ data }) => (this.clarities = data));
    },
    getAllCuts() {
      axios
        .get(this.API_URL + "cps/getAllcps")
        .then(({ data }) => (this.cuts = data));
    },
    getAllFluro() {
      axios
        .get(this.API_URL + "fluro/getAllFluro")
        .then(({ data }) => (this.fluros = data));
    },
    getAllLabs() {
      axios
        .get(this.API_URL + "lab/getAllLabs")
        .then(({ data }) => (this.labs = data));
    },
    getAllCountries() {
      axios
        .get(this.API_URL + "country/getAllCountry")
        .then(({ data }) => (this.countries = data));
    },
    setBuyingCompany(diamond) {
      diamond.currentLocation = diamond.buyingCompany.mainCompany.country;
    }
  }
};
</script>

