<template>
  <div>
    <v-snackbar v-model="snackbar" :color="color" :timeout="timeout" :top="true">
      {{ text }}
      <v-btn dark text @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-container>
      <v-toolbar shaped class="toolbase" elevation="3" dense>
        <v-toolbar-title
          elevation="3"
        >{{this.$route.params.company}} | {{this.$route.params.process}} | {{this.$route.params.purpose}}</v-toolbar-title>

        <v-spacer></v-spacer>
      </v-toolbar>
    </v-container>
    <div>
      <v-row>
        <!-- <v-col cols="12" md="3">
          <v-radio-group v-model="option" row>
            <v-radio label="BY STOCK #" value="byStockId"></v-radio>
            <v-radio label="BY REPORT #" value="byReportNo"></v-radio>
          </v-radio-group>
        </v-col>

        <v-col cols="12" md="3">
          <v-text-field v-model="requiredParams" label="STOCK # / REPORT #" outlined dense></v-text-field>
        </v-col>
        <v-col cols="12" md="1">
          <v-btn outlined color="indigo">LOAD DATA</v-btn>
        </v-col> -->
        <v-col cols="12" md="3">
          <v-card dense v-if="isSendToLab">
            <v-card-title title="SELECT LAB LOCATION">
              <v-radio-group v-model="labOption" row>
                <v-radio label="INDIA LAB" value="indiaLab"></v-radio>
                <v-radio label="HK LAB" value="hkLab"></v-radio>
              </v-radio-group>
            </v-card-title>
          </v-card>
        </v-col>
      </v-row>
    </div>

    <v-container>
      <v-row>
        <v-col cols="12" md="4">
          <v-autocomplete
            chips
            required
            v-model="selectedCompanies"
            :items="companies"
            color="blue"
            item-text="name"
            solo
            dense
            outlined
            hide-details
            opeque
            return-object
            multiple
          ></v-autocomplete>
        </v-col>
        <v-col
          cols="12"
          md="7"
          style="background:rgb(26, 172, 216);color:white;font-size:20px"
          align="center"
          justify="center"
        >{{updatedStoneList}}</v-col>
        <!-- <v-col cols="12" md="3">
          <v-autocomplete
            required
            v-model="action"
            auto-select-first
            :items="statuses"
            color="white"
            item-text="name"
            dense
            solo-inverted
            hide-details
            outlined
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="2">
          <v-btn outlined color="indigo" @click="getPurchase()">Retrive Report</v-btn>
        </v-col>-->
      </v-row>
    </v-container>

    <v-card flat>
      <v-card-text>
        <v-data-table
          v-model="selectedStones"
          :headers="headers"
          :items="stoneCertLists"
          :items-per-page="10"
          show-select
          class="elevation-1"
        >
          <template v-slot:item.data-table-select="{ item, isSelected, select }">
            <v-simple-checkbox
              :ripple="false"
              :value="item.stoneBlock == false && isSelected"
              :readonly="item.stoneBlock"
              :disabled="item.stoneBlock"
              @input="select($event)"
            ></v-simple-checkbox>
          </template>
          <template
            v-slot:item.id="{ item }"
          >{{(stoneCertLists.map(function(x) {return x.id; }).indexOf(item.id)) + 1}}</template>
          <!-- { text: "PURCHASE DAY", value: "diaTracking[diaTracking.length - 1].opendate" }, -->
          <template
            v-slot:item.pur_lines.transdate="{ item }"
          >{{countDays(item.pur_lines.transdate)}}</template>

          <template v-slot:item.diaTracking="{ item }">{{countDaysForLast(item.diaTracking)}}</template>
        </v-data-table>

        <v-divider></v-divider>
        <v-btn outlined color="indigo" @click="saveSelectedCertList(selectedStones)">SAVE SELECTED</v-btn>
      </v-card-text>
    </v-card>

    <v-divider></v-divider>
  </div>
</template>

<script>
import Vue from "vue";
import CompanyRelation from "../models/CompanyRelation";
import commonVari from "../models/commonVari.js";
import axios from "axios";
import { GridPlugin } from "@syncfusion/ej2-vue-grids";
Vue.use(GridPlugin);
export default {
  data() {
    return {
      actions: [],
      updatedStoneList: "UPDATED STONE LIST",
      color: "success",
      snackbar: false,
      text: "",
      timeout: 3000,
      action: "",
      statuses: [
        { name: "SEND TO INDIA" },
        { name: "COLLECT FROM HK" },
        { name: "SEND FOR RECUT" },
        { name: "RECEIVE FROM RECUT" },
        { name: "SEND TO LAB" },
        { name: "RECEIVE FROM LAB" },
        { name: "QC CHECK FROM LAB" },
        { name: "DNA" },
        { name: "PRICING" },
        { name: "SEND TO HK" },
        { name: "COLLECT FROM INDIA" }
      ],
      requiredParams: "",
      option: null,
      isSendToLab: false,
      labOption: null,
      option1: null,
      purchaselists1: [],
      selectedStones: [],
      selectedstoneCertLists: [],
      selectedstoneLists: [],
      selectedcertLists: [],
      stoneCertLists: [],
      stoneLists: [],
      certLists: [],
      savedList: [],
      tabs: null,
      actionPurpose: "",

      API_URL: commonVari.API_URL,
      purchaselists: [],
      companies: [],
      selectedCompanies: [],
      selectcompany: new CompanyRelation(),
      company: new CompanyRelation(),

      headers: [
        { text: "NO", value: "id" },
        { text: "STOCK #", value: "stockid" },
        { text: "STATUS", value: "processActionRequire" },
        { text: "BOUGHT FOR", value: "stoneOwner.name" },
        { text: "SHAPE", value: "shape.shapename" },
        { text: "WEIGHT", value: "originalweight" },
        { text: "COLOR", value: "color.colorname" },
        { text: "CLARITY", value: "clarity.clarityname" },
        { text: "CUT", value: "cut.name" },
        { text: "POLISH", value: "polish.name" },
        { text: "SYM", value: "sym.name" },
        { text: "FLURO", value: "fluro.fluroname" },
        { text: "LAB", value: "lab.labname" },
        { text: "REPORT #", value: "reportno" },
        { text: "KEY TO SYMBOL", value: "keyToSymbol" },
        { text: "REPORT COMMENT", value: "infoMessage" },
        { text: "PURCHASE DAY", value: "pur_lines.transdate" },
        { text: "STAGE DAY", value: "diaTracking" },
        { text: "REMARK", value: "remark" },
        { text: "REMARK 1", value: "recutremark" }
      ]
    };
  },
  created() {
    this.company = this.$route.params.company;
    this.actionPurpose = this.$route.params.purpose;
    this.action = this.$route.params.process;
    this.actions.push(this.action);
    if (this.action == "SEND TO LAB") {
      this.isSendToLab = true;
    }
    this.getAllCompanies(this.company);
  },
  methods: {
    getIndexOf(value) {
      console.log(value);
      return 1;
    },

    async saveSelectedCertList(selectedList) {
      const itemNames = selectedList
        .filter(item => {
          return item.stoneBlock == false;
        })
        .map(item => {
          return item;
        });

      if (this.isSendToLab) {
        if (this.labOption == "hkLab") {
          this.action = "SEND TO HK";
        } else {
          this.action = "SEND TO LAB";
        }
      }

      var stoneno = [];
      selectedList.forEach(function(item) {
        stoneno.push(item.stockid);
      });
      const formData = new FormData();
      formData.append("company", JSON.stringify(itemNames));
      formData.append("action", this.action);
      formData.append("isExportForm", false);
      formData.append("actionPurpose", this.actionPurpose);

      await axios
        .post(this.API_URL + "diamond/updateDiamondProcess", formData)
        .then(data => {
          console.log(data),
            (this.text = "SUCCESS ...."),
            (this.snackbar = true),
            (this.updatedStoneList = stoneno),
            (this.purchaselists1 = []),
            (this.selectedStones = []),
            (this.selectedstoneCertLists = []),
            (this.selectedstoneLists = []),
            (this.selectedcertLists = []),
            (this.stoneCertLists = []),
            (this.stoneLists = []),
            (this.certLists = []),
            (this.selectedList = []);
        })
        .catch(error => {
          this.text = error;
          this.snackbar = true;
        });
      this.getPurchase();
    },
    countDays(date) {
      var dt = new Date(date);
      var currentDate = new Date();
      var timeDiff = currentDate.getTime() - dt.getTime();
      var days = Math.round(timeDiff / (1000 * 3600 * 24));
      return days;
    },
    countDaysForLast(records) {
      // <!-- { text: "PURCHASE DAY", value: "diaTracking[diaTracking.length - 1].opendate" }, -->
      var dt = new Date(records[records.length - 1].opendate);
      var currentDate = new Date();
      var timeDiff = currentDate.getTime() - dt.getTime();
      var days = Math.round(timeDiff / (1000 * 3600 * 24));
      return days;
    },

    async getAllCompanies(company) {
      const formData = new FormData();
      formData.append("company", company);
      await axios
        .post(
          this.API_URL + "companyrelation/getCompanyByMainCompany",
          formData
        )
        .then(
          ({ data }) => (
            (this.companies = data), (this.selectedCompanies = data)
          )
        );
      this.getPurchase();
    },
    async getPurchase() {
      this.action = this.$route.params.process;
      const formData = new FormData();
      formData.append("company", JSON.stringify(this.selectedCompanies));
      formData.append("action", JSON.stringify(this.actions));
      await axios
        .post(this.API_URL + "diamond/getDiamondProcess", formData)
        .then(({ data }) => ((this.stoneCertLists = data), console.log(data)));
    }
  }
};
</script>

<style scoped>
.theme--light.v-toolbar.v-sheet {
  background-color: rgb(26, 172, 216);
  color: white;
}
.container {
  max-width: 1185px;
  min-width: -webkit-fill-available;
}
.v-input--selection-controls {
  margin-top: 0px;
  padding-top: 4px;
  height: 30px;
}
</style>